import { formatDate } from '@angular/common';
import {
  Component,
  Input,
  Output,
  EventEmitter,
  AfterViewInit,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { STATEMENT_MODE } from '@statement/models/statement.model';

import {
  TRANSACTION_GROUP_TYPE,
  ITransactionGroup,
  Transaction,
} from '../../models/transaction.model';

@Component({
  selector: 'j-transaction-group',
  templateUrl: './transaction-group.component.html',
  styleUrls: ['./transaction-group.component.scss'],
})
export class TransactionGroupComponent implements AfterViewInit {
  @Input() mode: STATEMENT_MODE;
  @Input() group: ITransactionGroup;
  @Input() type: TRANSACTION_GROUP_TYPE;

  @Input() hasLargeTransactions: boolean;

  @Input() isTotalsShown: boolean = true;
  @Input() isSelectable: boolean = false;
  @Input() hasExtraPadding: boolean = false;

  @Input() isLoadingExtra: boolean = false;

  @Output() selected = new EventEmitter<Transaction[]>();

  @Output() deleted = new EventEmitter<Transaction>();
  @Output() signed = new EventEmitter<Transaction>();
  @Output() sentForSign = new EventEmitter<Transaction>();
  @Output() exported = new EventEmitter<Transaction>();
  @Output() revoked = new EventEmitter<Transaction>();

  locale: string = 'ru';
  private selectedTransactions = [];

  constructor(private translateService: TranslateService) {
    this.locale = this.translateService.currentLang;
    this.translateService.onLangChange.subscribe(() => {
      this.locale = this.translateService.currentLang;
    });
  }

  ngAfterViewInit() {}

  get transactions() {
    if (this.group && this.group.transactions) {
      return this.group.transactions;
    }
    return [];
  }

  get title() {
    if (this.group && this.group.title) {
      return this.group.title;
    } else if (this.type == TRANSACTION_GROUP_TYPE.DAY) {
      if (this.isToday) {
        return this.translateService.instant(`FILTER.PERIOD.TODAY`);
      } else if (this.isYesterday) {
        return this.translateService.instant(`FILTER.PERIOD.YESTERDAY`);
      } else {
        return this.capitalized(this.convertToDayFormat(this.date));
      }
    } else if (this.type == TRANSACTION_GROUP_TYPE.MONTH) {
      if (this.isCurrentYear) {
        return this.capitalized(this.convertToMonth(this.date));
      } else {
        return this.capitalized(this.convertToMonthYear(this.date));
      }
    } else {
      return null;
    }
  }

  onSelect(isSelected: boolean, transaction: Transaction, index: number) {
    if (isSelected) {
      this.selectedTransactions.push(transaction);
    } else {
      this.selectedTransactions.splice(index, 1);
    }
    this.selected.emit(this.selectedTransactions);
  }

  onDelete(transaction: Transaction) {
    this.deleted.emit(transaction);
  }

  onRevoke(transaction: Transaction) {
    this.revoked.emit(transaction);
  }

  onSign(transaction: Transaction) {
    this.signed.emit(transaction);
  }

  onSentForSign(transaction: Transaction) {
    this.sentForSign.emit(transaction);
  }

  private get date() {
    return new Date(this.group.groupDate);
  }

  private get isToday() {
    const today = new Date();
    return this.convertToDayFormat(this.date) == this.convertToDayFormat(today);
  }

  private get isYesterday() {
    const today = new Date();
    const yesterday = new Date(today.setDate(today.getDate() - 1));

    return (
      this.convertToDayFormat(this.date) == this.convertToDayFormat(yesterday)
    );
  }

  private get isCurrentYear() {
    const today = new Date();
    return this.date.getFullYear() == today.getFullYear();
  }

  private convertToDayFormat(date: Date): string {
    return formatDate(date, 'd MMMM yyyy', this.locale);
  }

  private convertToMonth(date: Date): string {
    return formatDate(date, 'LLLL', this.locale);
  }

  private convertToMonthYear(date: Date): string {
    return formatDate(date, 'LLLL y', this.locale);
  }

  private capitalized(word: string) {
    return `${word[0].toUpperCase()}${word.substr(1).toLowerCase()}`;
  }
}
