import { AccountsService } from '@accounts/index';
import { IUserGrant } from '@accounts/account.model';
import { Component, Input, OnInit } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AccountGrantsComponent } from '../account-grants/account-grants.component';
import { SingleEntityStore } from '@utils/simple-store';
import { Observable } from 'rxjs';

@Component({
  selector: 'j-account-settings',
  templateUrl: './account-settings.component.html',
  styleUrls: ['./account-settings.component.scss']
})
export class AccountSettingsComponent implements OnInit {
  @Input() companyId: string;
  @Input() accountId: string;
  users: IUserGrant[];
  errorText = null;
  constructor(private modalService: NgbModal,
              public accountsService: AccountsService) { }

  ngOnInit(): void {
    this.retrieveTotalLimitations();
  }

  async retrieveTotalLimitations() {
    try {
      await this.accountsService.retrieveTotalLimitations(this.companyId, this.accountId);
    } catch (error) {
    }
  }

  async openAccountGrants() {
    const modalRef = this.modalService
      .open(AccountGrantsComponent, {
        backdropClass: 'backdrop_light',
        windowClass: 'modal_light modal_dictionary modal-adaptive',
        centered: true,
        size: 'md'
      });
      modalRef.componentInstance.companyId = this.companyId;
      modalRef.componentInstance.accountId = this.accountId;
      try {
        await modalRef.result;
      } catch (error) {
      }
  }

}
