import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'j-settings-package-price',
  templateUrl: './settings-package-price.component.html',
  styleUrls: ['./settings-package-price.component.scss'],
})
export class SettingsPackagePriceComponent implements OnInit {
  @Input() item = {
    totalCost: 0,
    saving: 0,
    standartCost: 0
  };
  @Input() disabledButton = true;
  @Input() isWorkingDay = true;
  @Output() openModal = new EventEmitter();

  currency = this.translateService.instant('SETTINGS.PACKAGE.PRICE.CURRENCY');

  constructor(private translateService: TranslateService) {
    this.translateService.onLangChange.subscribe(() => {
      this.currency = this.translateService.instant(
        'SETTINGS.PACKAGE.PRICE.CURRENCY'
      );
    });
  }

  ngOnInit(): void {}
}
