import { AfterViewInit, Component, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DatePipe } from '@angular/common';

import { Subscription } from 'rxjs';
import { map, take } from 'rxjs/operators';

import { FilesService } from '@shared/services/files.service';
import { MaskedService } from '@shared/masked/masked.service';

import { CompanyService } from '@company/company.service';

import { IStatement, StatementService, Transaction } from '@statement/index';

import { FILTER_MODE, OPERATION_CODE } from '@filter/index';
import { FilterService } from '@filter/filter.service';
import { IFilterScheme, FILTER_PERIOD_TYPE } from '@filter/filter.model';

import { AccountsService, RegularAccount } from '@accounts/index';

import { AUTHORITY_TYPE } from '@shared/models';
import { CURRENCY } from '@shared/currency/currency.model';
import { NavService } from '@shared/nav/nav.service';

const PAGE_START = 1;
const PAGE_SIZE = 10;

@Component({
  selector: 'j-account',
  templateUrl: './account.component.html',
  styleUrls: ['./account.component.scss'],
})
export class AccountComponent implements OnDestroy {
  account: RegularAccount;
  statement: IStatement;
  activeTab: string;
  errorText: string;
  isDetailsShown = false;
  isLoading = true;
  isLoadingExtra = true;
  isReady = false;
  hasMorePages = true;
  filter: IFilterScheme;
  iban: string;
  selectedTransactions: Transaction[] = [];
  selectedCount = 0;

  readonly subscription = new Subscription();
  private pageNumber = PAGE_START;
  private pageSize = PAGE_SIZE;

  private cashedRouteQuery = JSON.parse(
    sessionStorage.getItem('accountRouteQuery')
  );

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private company: CompanyService,
    private filterService: FilterService,
    private accountsService: AccountsService,
    private datePipe: DatePipe,
    private filesService: FilesService,
    private statementService: StatementService,
    private maskedService: MaskedService,
    private navService: NavService
  ) {
    const routeParamSubscription = this.route.params.subscribe((params) => {
      this.iban = params.iban;
      this.loadData();
    });
    this.subscription.add(routeParamSubscription);

    const routeDataSubscription = this.route.data.subscribe((data) => {
      this.activeTab = data.tab;
    });

    this.subscription.add(routeDataSubscription);

    if (this.cashedRouteQuery) {
      this.filter = this.mapQueryParams(this.cashedRouteQuery);
      this.filterService.updateExtra(this.filter);
    } else {
      const routeQuerySubscription = this.route.queryParams
        .pipe(take(1))
        .subscribe((queryParams) => {
          this.updateCashedRouteQuery(queryParams);

          this.filter = this.mapQueryParams(queryParams);
          this.filterService.updateExtra(this.filter);
        });
      this.subscription.add(routeQuerySubscription);
    }

    const filterSubscription = this.filterService
      .getScheme()
      .subscribe((scheme) => {
        this.filter = Object.assign({}, scheme);
        this.pageNumber = 1;
        this.hasMorePages = true;
        this.isLoading = true;
        this.updateQueryParams();
        this.loadStatement();
      });

    this.subscription.add(filterSubscription);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  onTabClick(tab: string, event: Event) {
    event.preventDefault();
    this.router.navigate([tab], { relativeTo: this.route.parent });
  }

  onClose() {
    this.navigateToHome();
  }

  onScroll() {
    if (this.hasMorePages && !this.isLoading) {
      this.pageNumber++;
      this.loadStatement();
    }
  }

  get emptyLabel() {
    if (this.account.isMigrated) {
      return 'ACCOUNTS.EMPTY_MIGRATED';
    }

    return 'ACCOUNTS.EMPTY_STATEMENT';
  }

  get isSelectableForExport() {
    return (
      this.filter.operationType === OPERATION_CODE.INCOME &&
      this.account.balance.currency === CURRENCY.KZT
    );
  }

  async onDownload(exportType: string) {
    const downloadStatement = await this.accountsService.getStatement(
      this.iban,
      this.filter,
      this.pageNumber,
      this.pageSize,
      exportType,
      this.company.currentId
    );

    let extension = exportType.substring(
      exportType.lastIndexOf('/') + 1,
      exportType.length
    );

    if (extension === '1c') {
      extension += '.txt';
    }

    const filename = `${this.iban}.${extension}`;

    this.filesService.downloadFile(downloadStatement, filename);
  }

  async loadData() {
    try {
      await this.loadAccount();
    } catch (error) {
      console.error(error);
    } finally {
      this.isReady = true;
    }
  }

  async loadAccount() {
    try {
      this.account = await this.accountsService.getAccountByIban(
        this.company.currentId,
        this.iban
      );

      if (this.account.isMigrated) {
        this.accountsService.retrieveMergeDate();
      }
    } catch (error) {
      this.navigateToHome();
    }
  }

  async loadStatement() {
    this.errorText = '';
    this.isLoadingExtra = true;

    try {
      if (
        this.filter.periodType &&
        this.filter.periodType !== FILTER_PERIOD_TYPE.CUSTOM
      ) {
        const period = this.filterService.PERIOD_LIST.find((item) => {
          return item.type === this.filter.periodType;
        });
        this.filter.periodFrom = period.dates.from;
        this.filter.periodTo = period.dates.to;
      }

      const retrievedStatement = await this.accountsService.getStatement(
        this.iban,
        this.filter,
        this.pageNumber,
        this.pageSize
      );

      this.hasMorePages =
        this.pageSize <= retrievedStatement.totalTransactionsCount;

      this.statement = retrievedStatement;
    } catch (error) {
      this.errorText = error;
    } finally {
      this.isReady = true;
      this.isLoading = false;
      this.isLoadingExtra = false;
    }
  }

  get balance() {
    return this.account && this.account.balance;
  }

  private navigateToHome() {
    this.navService.retrieveContractBadges();
    this.navService.retrieveLetterBadges();

    this.router.navigate(['../..'], {
      relativeTo: this.route,
    });
  }

  private mapQueryParams(queryParams: any) {
    const filter = Object.assign(
      {},
      this.filterService.getDefaults(FILTER_MODE.ACCOUNTS)
    );

    for (const key in queryParams) {
      if (Object.prototype.hasOwnProperty.call(queryParams, key)) {
        const param = queryParams[key];

        if (param && param != null && param !== 'null') {
          if (key === 'periodFrom' || key === 'periodTo') {
            filter[key] = new Date(param);
          } else {
            filter[key] = param;
          }
        }
      }
    }

    return filter;
  }

  private updateQueryParams() {
    const queryParams: any = Object.assign({}, this.filter);

    const newFilter = {};

    for (const key in queryParams) {
      if (Object.prototype.hasOwnProperty.call(queryParams, key)) {
        const param = queryParams[key];

        if (param && param != null && param !== 'null') {
          if (key === 'periodFrom' || key === 'periodTo') {
            newFilter[key] = this.datePipe.transform(param, 'yyyy-MM-dd');
          } else {
            newFilter[key] = param;
          }

          if (key === 'amountFrom' || key === 'amountTo') {
            newFilter[key] = this.maskedService.getNumberedAmount(param);
          }
        }
      }
    }

    this.updateCashedRouteQuery(queryParams);
    this.router.navigate([], {
      queryParams: newFilter,
      relativeTo: this.route,
    });
  }

  private updateCashedRouteQuery(queryParams) {
    sessionStorage.setItem('accountRouteQuery', JSON.stringify(queryParams));
  }

  get companyId() {
    return this.company.currentId;
  }

  get isSettingsAllowed() {
    return (
      this.company.currentAuthority.authority ===
      AUTHORITY_TYPE.PRIMARY_SIGNATURE
    );
  }

  onSelect() {
    this.updateSelectable();
  }
  private updateSelectable() {
    this.resetSelectable();
    this.updateSelectedValues();
  }

  private resetSelectable() {
    this.selectedTransactions = [];
    this.selectedCount = 0;
  }

  get currentTransactions() {
    const totalTransactions = this.statement?.transactionGroups?.map(
      (group) => {
        return group.transactions;
      }
    );

    return totalTransactions?.flat() || [];
  }

  private updateSelectedValues() {
    this.currentTransactions.forEach((item) => {
      if (item.isSelected) {
        this.selectedTransactions.push(item);
        this.selectedCount++;
      }
    });
  }

  async exportSelectedTransactions(event: Event = null) {
    if (event) {
      event.preventDefault();
    }
    if (this.selectedTransactions.length > 0) {
      const exportedTransactions =
        await this.statementService.exportIncomingPaymentsList(
          this.selectedTransactions
        );
      const filename = `${this.selectedCount}_operations.pdf`;
      this.filesService.downloadFile(exportedTransactions, filename);
    }
  }
}
