import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { FEATURE_FLAG } from '@feature-flags/index';

import { paymentsRoutes } from '@payments/payments-routes';

import { CoreResourcesLoadedGuard } from '@shared/guards/core-resources-loaded.guard';
import { IsFeatureEnabledGuard } from '@shared/guards/is-feature-enabled.guard';

import {
  HomeComponent,
  AccountsListComponent,
  AccountComponent,
} from './containers';
import { homeRoutes } from './home-routes';

const routes: Routes = [
  {
    path: 'accounts',
    component: HomeComponent,
    data: {
      title: 'ACCOUNTS.TITLE',
    },
    children: [
      {
        path: '',
        component: AccountsListComponent,
      },
    ],
  },
  {
    path: 'accounts/:iban',
    children: [
      {
        path: 'statement',
        component: AccountComponent,
        data: {
          tab: 'statement',
        },
      },
      {
        path: 'requisites',
        component: AccountComponent,
        data: {
          tab: 'requisites',
        },
      },
      {
        path: 'auto-export',
        component: AccountComponent,
        data: {
          tab: 'auto-export',
        },
      },
      {
        path: 'settings',
        component: AccountComponent,
        data: {
          tab: 'settings',
        },
      },
      {
        path: '**',
        redirectTo: 'statement',
      },
    ],
  },
  {
    path: 'deposits',
    loadChildren: () =>
      import('@pages/deposits/deposits.module').then((m) => m.DepositsModule),
  },
  {
    path: 'cards',
    loadChildren: () =>
      import('@cards/cards.module').then((m) => m.CardsModule),
  },
  {
    path: 'loans',
    canActivate: [IsFeatureEnabledGuard],
    loadChildren: () =>
      import('@loans/loans.module').then((m) => m.LoansModule),
  },
  {
    canActivate: [CoreResourcesLoadedGuard],
    path: paymentsRoutes.root,
    data: {
      animation: 'routerFadeFromBottom',
    },
    loadChildren: () =>
      import('@payments/payments.module').then((m) => m.PaymentsModule),
  },
  {
    path: homeRoutes.transfers,
    loadChildren: () =>
      import('@transfers/transfers.module').then((m) => m.TransfersModule),
  },
  {
    path: homeRoutes.conversion,
    loadChildren: () =>
      import('@conversion/conversion.module').then((m) => m.ConversionModule),
  },
  {
    path: homeRoutes.history,
    loadChildren: () =>
      import('@history/history.module').then((m) => m.HistoryModule),
  },
  {
    path: 'history2',
    loadChildren: () =>
      import('@history2/history2.module').then((m) => m.History2Module),
  },
  {
    path: homeRoutes.contracts,
    loadChildren: () =>
      import('@contracts/contracts.module').then((m) => m.ContractsModule),
  },
  {
    path: homeRoutes.settings,
    loadChildren: () =>
      import('@settings/settings.module').then((m) => m.SettingsModule),
  },
  {
    path: homeRoutes.directory,
    loadChildren: () =>
      import('@directory/directory.module').then((m) => m.DirectoryModule),
  },
  {
    path: homeRoutes.letters,
    canActivate: [IsFeatureEnabledGuard],
    data: { featureFlag: FEATURE_FLAG.LETTER },
    loadChildren: () =>
      import('@letters/letters.module').then((m) => m.LettersModule),
  },
  {
    path: homeRoutes.extraSettings,
    loadChildren: () =>
      import('@extraSettings/extra-settings.module').then(
        (m) => m.ExtraSettingsModule
      ),
  },
  {
    path: homeRoutes.jmart,
    loadChildren: () =>
      import('@jmart/jmart.module').then((m) => m.JmartModule),
  },
  {
    path: homeRoutes.authorities,
    // canActivate: [IsFeatureEnabledGuard],
    // data: { featureFlag: FEATURE_FLAG.LETTER },
    loadChildren: () =>
      import('@authorities/authorities.module').then(
        (m) => m.AuthoritiesModule
      ),
  },
  {
    path: '**',
    redirectTo: 'accounts',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class HomeRoutingModule {}
