import { AccountsService, RegularAccount } from '@accounts/index';
import {
  Component,
  OnDestroy,
  OnInit,
  Output,
  EventEmitter,
  ViewChild,
  Input,
  ChangeDetectorRef,
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CompanyService } from '@company/company.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { BasePaymentContainer } from '@payments/containers/base-payment.container';
import { OrderPaymentFormModel } from '@payments/services/form-model';
import { PaymentsPipelineService } from '@payments/services/payments-pipeline.service';
import { PaymentsService } from '@payments/services/payments.service';
import { MaskedService } from '@shared/masked/masked.service';
import { DictionariesService } from '@shared/services/dictionaries.service';
import { SimpleStore } from '@utils/index';
import { combineLatest, Observable } from 'rxjs';
import { PaymentApiService } from '@payments/services/payment-api.service';
import { RecipientsService } from '@company/recipients.service';
import { PaymentsFormFactoryService } from '@payments/services/payments-form-factory.service';
import { UserService } from '@shared/services';
import { TranslateService } from '@ngx-translate/core';
import { AuthorityPermissionsService } from '@authorities/services';
import { FeatureFlagsService } from '@feature-flags/feature-flags.service';
import { debounceTime, filter, map } from 'rxjs/operators';
import { ACCOUNT_TYPE } from '@accounts/account.model';
import { CURRENCY } from '@shared/currency/currency.model';
import { AccountDropdownComponent } from '@accounts/components/account-dropdown/account-dropdown.component';
import { BankFromAccountPipe } from '@shared/pipes';
@Component({
  selector: 'j-settings-package-account',
  templateUrl: './settings-package-account.component.html',
  styleUrls: ['./settings-package-account.component.scss'],
})
export class SettingsPackageAccountComponent
  extends BasePaymentContainer<OrderPaymentFormModel>
  implements OnInit, OnDestroy
{
  @Input() isSubmitted: boolean = false;
  @Input() item: RegularAccount;
  @Output() getValue = new EventEmitter();
  @ViewChild('fromDropdown') fromDropdown: AccountDropdownComponent;
  isSelected: boolean = false;
  acceptableAccounts$: Observable<RegularAccount[]>;
  accountStore: SimpleStore<RegularAccount>;

  masks: {
    [k in keyof OrderPaymentFormModel]?: any;
  } = {
    iinOrBin: 'iin',
    bankAccount: 'tengeAccount',
    kbeCode: 'kbe',
    knp: 'knp',
    number: 'paymentNum',
    amount: 'amount',
  };

  constructor(
    public companyService: CompanyService,
    public paymentsService: PaymentsService,
    public maskService: MaskedService,

    protected route: ActivatedRoute,
    protected router: Router,
    protected api: PaymentApiService,
    protected dictionaries: DictionariesService,
    protected translateService: TranslateService,
    protected authorityPermissionsService: AuthorityPermissionsService,
    protected featureFlagsService: FeatureFlagsService,
    protected bankFromAccount: BankFromAccountPipe,
    protected paymentsFormsFactory: PaymentsFormFactoryService,
    
    private accountsService: AccountsService,
    private modalService: NgbModal,
    private pipelines: PaymentsPipelineService,

    private recipientsService: RecipientsService,
    private userService: UserService,
    private cdref: ChangeDetectorRef
  ) {
    super(
      paymentsFormsFactory.createPaymentInTengeForm(),
      api,
      route,
      router,
      companyService,
      paymentsService,
      maskService,
      modalService,
      dictionaries,
      pipelines,
      translateService,
      authorityPermissionsService,
      featureFlagsService,
      bankFromAccount
    );
  }

  ngOnInit() {
    this.accountStore = this.accountsService.store;
    this.acceptableAccounts$ = this.combineAcceptableAccounts(
      this.accountStore
    );
  }

  combineAcceptableAccounts(accountStore: AccountsService['store']) {
    return combineLatest([
      accountStore.items$.pipe(
        map((items) =>
          items.filter((item) => item.accountType === ACCOUNT_TYPE.ACCOUNT)
        )
      ),
      accountStore.fetching$,
      accountStore.fetched$,
    ]).pipe(
      debounceTime(0),
      filter(([a, fetching, fetched]) => !fetching || fetched),
      map(([arr]) =>
        arr.filter(
          (acc) => acc.balance.currency === CURRENCY.KZT && !acc.isClosed
        )
      )
    );
  }

  onSenderAccountSelect(account: RegularAccount) {
    this.isSelected = true;
    this.getValue.emit({ iban: account.iban });
  }

  ngAfterViewInit() {
    this.fromDropdown.selectAccount(this.item);
    this.cdref.detectChanges();
  }
}
