import { Injectable } from '@angular/core';
import { formatDate } from '@angular/common';

import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { TranslateService } from '@ngx-translate/core';

import { AUTHORITY_TYPE, IUserAuthority } from '@shared/models';

import { CompanyService } from '@company/company.service';
import { IAuthorizedPerson } from '@company/company.model';

import { IEdisonAuthority } from './components';
import { MaskPipe } from 'ngx-mask';
import { Masks } from '@shared/masked/masked';
import { UserService } from '@shared/services/user.service';
import { MaskedService } from '@shared/masked/masked.service';

export interface ISettingsField {
  label: string;
  value: string;
  isPhone?: boolean;
  isEmail?: boolean;
  isTimestamp?: boolean; 
}

export interface ISettingsInfo {
  title: string;
  titleAdditional?: string;
  fields: ISettingsField[];
}

@Injectable({
  providedIn: 'root',
})
export class SettingsService {
  constructor(
    private companyService: CompanyService,
    private userService: UserService,
    private translateService: TranslateService,
    private maskedService: MaskedService,
    private maskPipe: MaskPipe
  ) { }

  generateMainInfo(): ISettingsInfo {
    return {
      title: this.mapCompanyPrefix('TITLE'),
      titleAdditional: this.mapCompanyPrefix('REQUISITES'),
      fields: [
        {
          label: this.mapCompanyPrefix('NAME'),
          value: this.companyService.currentCompany.name,
        },
        {
          label: this.mapCompanyPrefix('IIN'),
          value: this.maskPipe.transform(
            this.companyService.currentCompany.bin,
            Masks.iin.mask
          ),
        },
        {
          label: this.mapCompanyPrefix('KBE'),
          value: this.companyService.currentCompany.kod,
        },
        {
          label: this.mapCompanyPrefix('ADDRESS'),
          value: this.companyService.currentCompany.legalAddress,
        },
        {
          label: this.mapCompanyPrefix('SECTOR'),
          value: this.companyService.currentCompany.economicSector,
        },
        {
          label: this.mapCompanyPrefix('REGISTER_DATE'),
          value: this.mapDate(
            this.companyService.currentCompany.registrationDate
          ),
        },
      ],
    };
  }

  generatePrivateInfo(): ISettingsInfo {
    const user = this.userService.user;

    if (!user) {
      return;
    }

    return {
      title: this.mapPrivatePrefix('TITLE'),
      fields: [
        {
          label: this.mapPrivatePrefix('NAME'),
          value: `${user.lastname || ''} ${user.firstname || ''} ${
            user.middlename || '' 
          }`,
        },
        {
          label: this.mapPrivatePrefix('IIN'),
          value: this.maskPipe.transform(user.iin, Masks.iin.mask),
        },
        {
          label: this.mapPrivatePrefix('ROLE'),
          value: this.mapRole(this.companyService.currentAuthority),
        },
        {
          label: this.mapPrivatePrefix('RIGHTS'),
          value: this.mapSignText(this.companyService.currentAuthority),
        },
        {
          label: this.mapPrivatePrefix('EMAIL'),
          value: user.email,
          // isEmail: true,
        },
        {
          label: this.mapPrivatePrefix('PHONE'),
          value: this.maskedService.getMaskedPhone(user.phone),
          isPhone: true,
        },
      ],
    };
  }

  generateContactsInfo(): ISettingsInfo {
    return {
      title: this.mapContactsPrefix('TITLE'),
      fields: [
        // {
        //   label: this.mapContactsPrefix('MANAGER'),
        //   value: '-',
        // },
        // {
        //   label: this.mapContactsPrefix('PHONE'),
        //   value: '-',
        //   // isPhone: true,
        // },
        // {
        // label: this.mapContactsPrefix('EMAIL'),
        // value: '-',
        // isEmail: true,
        // },
        {
          label: this.mapContactsPrefix('CALL_CENTER'),
          value: '7711',
          isPhone: true,
        },
      ],
    };
  }

  async generateUserLastSessionInfo(): Promise<ISettingsInfo> {
    const session = await this.userService.retrieveUserLastSession();
    return {
      title: this.mapLastSessionPrefix('TITLE'),
      fields: [
        {
          label: this.mapLastSessionPrefix('TIMESTAMP'),
          value: session.timestamp,
          isTimestamp: true,
        },
        {
          label: this.mapLastSessionPrefix('DEVICE_INFO'),
          value: session.deviceInfo,
        },
        {
          label: this.mapLastSessionPrefix('IP_ADDRESS'),
          value: session.ipAddress,
        }
      ],
    };
  }

  getAuthoritiesList(): Observable<IEdisonAuthority[]> {
    return this.companyService.getAuthorizedPersons().pipe(
      map((persons) => {
        return persons?.sort(this.sortPersons).map((person) => {
          return {
            initials: this.retrieveInitials(person),
            name: this.companyService.retrieveShortName(person),
            role: this.mapRole(person),
            startDate: person.fromDate,
            endDate: person.toDate,
            sign: this.mapSignText(person),
            color: this.mapInitialsColor(person),
          };
        });
      })
    );
  }

  private mapAuthoritiesPrefix(key: string) {
    return this.mapTranslatePrefix('AUTHORITIES', key);
  }

  private mapLastSessionPrefix(key: string) {
    return this.mapTranslatePrefix('USER_LAST_SESSION', key);
  }

  private mapCompanyPrefix(key: string) {
    return this.mapTranslatePrefix('COMPANY', key);
  }

  private mapPrivatePrefix(key: string) {
    return this.mapTranslatePrefix('PRIVATE', key);
  }

  private mapContactsPrefix(key: string) {
    return this.mapTranslatePrefix('CONTACTS', key);
  }

  private mapTranslatePrefix(domain: string, key: string) {
    return `SETTINGS.${domain}.${key}`;
  }

  private mapDate(date: Date) {
    if (!date) {
      return null;
    } else {
      return formatDate(date, 'd MMMM yyyy', this.translateService.currentLang);
    }
  }

  private sortPersons(a: IAuthorizedPerson, b: IAuthorizedPerson) {
    if (a.authority === b.authority) {
      return 0;
    } else if (a.authority === AUTHORITY_TYPE.PRIMARY_SIGNATURE) {
      return -1;
    } else if (
      a.authority === AUTHORITY_TYPE.SECONDARY_SIGNATURE &&
      b.authority === AUTHORITY_TYPE.NO_SIGNATURE
    ) {
      return -1;
    } else {
      return 1;
    }
  }

  private mapRole(person: IAuthorizedPerson | IUserAuthority) {
    switch (person.authority) {
      case AUTHORITY_TYPE.PRIMARY_SIGNATURE:
        return this.mapAuthoritiesPrefix('HEAD');
      case AUTHORITY_TYPE.SECONDARY_SIGNATURE:
        return this.mapAuthoritiesPrefix('MAIN_ACCOUNTANT');
      case AUTHORITY_TYPE.NO_SIGNATURE:
      default:
        return this.mapAuthoritiesPrefix('ACCOUNTANT');
    }
  }

  private mapSignText(person: IAuthorizedPerson | IUserAuthority) {
    switch (person.authority) {
      case AUTHORITY_TYPE.PRIMARY_SIGNATURE:
        return this.mapAuthoritiesPrefix('FIRST_SIGN');
      case AUTHORITY_TYPE.SECONDARY_SIGNATURE:
        return this.mapAuthoritiesPrefix('SECOND_SIGN');
      case AUTHORITY_TYPE.NO_SIGNATURE:
      default:
        return this.mapAuthoritiesPrefix('NO_SIGN');
    }
  }

  private mapInitialsColor(person: IAuthorizedPerson | IUserAuthority) {
    switch (person.authority) {
      case AUTHORITY_TYPE.PRIMARY_SIGNATURE:
        return 'first-sign';
      case AUTHORITY_TYPE.SECONDARY_SIGNATURE:
        return 'second-sign';
      case AUTHORITY_TYPE.NO_SIGNATURE:
      default:
        return 'no-sign';
    }
  }

  private retrieveInitials(person: IAuthorizedPerson) {
    return `${person.lastname[0]}${person.firstname[0]}`;
  }
}
