import { Component, Input, OnInit } from '@angular/core';

import { AccountsService } from '../../../services';
import { ACCOUNT_TYPE, Deposit, RegularAccount } from '../../../account.model';

@Component({
  selector: 'j-account-item',
  templateUrl: './account-item.component.html',
  styleUrls: ['./account-item.component.scss'],
})
export class AccountItemComponent implements OnInit {
  @Input() account: RegularAccount | Deposit;

  constructor(private accountsService: AccountsService) {}

  ngOnInit(): void {}

  get lockedMessage() {
    return `ACCOUNTS.STATE.${this.account.blockType}`;
  }

  get label() {
    switch (this.account.accountType) {
      case ACCOUNT_TYPE.DEPOSIT:
        return this.accountsService.getDepositName(this.account as Deposit);

      case ACCOUNT_TYPE.ACCOUNT:
      default:
        return 'ACCOUNTS.DROPDOWN.REGULAR';
    }
  }

  get isBlocked() {
    return this.account.isMigrated;
  }
}
