import { Component, OnDestroy } from '@angular/core';

import { Subscription } from 'rxjs';

import { IEdisonAuthority } from '@settings/components';
import { SettingsService } from '@settings/settings.service';
import { CompanyService } from '@company/company.service';

@Component({
  selector: 'j-settings-authorities',
  templateUrl: './settings-authorities.component.html',
  styleUrls: ['./settings-authorities.component.scss'],
})
export class SettingsAuthoritiesComponent implements OnDestroy {
  authorities: IEdisonAuthority[];

  subscription = new Subscription();

  constructor(
    private settingsService: SettingsService,
    private companyService: CompanyService
  ) {
    const authoritiesSubscription = this.settingsService
      .getAuthoritiesList()
      .subscribe((authorities) => {
        this.authorities = authorities;
      });
    this.subscription.add(authoritiesSubscription);
  }

  onRetry() {
    this.companyService.fetchAuthorizedPersons();
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
}
