import { Component, Input, OnInit } from '@angular/core';
import { Card, CARD_CATEGORY, CARD_STATUS } from '@cards/models';
import { CardsService } from '@cards/services';

@Component({
  selector: 'j-account-card',
  templateUrl: './account-card.component.html',
  styleUrls: ['./account-card.component.scss'],
})
export class AccountCardComponent implements OnInit {
  @Input() card: Card;

  constructor(private cardsService: CardsService) {}

  ngOnInit(): void {}

  get cardImage() {
    const prefix = '/assets/icons/accounts';

    switch (this.card.category) {
      case CARD_CATEGORY.PRIVATE:
        return `${prefix}/card-private.svg`;

      case CARD_CATEGORY.COMMERCIAL:
      default:
        return `${prefix}/card-commercial.svg`;
    }
  }

  get cardMps() {
    const prefix = '/assets/icons/cards/mps';

    if (this.card.cardNumber[0] === '4') {
      return `${prefix}/visa.svg`;
    }

    return `${prefix}/mastercard.svg`;
  }

  get status() {
    if (this.card.status === CARD_STATUS.ACTIVE) {
      return;
    }
    return this.cardsService.mapCardStatus(this.card.status);
  }

  get label() {
    return this.cardsService.mapCardLabel(this.card);
  }

  get isBalanceShown() {
    return this.card.category === CARD_CATEGORY.COMMERCIAL;
  }

  get maskedCardNumber() {
    const cardNumber = this.card?.cardNumber;

    if (cardNumber) {
      return `${cardNumber.substring(0, 4)}   ${cardNumber.substring(
        cardNumber.length - 4,
        cardNumber.length
      )}`;
    }

    return '';
  }
}
