import { Component, Input, OnInit } from '@angular/core';
import { Loan } from '../../../loans/models';

@Component({
  selector: 'j-loan-box',
  templateUrl: './loan-box.component.html',
  styleUrls: ['./loan-box.component.scss']
})
export class LoanBoxComponent implements OnInit {
  @Input() loan: Loan;
  @Input() isRoutable: boolean = true;

  constructor() { }

  ngOnInit(): void {
  }

  get balance() {
    return this.loan && this.loan.amount;
  }

}
