import { Deposit } from '@accounts/account.model';
import { AccountsService } from '@accounts/services';
import {
  ChangeDetectionStrategy,
  Component,
  Input,
  OnInit,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { MaskedService } from '@shared/masked/masked.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'j-deposit-box',
  templateUrl: './deposit-box.component.html',
  styleUrls: ['./deposit-box.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DepositBoxComponent implements OnInit {
  @Input() deposit: Deposit;
  @Input() isRoutable: boolean = true;

  depositName: string;
  subscription = new Subscription();

  constructor(
    private translateService: TranslateService,
    private maskedService: MaskedService,
    private accountsService: AccountsService
  ) {
    this.subscription.add(
      this.translateService.onLangChange.subscribe(() => {
        this.mapDepositName();
      })
    );
  }

  ngOnInit() {
    this.mapDepositName();
  }

  get isLargeAmount() {
    return (
      this.deposit?.balance &&
      this.maskedService.getMaskedAmount(this.deposit.balance.amount).length >
        16
    );
  }

  private mapDepositName() {
    this.depositName = this.accountsService.getDepositName(this.deposit);
  }
}
