import { Component, Input } from '@angular/core';
import { Router } from '@angular/router';
import { ILoanScheduleField, Loan } from '@loans/models';
import { LoanService } from '@loans/services';
import { FilesService } from '@shared/services';
import { Company } from '@company/company.model';

@Component({
  selector: 'j-loan-info',
  templateUrl: './loan-info.component.html',
  styleUrls: ['./loan-info.component.scss']
})
export class LoanInfoComponent {
  @Input() loan: Loan;
  @Input() company: Company;
  @Input() currentPayment: ILoanScheduleField;

  constructor(
    private router: Router,
    private loanService: LoanService,
    private filesService: FilesService,
  ) {}

  async onDownload() {
    try {
      const pdf = await this.loanService.getPdf(this.loan.id);
      this.filesService.downloadFile(
        pdf,
        `${this.loan.colvirId}.pdf`
      );
    } catch (error) {
      console.error(error);
    }
  }

  get getCurrentPaymentObj() {
    return {amount: this.currentPayment?.remainDebt, currency: this.loan.amount.currency};
  }
}
