import { Injectable } from '@angular/core';
import { IMoney } from './currency.model';

import { MaskedService } from '@shared/masked/masked.service';

@Injectable({
  providedIn: 'root',
})
export class CurrencyService {
  constructor(private maskedService: MaskedService) {}

  formatBalance(balance: IMoney, showSign: boolean = false) {
    if (balance && balance.amount != null && balance.currency) {
      const currencyBalance = this.maskedService.formatAmount(
        Math.abs(balance.amount)
      );

      let sign = '';
      if (showSign && balance.amount > 0) {
        sign = `+ `;
      } else if (balance.amount < 0) {
        sign = `- `;
      }

      return `${sign}${currencyBalance}`;
    }
  }
}
