import {
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Card } from '@cards/models';
import { CardsService } from '@cards/services';
import { Subscription } from 'rxjs';

@Component({
  selector: 'j-card-header',
  templateUrl: './card-header.component.html',
  styleUrls: ['./card-header.component.scss'],
})
export class CardHeaderComponent implements OnDestroy {
  isLoading: boolean = true;
  cardId: string;
  card: Card;

  errorText: string;

  @Output() loaded = new EventEmitter<Card>();
  @Output() failed = new EventEmitter();
  readonly subscription = new Subscription();

  constructor(
    private route: ActivatedRoute,
    private cardsService: CardsService
  ) {
    const routeParamsSubscription = this.route.params.subscribe(
      (routeParams) => {
        this.cardId = routeParams.id;
        this.init();
      }
    );
    this.subscription.add(routeParamsSubscription);
  }
  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  private init() {
    this.loadCard();
  }

  private async loadCard() {
    this.isLoading = true;

    try {
      this.card = await this.cardsService.getCardById(this.cardId);
      this.loaded.emit(this.card);
    } catch (error) {
      this.errorText = error.description;
      this.failed.emit();
    } finally {
      this.isLoading = false;
    }
  }
}
