import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { CardRenameModalComponent } from '@cards/components';
import { Card, CARD_STATUS } from '@cards/models';
import { CardsService } from '@cards/services';
import { CompanyService } from '@company/company.service';
import { NgbModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import { Subscription } from 'rxjs';

const MODAL_CONFIG: Partial<NgbModalConfig> = {
  backdropClass: 'backdrop_light',
  windowClass: 'modal_light modal_dictionary',
  backdrop: 'static',
  centered: true,
};
@Component({
  selector: 'j-card-settings',
  templateUrl: './card-settings.component.html',
  styleUrls: ['./card-settings.component.scss'],
})
export class CardSettingsComponent implements OnDestroy {
  card: Card;
  isLoaded: boolean = false;
  
  isPrimaryAuthority: boolean = this.companyService.isPrimaryAuthority();

  activatedControl: FormControl;
  hiddenControl: FormControl;

  subscription = new Subscription();

  constructor(
    private router: Router,
    private cardsService: CardsService,
    private companyService: CompanyService,
    private modalService: NgbModal
  ) {}

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  onCardLoad(card: Card) {
    this.init(card);

    const cardsUpdateSubscription = this.cardsService.cardsUpdated$.subscribe(
      (cardsList) => {
        const updatedCard = this.cardsService.findCardById(this.card.cardId);
        this.init(updatedCard);
      }
    );

    this.subscription.add(cardsUpdateSubscription);
  }

  init(card: Card) {
    this.card = card;
    this.initActivatedControl();
    this.initHiddenControl();
    this.isLoaded = true;
  }

  onCardFail() {
    this.activatedControl = new FormControl({
      value: false,
      disabled: false,
    });

    this.hiddenControl = new FormControl({
      value: false,
      disabled: false,
    });

    this.isLoaded = true;
  }

  onClose() {
    this.navigateToHome();
  }

  async onCardRename() {
    const modal = this.modalService.open(
      CardRenameModalComponent,
      MODAL_CONFIG
    );
    (modal.componentInstance as CardRenameModalComponent).card = this.card;

    try {
      const alias = await modal.result;
      this.card.alias = alias;
    } catch (error) {}
  }

  private navigateToHome() {
    this.router.navigate(['/']);
  }

  private initActivatedControl() {
    this.activatedControl = new FormControl({
      value: this.card.status === CARD_STATUS.TEMPORARILY_LOCK,
      disabled: !(
        this.card?.status == CARD_STATUS.ACTIVE ||
        this.card?.status == CARD_STATUS.TEMPORARILY_LOCK
      ),
    });

    this.subscription.add(
      this.activatedControl.valueChanges.subscribe((value) => {
        this.handleActivatedSwitch(value);
      })
    );
  }

  private initHiddenControl() {
    this.hiddenControl = new FormControl(this.card.hidden);

    this.subscription.add(
      this.hiddenControl.valueChanges.subscribe((value) => {
        this.handleHiddenSwitch(value);
      })
    );
  }

  private async handleActivatedSwitch(value: boolean) {
    try {
      await this.cardsService.changeCardStatus(this.card.cardId, value);
    } catch (error) {
      this.activatedControl.setValue(!value, { emitEvent: false });
    }
  }

  private async handleHiddenSwitch(value: boolean) {
    try {
      await this.cardsService.changeCardVisibility(this.card.cardId, value);
    } catch (error) {
      this.hiddenControl.setValue(!value, { emitEvent: false });
    }
  }
}
