import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SettingsComponent } from './settings.component';

import {
  SettingsMainComponent,
  SettingsPasswordComponent,
  SettingsAuthoritiesComponent,
  SettingsPackageComponent,
} from './containers';
import { SettingsPackageAdditionalComponent } from './modals/settings-package-additional/settings-package-additional.component';
import { SettingsPackageFaqComponent } from './modals/settings-package-faq/settings-package-faq.component';
import { IsFeatureEnabledGuard } from '@shared/guards/is-feature-enabled.guard';
import { FEATURE_FLAG } from '@feature-flags/feature-flags.model';

const routes: Routes = [
  {
    path: '',
    component: SettingsComponent,
    data: {
      title: 'SETTINGS.TITLE',
    },
    children: [
      {
        path: 'main',
        component: SettingsMainComponent,
      },
      {
        path: 'authorities',
        component: SettingsAuthoritiesComponent,
      },
      {
        path: 'password',
        component: SettingsPasswordComponent,
      },
      {
        path: 'package',
        component: SettingsPackageComponent,
        canActivate: [IsFeatureEnabledGuard],
        data: { featureFlag: FEATURE_FLAG.TARIFF_PACKAGES},
        children: [
          {
            path: ':type',
            component: SettingsPackageAdditionalComponent,
            outlet: 'additional',
          },
          {
            path: ':key',
            component: SettingsPackageFaqComponent,
            outlet: 'faq',
          },
        ]
      },
      {
        path: '**',
        redirectTo: 'main',
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SettingsRoutingModule {}
