import { DOCUMENT } from '@angular/common';
import {
  Component,
  Inject,
  Input,
  OnDestroy,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { IFxContract, IFxContractRecipient } from '@shared/models';

@Component({
  selector: 'j-fx-contract-details',
  templateUrl: './fx-contract-details.component.html',
  styleUrls: ['./fx-contract-details.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FxContractDetailsComponent implements OnDestroy {
  @Input() contract: IFxContract;

  isShown: boolean = false;

  constructor(
    @Inject(DOCUMENT) private document: Document,
    private translateService: TranslateService
  ) {}

  get primaryRecipient() {
    return this.contract.recipients?.find((recipient) => recipient.primary);
  }

  get extraRecipients() {
    return this.contract.recipients?.filter((recipient) => !recipient.primary);
  }

  mapBank(recipient: IFxContractRecipient) {
    const { bankBic, bankName } = recipient;
    const name = bankBic
      ? bankName
        ? `- ${bankName}`
        : ''
      : bankName
      ? bankName
      : '';

    return `${bankBic}${name}`;
  }

  mapCountry(recipient: IFxContractRecipient) {
    if (recipient.country) {
      const code = recipient.country.code;
      let name = '';

      switch (this.translateService.currentLang) {
        case 'ru':
        default:
          name = recipient.country?.nameRu;
        case 'kk':
          name = recipient.country?.nameKk;
        case 'en':
          name = recipient.country?.nameEn;
      }

      return `${code} - ${name}`;
    }

    if (recipient.primary && this.contract.contractor?.country) {
      return `${this.contract.contractor.country.code} - ${this.contract.contractor.country.name}`;
    }

    if (recipient.countryCode) {
      return recipient.countryCode;
    }

    return '-';
  }

  ngOnDestroy() {
    this.document.body.classList.remove('overflow-hidden');
    this.close();
  }

  public show() {
    this.document.body.classList.add('overflow-hidden');
    this.isShown = true;
  }

  close(event: Event = null) {
    event?.preventDefault();
    this.document.body.classList.remove('overflow-hidden');
    this.isShown = false;
  }
}
