import { Component, Input, Output } from '@angular/core';
import {ItemCardSettingsPackage} from '../../models/index';
import { Router } from '@angular/router';
import { EventEmitter } from '@angular/core';
@Component({
  selector: 'j-settings-package-card',
  templateUrl: './settings-package-card.component.html',
  styleUrls: ['./settings-package-card.component.scss']
})
export class SettingsPackageCardComponent {
  @Input() itemCardSettingsPackage: ItemCardSettingsPackage  | null;
  @Input() additionalType: 'payment' | 'refill';
  @Output() additionalPackage: EventEmitter<string> = new EventEmitter<string>();
  constructor(private router: Router) { }
  navigateAdditionalPackage() {
    this.additionalPackage.emit(this.additionalType);
    if (this.additionalType) {
      this.router.navigate(
        ['/settings/package', { outlets: { additional: [this.additionalType] } }]
      ); }
  }
}
