import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Card } from '@cards/models';
import { CardsService } from '@cards/services';
import { CompanyService } from '@company/company.service';
import { TranslateService } from '@ngx-translate/core';
import { BankFromAccountPipe, FormatIbanPipe } from '@shared/pipes';
import { FilesService } from '@shared/services/files.service';
import { NotifierService } from 'angular-notifier';
import { Subscription } from 'rxjs';

@Component({
  selector: 'j-card-info',
  templateUrl: './card-info.component.html',
  styleUrls: ['./card-info.component.scss'],
})
export class CardInfoComponent implements OnDestroy {
  isLoaded: boolean = false;

  qrFile;

  card: Card;

  errorText: string;

  status: {
    label: string;
    value: string;
    valueToCopy?: string;
  }[];

  requisites: {
    label: string;
    value: string;
    valueToCopy?: string;
  }[];

  textToCopy: string;

  subscription = new Subscription();

  constructor(
    private router: Router,
    private companyService: CompanyService,
    private bankFromAccount: BankFromAccountPipe,
    private formatIban: FormatIbanPipe,
    private cardsService: CardsService,
    private notifierService: NotifierService,
    private translateService: TranslateService,
    private filesService: FilesService
  ) {}

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  onCardLoad(card: Card) {
    this.init(card);

    const cardsUpdateSubscription = this.cardsService.cardsUpdated$.subscribe(
      (cardsList) => {
        const updatedCard = this.cardsService.findCardById(this.card.cardId);
        this.init(updatedCard);
      }
    );

    this.subscription.add(cardsUpdateSubscription);
  }

  async init(card: Card) {
    this.card = card;

    this.generateStatus();
    this.generateRequisites();
    this.generateTextToCopy();

    this.loadQr();

    this.isLoaded = true;
  }

  onCardFail() {
    this.isLoaded = true;
    this.errorText = this.appendTranslationPrefix(`FAILED`);
  }

  onClose() {
    this.navigateToHome();
  }

  onCopy() {
    this.notifierService.notify(
      'default',
      this.translateService.instant(this.appendTranslationPrefix('COPIED'))
    );
  }

  async loadQr() {
    this.qrFile = await this.cardsService.getQrPrint(
      this.card.cardId,
      this.card.iban
    );
  }

  onQrPrint(event: Event) {
    event.preventDefault();

    this.filesService.downloadFile(
      this.qrFile,
      `${this.card.formattedFullName} ${this.card.maskedNumber} QR.jpg`
    );
  }

  async onDownload() {
    try {
      const pdf = await this.cardsService.getPdf(this.card.cardId);
      this.filesService.downloadFile(
        pdf,
        `${this.card.formattedFullName} ${this.card.maskedNumber}.pdf`
      );
    } catch (error) {
      console.error(error);
    }
  }

  private generateStatus() {
    this.status = [
      {
        label: this.appendTranslationPrefix('STATUS'),
        value: this.cardsService.mapCardStatus(this.card.status),
      },
      // {
      //   label: this.appendTranslationPrefix('EXPIRE_DATE'),
      //   value: this.card.expireDate
      // }
    ];
  }

  private generateRequisites() {
    this.requisites = [
      {
        label: this.appendTranslationPrefix('OWNER'),
        value: this.card.formattedFullName,
      },
      // {
      //   label: this.appendTranslationPrefix('IIN'),
      //   value: this.maskPipe.transform(
      //     this.companyService.currentCompany.bin,
      //     Masks.iin.mask
      //   ),
      // },
      {
        label: this.appendTranslationPrefix('KBE'),
        value: this.companyService.currentCompany.kod,
      },
      {
        label: this.appendTranslationPrefix('IBAN'),
        value: this.formatIban.transform(this.card.iban),
        valueToCopy: this.card.iban,
      },
      {
        label: this.appendTranslationPrefix('BANK'),
        value: this.bankFromAccount.transform(this.card.iban, 'name'),
      },
      {
        label: this.appendTranslationPrefix('BIC'),
        value: this.bankFromAccount.transform(this.card.iban, 'bic'),
      },
    ];
  }

  private generateTextToCopy() {
    this.textToCopy = this.requisites
      .map((item) => {
        if (!(item.value || item.valueToCopy)) {
          return;
        }
        return `${this.translateService.instant(item.label)}: ${
          item.valueToCopy || item.value
        }`;
      })
      .join('\n');
  }

  private navigateToHome() {
    this.router.navigate(['/']);
  }

  private appendTranslationPrefix(key: string) {
    return `CARDS.INFO.${key}`;
  }
}
