import {
  Component,
  Input,
  Output,
  EventEmitter,
  OnInit,
  OnDestroy,
  ViewEncapsulation,
  Inject,
} from '@angular/core';
import { DOCUMENT } from '@angular/common';
import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';

import { TranslateService } from '@ngx-translate/core';
import { NotifierService } from 'angular-notifier';

import { ERRORS } from '@api/errors';

import { IAuthorizedPerson } from '@company/company.model';
import { CompanyService } from '@company/company.service';

import { STATEMENT_MODE } from '@statement/models/statement.model';
import { StatementService } from '@statement/statement.service';

import { Payment } from '@payments/models';

import { CURRENCY, IMoney } from '@shared/currency/currency.model';
import {
  IExchangeRate,
  IForeignPaymentDetails,
  IHistoryPayment,
  IHistoryPaymentStatus,
  IPaymentEmployee,
  PAYMENT_TYPE,
} from '@shared/models';
import { PaymentsSharedService } from '@shared/services/payments-shared.service';

import {
  Transaction,
  TRANSACTION_CATEGORY,
} from '../../models/transaction.model';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AmlMainModalComponent, AmlWarningModalComponent } from '@payments/components';

@Component({
  selector: 'j-transaction-details',
  templateUrl: './transaction-details.component.html',
  styleUrls: ['./transaction-details.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: [
    trigger('details', [
      state(
        'shown',
        style({
          // right: '0%',
          // transform: 'scale(1)',
          opacity: '1',
        })
      ),
      state(
        'hidden',
        style({
          // right: '-100%',
          // transform: 'scale(0.8)',
          opacity: '0',
        })
      ),
      transition('shown=>hidden', animate('250ms')),
      transition('hidden=>shown', animate('250ms')),
    ]),
    trigger('employees', [
      state(
        'shown',
        style({
          transform: 'scale(1)',
          right: '0%',
          opacity: '1',
        })
      ),
      state(
        'hidden',
        style({
          transform: 'scale(0.5)',
          right: '-100%',
          opacity: '0',
        })
      ),
      transition('shown=>hidden', animate('250ms')),
      transition('hidden=>shown', animate('250ms')),
    ]),
  ],
})
export class TransactionDetailsComponent implements OnInit, OnDestroy {
  TRANSACTION_CATEGORY = TRANSACTION_CATEGORY;
  STATEMENT_MODE = STATEMENT_MODE;

  @Input() mode: STATEMENT_MODE;
  @Input() transaction: Transaction;
  @Input() hasExtraPadding: boolean = false;

  @Output() repeated = new EventEmitter<Transaction>();
  @Output() edited = new EventEmitter<Transaction>();
  @Output() deleted = new EventEmitter<Transaction>();
  @Output() signed = new EventEmitter<Transaction>();
  @Output() sentForSign = new EventEmitter<Transaction>();
  @Output() exported = new EventEmitter<Transaction>();
  @Output() downloaded = new EventEmitter<Transaction>();
  @Output() revoked = new EventEmitter<Transaction>();
  @Output() refundDetailsOpened = new EventEmitter<Transaction>();
  @Output() closed = new EventEmitter();

  isAnimating: boolean = false;
  isSelectorShown: boolean = false;
  wrapperClasses = '';
  selectorClasses = '';

  transactionPaymentId: string = null;
  hasTwoActions: boolean;
  hasThreeActions: boolean;

  @Input()
  signers: IAuthorizedPerson[];

  paymentStatus: {
    label: string;
    classList: string;
  };

  isEmployeesShown = false;

  employees: IPaymentEmployee[];

  loadedPayment: Payment | IHistoryPayment;
  foreignPaymentDetails: IForeignPaymentDetails;

  conversionRateFrom: IMoney;
  conversionRateTo: IMoney;
  conversionToAmount: IMoney;

  parentPayment;
  isParentPaymentShown: boolean = false;
  isParentPaymentLoading: boolean = false;

  constructor(
    private statementService: StatementService,
    private translateService: TranslateService,
    private paymentsSharedService: PaymentsSharedService,
    private companyService: CompanyService,
    private notifierService: NotifierService,
    private modalService: NgbModal,
    @Inject(DOCUMENT) private document: Document
  ) {}

  get title() {
    return this.statementService.getTransactionTitle(this.transaction, {
      name: this.transaction?.associatedField?.depositName || '',
    });
  }

  get country() {
    const country = this.loadedPayment?.paymentRecipient?.country;

    if (country) {
      let countryName;

      switch (this.translateService.currentLang) {
        case 'ru':
        default:
          countryName = country.nameRu;
          break;
        case 'kk':
          countryName = country.nameKk;
          break;
        case 'en':
          countryName = country.nameEn;
          break;
      }

      return `${country.code} - ${countryName}`;
    }

    return this.transaction?.foreignRecipient?.countryCode || '-';
  }

  get isTransferOwnGroup() {
    return (
      this.transaction.paymentType === PAYMENT_TYPE.IN_BETWEEN ||
      this.transaction.paymentType === PAYMENT_TYPE.CARD_TO_ACCOUNT ||
      this.transaction.paymentType === PAYMENT_TYPE.ACCOUNT_TO_CARD ||
      this.transaction.paymentType === PAYMENT_TYPE.CARD_TO_CARD
    );
  }

  get isForeignGroup() {
    return this.transaction.paymentType === PAYMENT_TYPE.FOREIGN;
  }

  get isConversionGroup() {
    return this.transaction.paymentType === PAYMENT_TYPE.CONVERSION;
  }

  get isDefaultGroup() {
    return (
      !this.isTransferOwnGroup &&
      !this.isForeignGroup &&
      !this.isConversionGroup
    );
  }

  get payerCardNumber() {
    const cardNumber =
      (this.loadedPayment?.details as any)?.associatedField?.parameters
        ?.payerCardNumber || null;

    if (!cardNumber) {
      return null;
    }

    return `${cardNumber.substring(0, 4)}   ${cardNumber.substring(
      cardNumber.length - 4,
      cardNumber.length
    )}`;
  }

  get recipientCardNumber() {
    const cardNumber =
      (this.loadedPayment?.details as any)?.associatedField?.parameters
        ?.recipientCardNumber || null;

    if (!cardNumber) {
      return null;
    }

    return `${cardNumber.substring(0, 4)}   ${cardNumber.substring(
      cardNumber.length - 4,
      cardNumber.length
    )}`;
  }

  get isDownloadableKzt() {
    return (
      this.transaction.isCredit &&
      this.transaction.credit?.currency === CURRENCY.KZT
    );
  }

  showAmlModal() {
    this.modalService.open(AmlWarningModalComponent , {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_sm',
      centered: true,
    });
  }

  onClose(event: Event) {
    event.preventDefault();
    this.closed.emit();
  }

  ngOnInit() {
    this.paymentStatus = this.statementService.generatePaymentStatusLabel(
      this.transaction,
      true
    );

    this.loadPaymentById();
    this.generateSignersList();
    this.countActions();
  }

  ngOnDestroy() {
    this.document.body.classList.remove('overflow-hidden');
    this.closed.emit();
  }

  repeatPayment(
    event: Event = null,
    transaction: Transaction = this.transaction
  ) {
    event?.preventDefault();

    this.repeated.emit(transaction);
  }

  editPayment(event: Event, transaction: Transaction = this.transaction) {
    event?.preventDefault();

    this.edited.emit(transaction);
  }

  signPayment(event: Event, transaction: Transaction = this.transaction) {
    event?.preventDefault();
    event?.stopPropagation();

    this.signed.emit(transaction);
  }

  sendForSign(event: Event, transaction: Transaction = this.transaction) {
    event?.preventDefault();
    event?.stopPropagation();

    this.sentForSign.emit(transaction);
  }

  deletePayment(event: Event, transaction: Transaction = this.transaction) {
    event?.preventDefault();
    event?.stopPropagation();

    this.deleted.emit(transaction);
  }

  revokePayment(event: Event, transaction: Transaction = this.transaction) {
    event?.preventDefault();
    event?.stopPropagation();

    this.revoked.emit(transaction);
  }

  exportPayment(event: Event, transaction: Transaction = this.transaction) {
    event?.preventDefault();
    event?.stopPropagation();

    this.exported.emit(transaction);
  }

  downloadIncomingPayment(
    event: Event,
    transaction: Transaction = this.transaction
  ) {
    event?.preventDefault();
    event?.stopPropagation();

    this.downloaded.emit(transaction);
  }

  showEmployees(event: Event) {
    event.preventDefault();

    this.isEmployeesShown = true;
  }

  hideEmployees() {
    this.isEmployeesShown = false;
  }

  isSignerShown(value: string) {
    return value && value != ' ' && value != ' ';
  }

  mapMoney(amount: number, currency: CURRENCY) {
    return {
      amount,
      currency,
    };
  }

  mapActionLabel(action: string) {
    const prefix = 'STATEMENT';
    switch (this.transaction.paymentType) {
      case PAYMENT_TYPE.IN_BETWEEN:
        return `${prefix}.TRANSFER_ACTIONS.${action}`;

      case PAYMENT_TYPE.CONVERSION:
        return `${prefix}.CONVERSION_ACTIONS.${action}`;

      default:
        return `${prefix}.DETAILS.${action}`;
    }
  }

  async loadPaymentById() {
    if (this.transaction.isPayment) {
      this.loadedPayment = await this.statementService.getPaymentDetails(
        this.transaction.id
      );
      this.employees = this.loadedPayment.paymentEmployees;

      this.applyConversionRules(this.loadedPayment);
    } else if (
      this.transaction.paymentColvirId &&
      this.transaction.category != TRANSACTION_CATEGORY.COMMISSION
    ) {
      this.loadedPayment =
        await this.statementService.getPaymentDetailsFromColvir(
          this.transaction.paymentColvirId
        );
      this.transaction.paymentStatus = this.loadedPayment
        .status as IHistoryPaymentStatus;

      if (this.loadedPayment.id) {
        this.transaction.paymentId = this.loadedPayment.id;

        this.transaction.isExportable = true;
        this.transaction.isRepeatable = true;

        this.countActions();
      }
    }
  }

  async loadParentPayment(event: Event) {
    event.preventDefault();

    if (this.parentPayment) {
      this.isParentPaymentShown = true;
    } else {
      this.isParentPaymentLoading = true;

      try {
        this.parentPayment = await this.statementService.loadParentPayment(
          this.transaction.iban,
          this.transaction.parentReference
        );
        this.isParentPaymentShown = true;
      } catch (error) {
        if (error === ERRORS.RESOURCE_NOT_FOUND) {
          this.notifierService.notify(
            'error',
            this.translateService.instant(
              'STATEMENT.DETAILS.INITIAL_PAYMENT_NOT_FOUND'
            )
          );
        } else {
          this.notifierService.notify(
            'error',
            this.translateService.instant('ERRORS.UNKNOWN_ERROR')
          );
        }
      } finally {
        this.isParentPaymentLoading = false;
      }
    }
  }

  hideInitialPayment() {
    this.isParentPaymentShown = false;
  }

  private applyConversionRules(payment: Payment) {
    const exchangeRate = payment.details?.exchangeRate;

    if (payment.type !== PAYMENT_TYPE.CONVERSION || !exchangeRate) {
      return;
    }

    this.mapConversionToAmount(exchangeRate);
    this.calculateConversionRates(exchangeRate);
  }

  private mapConversionToAmount(exchangeRate: IExchangeRate) {
    const { sum, currencyFrom, currencyTo, calculatedSum } = exchangeRate;

    this.conversionToAmount =
      sum.currency === currencyFrom
        ? {
            amount: calculatedSum,
            currency: currencyTo,
          }
        : sum;
  }

  private calculateConversionRates(exhangeRate: IExchangeRate) {
    const { from, to } = this.paymentsSharedService.getRateAmounts(exhangeRate);

    this.conversionRateFrom = from;
    this.conversionRateTo = to;
  }

  private generateSignersList() {
    this.signers = this.companyService.generateSignersList(
      this.transaction.paymentStatus?.code,
      this.transaction.signatureScheme,
      this.transaction.signatures
    );
  }

  private countActions() {
    let count = [
      'isEditable',
      'isRemovable',
      'isRepeatable',
      'isRevokable',
    ].reduce((acc, key) => {
      return (this.transaction[key] && ++acc) || acc;
    }, 0);

    if (
      !this.transaction.isRepeatable &&
      !this.transaction.isPayment &&
      this.transaction.category != TRANSACTION_CATEGORY.COMMISSION &&
      this.transaction.paymentColvirId
    ) {
      count++;
    }

    this.hasTwoActions = count === 2;
    this.hasThreeActions = count === 3;
  }

  showRefundDetails(event: Event, transaction: Transaction = this.transaction) {
    event?.preventDefault();
    event?.stopPropagation();
    if (!this.isAnimating) {
      this.isAnimating = true;
      this.isSelectorShown = true;
      this.wrapperClasses = 'animate__animated zoomInJ';

      setTimeout(() => {
        this.document.body.classList.add('overflow-hidden');
        this.selectorClasses = 'is-shown';
      }, 250);

      setTimeout(() => {
        this.wrapperClasses = '';
        this.isAnimating = false;
      }, 300);
    }
  }

  hideSelector() {
    if (!this.isAnimating) {
      this.isAnimating = true;
      this.selectorClasses = '';

      setTimeout(() => {
        this.document.body.classList.remove('overflow-hidden');
        this.wrapperClasses = 'animate__animated zoomOutJ';
      }, 250);

      setTimeout(() => {
        this.wrapperClasses = '';
        this.isSelectorShown = false;
        this.isAnimating = false;
      }, 300);
    }
  }
}
