import { formatDate } from '@angular/common';
import { Component, ElementRef, Inject, Input, LOCALE_ID, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { myProgressPaymentData, myProgressRefillData } from '@settings/data';
import { ItemCardSettingsPackage, ItemFormsSettingsSale, MyItemSettingsProgress, PackageRest } from '@settings/models';
import dayjs from 'dayjs';

@Component({
  selector: 'j-package-connected',
  templateUrl: './package-connected.component.html',
  styleUrls: ['./package-connected.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class PackageConnectedComponent implements OnInit {
  @Input() isCollapsed = false;
  @ViewChild('collapse') collapse: ElementRef<HTMLElement>;

  public payment: ItemCardSettingsPackage = new ItemCardSettingsPackage(
    'SETTINGS.PACKAGE.CALCULATE.PAYMENTS',
    '',
    ''
  );
  public paymentTitle: string = 'SETTINGS.PACKAGE.CALCULATE.PAYMENTS';
  public depositsTitle: string = 'SETTINGS.PACKAGE.CALCULATE.DEPOSITS';

  public myPaymentData: MyItemSettingsProgress = { ...myProgressPaymentData };
  public myRefillData: MyItemSettingsProgress = { ...myProgressRefillData };
  public discountInRubles: boolean = false;
  public discountInForeignCurrency: boolean = false;
  public nextWriteOff: string = '';
  public differenceInDays: number = 0;

  @Input() set data(packageRest: PackageRest) {
    // progressBar  set myPaymentData
    this.myPaymentData.current =
      packageRest.restOfPayments.currentNumberOfPayments;
    this.myPaymentData.total = packageRest.restOfPayments.totalNumberOfPayments;
    this.myPaymentData.description =  this.translateService.get(
      'SETTINGS.PACKAGE.CALCULATE.MY_PAYMENT_DESCRIPTION',
      {
        totalNumberOfPayments: packageRest.restOfPayments.totalNumberOfPayments,
        payment: packageRest.restOfPayments.costNextPayment,
      }
    );
    this.myPaymentData.color  = packageRest.restOfPayments.colorIndicator;

    // progressBar component set myRefillData
    this.myRefillData.current =
      packageRest.depositAndWithdrawals.currentDepositAndWithdrawals;
    this.myRefillData.total =
      packageRest.depositAndWithdrawals.totalDepositAndWithdrawals;

    this.myRefillData.description = this.translateService.get(
      'SETTINGS.PACKAGE.CALCULATE.DEPOSITS_DESCRIPTION',
      {
        deposit:
          packageRest.depositAndWithdrawals.totalDepositAndWithdrawals / 1e6,
        depositingOverLimit:
        packageRest.depositAndWithdrawals.depositingOverLimit,
        withdrawalOverLimit:
        packageRest.depositAndWithdrawals.withdrawalOverLimit,
      }
    );
    this.myRefillData.color  = packageRest.depositAndWithdrawals.colorIndicator;
    this.myRefillData.tenge = '';

    // Discounts and transfers
    this.discountInRubles = packageRest?.discountOnTransferInRubles?.hasDiscount;
    this.discountInForeignCurrency = packageRest?.discountOnTransferInCurrency?.hasDiscount;

    // Set PriceData
    this.nextWriteOff = packageRest.nextWriteOff;
  }

  constructor(private translateService: TranslateService) { }

  ngOnInit(): void {
    const today = dayjs().format('YYYY-MM-DD');
    const nextWriteOff = dayjs(this.nextWriteOff);
    this.differenceInDays = dayjs(today).diff(nextWriteOff, 'd');
   }

  onCollapse() {
    this.isCollapsed = !this.isCollapsed;
  }
}
