import { Injectable } from '@angular/core';
import {
  FormGroupTypeSafe,
  FormBuilderTypeSafe,
  ibanPrefix,
} from '@utils/index';
import { Validators, AbstractControl, FormArray } from '@angular/forms';
import { MiscValidators } from '@shared/validators';
import {
  OrderPaymentFormModel,
  TaxPaymentFormModel,
  PensionPaymentFormModel,
  SocialPaymentFormModel,
  MedicalPaymentFormModel,
  SalaryPaymentFormModel,
  CurrencyPaymentFormModel,
} from './form-model';
import { PaymentsService } from './payments.service';
import { BankFromAccountPipe } from '@shared/pipes';
import { DictionariesService } from '@shared/services/dictionaries.service';
import { PAYMENT_TYPE, SPECIAL_ACCOUNTS } from '@shared/models';
import { CURRENCY } from '@shared/currency/currency.model';
import { PaymentAsyncValidators } from '@shared/services/payment-validators';

@Injectable({
  providedIn: 'root',
})
export class PaymentsFormFactoryService {
  constructor(
    private fb: FormBuilderTypeSafe,
    private paymentsService: PaymentsService,
    public bankFromAccount: BankFromAccountPipe,
    private dictionaries: DictionariesService
  ) {}

  createPaymentInTengeForm() {
    const form: FormGroupTypeSafe<OrderPaymentFormModel> =
      this.fb.group<OrderPaymentFormModel>(
        {
          contract: [null],
          contractId: [null],
          fxContractRecipient: [null],
          fxContractRecipientId: [null],
          withinContract: [false],

          type: undefined,
          iinOrBin: ['', [Validators.required, Validators.minLength(12)]],
          name: ['', [Validators.required, Validators.maxLength(250)]],
          bankAccount: [undefined, Validators.required],
          kbeCode: ['', [Validators.required, MiscValidators.kbe]],
          number: ['', Validators.required],
          date: null,
          knp: [undefined, [Validators.required]],
          purpose: [''],
          senderAccount: [''],
          amount: [
            '',
            [
              Validators.required,
              Validators.min(this.paymentsService.rules.minAmount),
              Validators.max(this.paymentsService.rules.maxAmount),
              (control: AbstractControl) => {
                if (control?.value === 0) {
                  return { cantBeZero: true };
                }
                return null;
              },
            ],
          ],
          // currency: ['', Validators.required],
          vat: [false],
          urgent: [false],
          notify: [false],
          emailOnSuccess: [
            {
              value: '',
              disabled: true,
            },
            [Validators.required, Validators.email],
          ],
        }

        // {
        //   validators: (group: FormGroupTypeSafe<NativeCurPaymentFormModel>) =>
        // }
      );

    const { bankAccount, senderAccount } = form.controls;

    bankAccount.setValidators([
      Validators.required,
      (control: AbstractControl) =>
        this.isEqualValidator(control, senderAccount),
      (control: AbstractControl) => this.accountExistsValidator(control),
      (control: AbstractControl) => {
        if (`KZ${control.value}` !== SPECIAL_ACCOUNTS.TAX_ACCOUNT) {
          return null;
        }
        return { taxAccountError: true };
      },
    ]);

    bankAccount.setAsyncValidators(
      PaymentAsyncValidators.internalIbanRestricted(this.dictionaries)
    );

    senderAccount.setValidators([
      Validators.required,
      (control: AbstractControl) => {
        if (control.value) {
          bankAccount.updateValueAndValidity();
        }
        return null;
      },
    ]);
    return form;
  }

  createTaxForm() {
    const form: FormGroupTypeSafe<TaxPaymentFormModel> =
      this.fb.group<TaxPaymentFormModel>({
        type: PAYMENT_TYPE.TAX,
        ugd: [undefined, Validators.required],
        kbk: ['', [Validators.required]],
        number: ['', Validators.required],
        date: undefined,
        knp: [
          {
            value: '',
          },
          [Validators.required],
        ],
        bankAccount: ['', Validators.required],
        senderAccount: ['', Validators.required],
        purpose: [''],
        amount: [
          '',
          [
            Validators.required,
            Validators.min(this.paymentsService.rules.minAmount),
            Validators.max(this.paymentsService.rules.maxAmount),
          ],
        ],
        urgent: [false],
        vin: [
          {
            value: '',
            disabled: true,
          },
        ],
        intervalEnabled: false,
        period: [
          {
            value: '',
            disabled: true,
          },
          [Validators.required],
        ],
        periodStart: [
          {
            value: '',
            disabled: true,
          },
        ],
        periodEnd: [
          {
            value: '',
            disabled: true,
          },
        ],
        protocolNumber: [
          {
            value: '',
            disabled: true,
          },
          [Validators.required],
        ],
      });

    const { periodEnd, periodStart } = form.controls;
    const isSmallerThan = MiscValidators.isSmallerThan(periodEnd);
    const isGreaterThan = MiscValidators.isGreaterThan(periodStart);

    periodStart.setValidators([
      Validators.required,
      isSmallerThan,
      MiscValidators.revalidateLinkControlIf(
        () =>
          !isSmallerThan(periodStart) && periodEnd.hasError('isGreaterThan'),
        periodEnd
      ),
    ]);
    periodEnd.setValidators([
      Validators.required,
      isGreaterThan,
      MiscValidators.revalidateLinkControlIf(
        () =>
          !isGreaterThan(periodEnd) && periodStart.hasError('isSmallerThan'),
        periodStart
      ),
    ]);

    const { bankAccount, senderAccount } = form.controls;

    bankAccount.setValidators([
      Validators.required,
      (control: AbstractControl) =>
        this.isEqualValidator(control, senderAccount),
      (control: AbstractControl) => this.accountExistsValidator(control),
      (control: AbstractControl) => {
        if (`KZ${control.value}`.substring(4, 7) === '070') {
          return null;
        }
        return { wrongTaxAccountError: true };
      },
    ]);

    bankAccount.setAsyncValidators(
      PaymentAsyncValidators.internalIbanRestricted(this.dictionaries)
    );

    return form;
  }

  createPensionForm() {
    const form: FormGroupTypeSafe<PensionPaymentFormModel> =
      this.fb.group<PensionPaymentFormModel>({
        type: PAYMENT_TYPE.PENSION,
        date: undefined,
        period: [
          {
            value: undefined,
            disabled: true,
          },
          [Validators.required],
        ],
        number: ['', Validators.required],
        knp: ['010', [Validators.required]],
        purpose: '',
        employees: [
          undefined,
          [
            Validators.required,
            Validators.minLength(1),
            this.paymentsService.employeeGroupControlValidator(
              PAYMENT_TYPE.PENSION
            ),
          ],
        ],
        senderAccount: ['', Validators.required],
        amount: [0, [Validators.required, Validators.min(0.01)]],
        urgent: [false],
        commonPeriodEnabled: false,
      });
    return form;
  }

  createSocialForm() {
    const form: FormGroupTypeSafe<SocialPaymentFormModel> =
      this.fb.group<SocialPaymentFormModel>({
        type: PAYMENT_TYPE.SOCIAL,
        date: undefined,
        period: [
          {
            value: undefined,
            disabled: true,
          },
          [Validators.required],
        ],
        number: ['', Validators.required],
        knp: ['012', [Validators.required]],
        employees: [
          undefined,
          [
            Validators.required,
            Validators.minLength(1),
            this.paymentsService.employeeGroupControlValidator(
              PAYMENT_TYPE.SOCIAL
            ),
          ],
        ],
        senderAccount: ['', Validators.required],
        amount: [0, [Validators.required, Validators.min(0.01)]],
        urgent: [false],
        purpose: '',
        commonPeriodEnabled: false,
      });
    return form;
  }

  createMedicalForm() {
    const form: FormGroupTypeSafe<MedicalPaymentFormModel> =
      this.fb.group<MedicalPaymentFormModel>({
        type: PAYMENT_TYPE.MEDICAL,
        date: undefined,
        period: [
          {
            value: undefined,
            disabled: true,
          },
          [Validators.required],
        ],
        number: ['', Validators.required],
        knp: ['121', [Validators.required]],
        employees: [
          undefined,
          [
            Validators.required,
            Validators.minLength(1),
            this.paymentsService.employeeGroupControlValidator(
              PAYMENT_TYPE.MEDICAL
            ),
          ],
        ],
        senderAccount: ['', Validators.required],
        amount: [0, [Validators.required, Validators.min(0.01)]],
        urgent: [false],
        purpose: '',
        commonPeriodEnabled: false,
      });
    return form;
  }

  createSalaryForm() {
    const form: FormGroupTypeSafe<SalaryPaymentFormModel> =
      this.fb.group<SalaryPaymentFormModel>({
        type: PAYMENT_TYPE.SALARY,
        bankReceiver: [''],
        bankAccount: [
          {
            value: '',
            disabled: true,
          },
        ],
        date: undefined,
        // period: ['', [Validators.required]],
        number: ['', Validators.required],
        knp: [undefined, [Validators.required]],
        employees: [
          undefined,
          [
            Validators.required,
            Validators.minLength(1),
            this.paymentsService.employeeGroupControlValidator(
              PAYMENT_TYPE.SALARY
            ),
          ],
        ],
        senderAccount: ['', Validators.required],
        amount: [0, [Validators.required, Validators.min(0.01)]],
        urgent: [false],
        purpose: [''],
        isResidents: true,
        // commonPeriodEnabled: false,
      });

    const { bankReceiver, bankAccount } = form.controls;
    bankReceiver.setValidators([
      Validators.required,
      (control: AbstractControl) => {
        if (control.value) {
          bankAccount.updateValueAndValidity();
        }
        return null;
      },
    ]);

    bankReceiver.setAsyncValidators(
      PaymentAsyncValidators.bankRestrictedById(this.dictionaries)
    );

    bankAccount.setValidators([
      Validators.required,
      (control: AbstractControl) => {
        const normalized = ibanPrefix(control.value, CURRENCY.KZT);
        if (normalized.length < 7 || !bankReceiver.value) {
          return null;
        }
        const bank = this.dictionaries.bankDictionary.getItemsByIds(
          bankReceiver.value as string
        );
        const bankAccountBic =
          this.paymentsService.bankFromAccount.transform(normalized);
        if (!bank || bank.bic === bankAccountBic) {
          return null;
        }
        return { difReceiverAndBankBiq: true };
      },
      // (control) =>
      //   this.paymentsService.bankExist(
      //     ibanPrefix(control.value || '', CURRENCY.KZT)
      //   ),
    ]);

    bankAccount.setAsyncValidators(
      PaymentAsyncValidators.internalIbanRestricted(this.dictionaries)
    );

    return form;
  }

  createCurrencyForm() {
    const form: FormGroupTypeSafe<CurrencyPaymentFormModel> =
      this.fb.group<CurrencyPaymentFormModel>({
        type: PAYMENT_TYPE.FOREIGN,

        senderAccount: ['', [Validators.required]],

        commissionAccount: ['', Validators.required],
        comission: [null],
        comissionCurrency: [null],
        payComission: [false],

        countryCode: [undefined, Validators.required],
        country: [null],
        cityName: [undefined],

        contract: [null],
        contractId: [null],
        fxContractRecipient: [null],
        fxContractRecipientId: [null],
        withinContract: [false],

        name: ['', [Validators.required, Validators.maxLength(103)]],

        bankAccount: [''],

        kbeCode: ['', [Validators.required, MiscValidators.kbe]],

        number: ['', Validators.required],

        date: [null],

        knp: [undefined, [Validators.required]],
        knpObject: [null],

        purpose: ['', Validators.required],

        amount: [
          '',
          [
            Validators.required,
            Validators.min(this.paymentsService.rules.minAmount),
            Validators.max(this.paymentsService.rules.maxAmount),
            (control: AbstractControl) => {
              if (control?.value === 0) {
                return { cantBeZero: true };
              }
              return null;
            },
          ],
        ],

        bankLabel: [
          '',
          Validators.required,
          PaymentAsyncValidators.bankRestricted(this.dictionaries),
        ],
        bank: [null],

        hasIntermediaryBank: [false],
        intermediaryBankLabel: [''],
        intermediaryBank: [null],

        notify: [false],

        emailOnSuccess: [
          {
            value: '',
            disabled: true,
          },
          [Validators.required, Validators.email],
        ],
        kpp: [''],
        kvo: [undefined],

        codeWord: [undefined],

        kio: [undefined, Validators.required],

        isRussianBudget: [null],

        isRussianTaxPayer: [null],
        payerInn: [null],
        payerKpp: [''],

        kbk: [null],
        oktmo: [''],

        payerStatus: ['S08'],

        hasRussianBudgetExtras: [null],
        paymentBasis: [''],
        budgetDocNumber: [''],
        budgetPeriod: [''],
        uip: [null],

        isRubNonRussia: [false],
        rubNonRussiaCorrespondentBankLabel: [''],
        rubNonRussiaCorrespondentBank: [null],
        rubNonRussiaBankLabel: [''],
        rubNonRussiaBank: [null],
        rubNonRussiaCorrespondentIban: [''],

        audIndex: [''],
        audState: [''],
        audStreet: [''],
        audBuildingNumber: [''],

        audAddress: [''],

        canAddress: [''],
      });

    return form;
  }

  // Validators
  private accountExistsValidator(control: AbstractControl) {
    return this.paymentsService.bankExist(
      ibanPrefix(control.value || '', CURRENCY.KZT)
    );
  }

  private isEqualValidator(
    control: AbstractControl,
    senderAccount: AbstractControl
  ) {
    const recipientIban = ibanPrefix(control.value, CURRENCY.KZT);
    const senderAcc = senderAccount.value;
    if (
      !control.value ||
      recipientIban.length < 18 ||
      !senderAcc ||
      recipientIban !== senderAcc.iban
    ) {
      return null;
    }
    return { isEqual: true };
  }
}
