import { Component, Input, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { ILoanSchedule, ILoanScheduleField, Loan, LoanScroll } from '@loans/models';

@Component({
  selector: 'j-loan-header',
  templateUrl: './loan-header.component.html',
  styleUrls: ['./loan-header.component.scss'],
})
export class LoanHeaderComponent implements OnDestroy {
  loanId: string;
  @Input() paymentsCount = 0;
  @Input() unpaid = 0;
  @Input() loan: Loan;
  @Input() schedule: ILoanSchedule;
  @Input() currentPayment: ILoanScheduleField;
  @Input() scrollState: LoanScroll;
  errorText: string;

  readonly subscription = new Subscription();

  constructor() {}

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  get getCurrentPaymentObj() {
    return {amount: this.currentPayment?.remainDebt, currency: this.loan.amount.currency};
  }
}
