import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'j-settings-package-progressbar',
  templateUrl: './settings-package-progressbar.component.html',
  styleUrls: ['./settings-package-progressbar.component.scss']
})
export class SettingsPackageProgressbarComponent implements OnInit {
  @Input() item: any;
  @Input() withoutExtra = true;
  style = {
    width: '0%'
  };
  @Input() key;
  constructor() { }

  ngOnInit(): void {
    this.calculateRemaining(this.item);
  }
  private calculateRemaining(item): void {
    const value: number = Math.round((item?.current / item?.total) * 100);
    this.style.width = `${value || 1}%`;
  }
}
