import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { PaymentsService } from '@payments/services/payments.service';
import { KNP } from '@shared/models/dictionary';
import { Observable, combineLatest } from 'rxjs';
import {
  map,
  debounceTime,
  distinctUntilChanged,
  startWith,
  tap,
} from 'rxjs/operators';
import { FormControl } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { DictionariesService } from '@shared/services/dictionaries.service';
import { PAYMENT_TYPE } from '@shared/models';

@Component({
  selector: 'j-knp-dictionary',
  templateUrl: './knp-dictionary.component.html',
  styleUrls: ['./knp-dictionary.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class KnpDictionaryComponent implements OnInit {
  @Input() paymentType: PAYMENT_TYPE;

  list$: Observable<KNP[]>;
  search: FormControl;

  constructor(
    private paymentsS: PaymentsService,
    protected dictionaries: DictionariesService,
    public modal: NgbActiveModal
  ) {}

  ngOnInit(): void {
    this.search = new FormControl('');

    this.list$ = combineLatest([
      this.dictionaries.KNPDictionary.items$,
      this.search.valueChanges.pipe(
        debounceTime(100),
        distinctUntilChanged(),
        startWith(this.search.value)
      ),
    ]).pipe(
      map(([list, search]) => {
        if (this.paymentType) {
          const filtered = this.dictionaries.selectByPaymentType(list, {
            type: this.paymentType,
          });

          return filtered
            .filter((item) => this.paymentsS.genericSearchTerm(search, item))
            .sort((a: KNP, b: KNP) => {
              return a.code > b.code ? 1 : -1;
            });
        }

        return list
          .filter((item) => this.paymentsS.genericSearchTerm(search, item))
          .sort((a: KNP, b: KNP) => {
            return a.code > b.code ? 1 : -1;
          });
      })
    );
  }

  closeModal(item: KNP) {
    this.modal.close(item);
  }
}
