import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormControl } from '@angular/forms';

import {
  map,
  debounceTime,
  distinctUntilChanged,
  startWith,
} from 'rxjs/operators';
import { Observable, combineLatest, BehaviorSubject } from 'rxjs';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

import { PaymentsService } from '@payments/services/payments.service';

import {
  Recipient,
  RecipientAccount,
} from '@company/recipient/recipient.model';
import { RecipientsService } from '@company/recipients.service';
@Component({
  selector: 'j-recipients-dictionary',
  templateUrl: './recipients-dictionary.component.html',
  styleUrls: ['./recipients-dictionary.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class RecipientsDictionaryComponent implements OnInit {
  collectionSize$ = new BehaviorSubject(0);
  list$: Observable<Recipient[]>;
  search: FormControl;
  currentPage$ = new BehaviorSubject(0);

  pageSize = 10;

  constructor(
    private paymentsS: PaymentsService,
    private recipients: RecipientsService,
    public modal: NgbActiveModal
  ) {}

  ngOnInit(): void {
    this.recipients.getRecipients();

    this.search = new FormControl('');

    this.list$ = combineLatest([
      this.recipients.store.items$.pipe(
        map((items) => {
          return items.filter((item) => !!item?.accounts);
        })
      ),
      this.search.valueChanges.pipe(
        debounceTime(100),
        distinctUntilChanged(),
        startWith(this.search.value)
      ),
      this.currentPage$,
    ]).pipe(
      map(([list, search, page]) => {
        const filtered = this.recipients.filterRecipients(list, search);

        this.collectionSize$.next(
          filtered.length > this.pageSize
            ? filtered.length - this.pageSize
            : filtered.length
        );

        return filtered.slice(
          page > 1 ? page * this.pageSize : 0,
          (page + 1) * this.pageSize
        );
      })
    );
  }

  get page() {
    return this.currentPage$.getValue();
  }

  set page(next: number) {
    this.currentPage$.next(next);
  }

  onClose() {
    this.modal.dismiss();
  }

  closeModal(recipient: Recipient, account: RecipientAccount) {
    this.modal.close({
      recipient,
      account,
    });
  }
}
