import { Component, OnInit, Input } from '@angular/core';
import {
  Transaction,
  TRANSACTION_TYPE,
} from '@statement/models/transaction.model';
import { PAYMENT_TYPE } from '@shared/models';

@Component({
  selector: 'j-transaction-type',
  templateUrl: './transaction-type.component.html',
  styleUrls: ['./transaction-type.component.scss'],
})
export class TransactionTypeComponent implements OnInit {
  @Input()
  transaction: Transaction;

  @Input()
  isInversed: boolean;

  isStatement: boolean = false;

  imgPath: string;

  constructor() {}

  ngOnInit(): void {
    this.generateImgPath();
  }

  generateImgPath() {
    if (this.transaction.isPayment) {
      this.imgPath = this.getPaymentImg(this.transaction.paymentType);
    } else {
      this.isStatement = true;
      this.imgPath = this.getStatementImg(this.transaction.type);
    }
  }

  private getPaymentImg(paymentType: PAYMENT_TYPE) {
    const prefix = `/assets/img/history/payment`;
    switch (paymentType) {
      case PAYMENT_TYPE.TAX:
        return `${prefix}/taxes.svg`;
      case PAYMENT_TYPE.PENSION:
        return `${prefix}/pension.svg`;
      case PAYMENT_TYPE.SOCIAL:
        return `${prefix}/social.svg`;
      case PAYMENT_TYPE.MEDICAL:
        return `${prefix}/medical.svg`;
      case PAYMENT_TYPE.SALARY:
        return `${prefix}/salary.svg`;
      case PAYMENT_TYPE.FOREIGN:
        return `${prefix}/foreign.svg`;
      case PAYMENT_TYPE.CONVERSION:
        return `${prefix}/conversion.svg`;
      case PAYMENT_TYPE.INTERNAL:
      case PAYMENT_TYPE.EXTERNAL:
      default:
        return `${prefix}/regular.svg`;
    }
  }

  private getStatementImg(statementType: TRANSACTION_TYPE) {
    const prefix = `/assets/img/history/statement`;
    switch (statementType) {
      case TRANSACTION_TYPE.CREDIT:
        return `${prefix}/credit.svg`;
      case TRANSACTION_TYPE.DEBIT:
        return `${prefix}/debit.svg`;
      case TRANSACTION_TYPE.RETURN:
      default:
        return `${prefix}/return.svg`;
    }
  }
}
