import { Component, Input, OnInit, OnChanges, SimpleChanges, Output, EventEmitter } from '@angular/core';
import { ItemFormsSettingsSale, PackageTerms } from '@settings/models';

@Component({
  selector: 'j-settings-package-form',
  templateUrl: './settings-package-form.component.html',
  styleUrls: ['./settings-package-form.component.scss']
})
export class SettingsPackageFormComponent implements OnInit {
  @Input() items: ItemFormsSettingsSale[] | null;
  @Output() getValue = new EventEmitter();
  constructor() { }
  ngOnInit() {
    // this.items.map(v => v.disabled ? v.checked : v.checked  = false );
  }
  @Input() set select(packageTerms: PackageTerms) {
    this.items[0].checked = packageTerms.discountInRubles;
    this.items[1].checked = packageTerms.discountInForeignCurrency;
  }

  setValue(e: ItemFormsSettingsSale): void {
    e.checked = !e.checked;
    this.getValue.emit({[e.key] : e.checked});
  }
}
