import { Component, HostListener, Inject, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ILoanSchedule, ILoanScheduleField, IOperationType, Loan, LoanScroll } from '@loans/models';
import { Subscription } from 'rxjs';

import { LoanService } from '@loans/services';
import { CompanyService } from '@company/company.service';
import { Company } from '@company/company.model';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'j-loans',
  templateUrl: './loans.component.html',
  styleUrls: ['./loans.component.scss']
})
export class LoansComponent implements OnInit, OnDestroy {
  loanId: string;
  selectedTab: string;
  loan: Loan;
  schedule: ILoanSchedule;
  currentPayment: ILoanScheduleField;
  unpaid = 0;
  paymentsCount = 0;
  readonly subscription = new Subscription();
  errorText: string;
  isLoading = true;
  wrapperClasses = '';
  isAnimating = false;
  detailsClasses = '';
  operationTypes: IOperationType[] = [];
  operationTypeList: IOperationType[] = [];
  operationTypePage = 0;
  selected: IOperationType;
  company: Company;
  scrollState: LoanScroll = {coord_y: 0, selectedTab: 'statement'};
  payment;
  @HostListener('window:scroll', ['$event']) onScrollEvent($event) {
    const verticalOffset = window.pageYOffset
      || document.documentElement.scrollTop
      || document.body.scrollTop || 0;
    this.scrollState = {coord_y: verticalOffset, selectedTab: this.selectedTab};
  }
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private loanService: LoanService,
    private companyService: CompanyService,
    @Inject(DOCUMENT) private document: Document,
  ) { }

  ngOnInit(): void {
    const paramMapSub = this.route.queryParamMap.subscribe(params => {
      if (params.get('view')) {
        this.selectedTab = params.get('view');
      } else {
        const queryParams = {view: 'statement'};
        this.router.navigate([], {queryParams, queryParamsHandling: 'merge'});
      }
    });
    const paramSub = this.route.params.subscribe(params => {
      if (params.id) {
        this.loanId = params.id;
        this.loadLoan();
      }
    });
    this.subscription.add(paramMapSub);
    this.subscription.add(paramSub);
  }

  private async loadLoan() {
    this.isLoading = true;

    try {
      this.loan = await this.loanService.getLoanById(this.loanId);
      this.loanService.getSchedule(this.loan.colvirReferenceId).then(data => {
        this.schedule = data;
        this.parseSchedule();
        for (const [key] of Object.entries(this.schedule.loanSchedule)) {
          this.operationTypes.push({label: key, code: key});
        }
        this.onSchedulePageChanged(0);
        this.setSelected(this.operationTypes[0].code);
      });
      this.companyService.getCompany(this.loan.companyId).then(companyData => {
        this.company = companyData;
      });
    } catch (error) {
      this.errorText = error.description;
    } finally {
      this.isLoading = false;
    }
  }

  private parseSchedule() {
    if (this.schedule) {
      for (const [key, value] of Object.entries(this.schedule.loanSchedule)) {
        for (const payment of value) {
          if (payment.currentPayment) {
            this.currentPayment = payment;
            this.setSelected(key);
          }
          if (this.currentPayment) {
            this.unpaid++;
          }
          this.paymentsCount++;
        }
      }
    }
  }

  isSelected(type: IOperationType) {
    return type === this.selected;
  }

  onSelect(code) {
    this.setSelected(code);
  }

  onSchedulePageChanged(page) {
    this.operationTypePage = page;
    this.operationTypeList = [];
    for (let i = page * 7; i <= (page + 1) * 7; i++) {
      if (this.operationTypes[i]) {
        this.operationTypeList.push(this.operationTypes[i]);
      }
    }
  }

  private setSelected(code) {
    this.selected = this.operationTypeList.find((item) => code === item.code);
  }

  setShowAll(value = true) {
    this.selected = null;
  }

  goBack() {
    this.router.navigate(['/']);
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
}
