import { Component, Input } from '@angular/core';
import { IRefundDetails } from '@shared/models';

@Component({
  selector: 'j-refund-employee',
  templateUrl: './refund-employee.component.html',
  styleUrls: ['./refund-employee.component.scss']
})
export class RefundEmployeeComponent {
  @Input() refundDetailsItem: IRefundDetails;
  constructor() { }

  get getEmployeeName() {
    return `${this.refundDetailsItem.lastName} ${this.refundDetailsItem.firstName} ${this.refundDetailsItem.middleName}`;
  }

}
