import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';

import { combineLatest } from 'rxjs';
import {
  map,
  debounceTime,
  distinctUntilChanged,
  startWith,
} from 'rxjs/operators';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

import { PaymentsService } from '@payments/services/payments.service';
import { DictionariesService } from '@shared/services/dictionaries.service';
import { Bank } from '@shared/models';

@Component({
  selector: 'j-banks-receivers',
  templateUrl: './banks-receivers.component.html',
  styleUrls: ['./banks-receivers.component.scss'],
})
export class BanksReceiversComponent implements OnInit {
  list$;
  store;
  search: FormControl;

  constructor(
    public modal: NgbActiveModal,
    private paymentsS: PaymentsService,
    private dict: DictionariesService
  ) {}

  ngOnInit(): void {
    this.search = new FormControl('');

    this.list$ = combineLatest(
      this.dict.bankDictionary.items$,
      this.search.valueChanges.pipe(
        debounceTime(100),
        distinctUntilChanged(),
        startWith(this.search.value)
      )
    ).pipe(
      map(([list, search]) => {
        return list.filter((item) =>
          this.paymentsS.bankReceiverSearchFn(search, item)
        );
      })
    );
  }

  closeModal(item: Bank) {
    this.modal.close(item);
  }
}
