import { Component, Input, OnInit } from '@angular/core';

import {
  AccountsSubscriptionService,
  ACCOUNT_SUBSCRIPTION_TYPE,
  IAccountSubscription,
} from '@accounts/services/accounts-subscription.service';
import { RegularAccount } from '@accounts/index';

import { IAccountSubscriptionForm } from '../account-subscription/account-subscription.component';
import { NotifierService } from 'angular-notifier';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'j-account-export',
  templateUrl: './account-export.component.html',
  styleUrls: ['./account-export.component.scss'],
})
export class AccountExportComponent implements OnInit {
  @Input() account: RegularAccount;

  loaded = false;

  isDailyLoading = true;
  isMonthlyLoading = true;

  dailySubscription: IAccountSubscription;
  monthlySubscription: IAccountSubscription;

  constructor(
    private accountSubscriptionService: AccountsSubscriptionService,
    private notifierService: NotifierService,
    private translateService: TranslateService
  ) {}

  ngOnInit() {
    this.init();
  }

  async init() {
    const subscriptions = await this.accountSubscriptionService.getSubscriptions(
      this.account.id
    );

    this.dailySubscription =
      subscriptions.find(
        (subscription) =>
          subscription.subscriptionType === ACCOUNT_SUBSCRIPTION_TYPE.DAILY
      ) || null;

    this.monthlySubscription =
      subscriptions.find(
        (subscription) =>
          subscription.subscriptionType === ACCOUNT_SUBSCRIPTION_TYPE.MONTHLY
      ) || null;

    this.isDailyLoading = false;
    this.isMonthlyLoading = false;

    this.loaded = true;
  }

  async onDailySubmit(subscriptionForm: IAccountSubscriptionForm) {
    this.isDailyLoading = true;
    try {
      this.dailySubscription = await this.submitSubscription(
        subscriptionForm,
        ACCOUNT_SUBSCRIPTION_TYPE.DAILY
      );
      this.notifierService.notify(
        'default',
        this.translateService.instant('ACCOUNTS.EXPORT.DAILY.SUBSCRIBED')
      );
    } catch (error) {
      this.notifierService.notify('error', error.description || error.code);
    } finally {
      this.isDailyLoading = false;
    }
  }

  async onMonthlySubmit(subscriptionForm: IAccountSubscriptionForm) {
    this.isMonthlyLoading = true;
    try {
      this.monthlySubscription = await this.submitSubscription(
        subscriptionForm,
        ACCOUNT_SUBSCRIPTION_TYPE.MONTHLY
      );
      this.notifierService.notify(
        'default',
        this.translateService.instant('ACCOUNTS.EXPORT.MONTHLY.SUBSCRIBED')
      );
    } catch (error) {
      this.notifierService.notify('error', error.description || error.code);
    } finally {
      this.isMonthlyLoading = false;
    }
  }

  async onDeleteDaily(subscriptionId: string) {
    if (this.isDailyLoading) {
      return;
    }

    this.isDailyLoading = true;
    try {
      await this.deleteSubscription(subscriptionId);
      this.dailySubscription = null;
      this.notifierService.notify(
        'default',
        this.translateService.instant('ACCOUNTS.EXPORT.DAILY.UNSUBSCRIBED')
      );
    } catch (error) {
      this.notifierService.notify('error', error.description || error.code);
    } finally {
      this.isDailyLoading = false;
    }
  }

  async onDeleteMonthly(subscriptionId: string) {
    if (this.isMonthlyLoading) {
      return;
    }

    this.isMonthlyLoading = true;
    try {
      await this.deleteSubscription(subscriptionId);
      this.monthlySubscription = null;
      this.notifierService.notify(
        'default',
        this.translateService.instant('ACCOUNTS.EXPORT.MONTHLY.UNSUBSCRIBED')
      );
    } catch (error) {
      this.notifierService.notify('error', error.description || error.code);
    } finally {
      this.isMonthlyLoading = false;
    }
  }

  private deleteSubscription(subscriptionId: string) {
    if (!subscriptionId) {
      return;
    }

    return this.accountSubscriptionService.deleteSubscription(
      this.account.id,
      subscriptionId
    );
  }

  private submitSubscription(
    subscriptionForm: IAccountSubscriptionForm,
    subscriptionType: ACCOUNT_SUBSCRIPTION_TYPE
  ) {
    const subscription = {
      accountId: this.account.id,
      emails: subscriptionForm.emails,
      exportType: subscriptionForm.exportType,
      subscriptionType: subscriptionType,
    };

    if (subscriptionForm.id) {
      return this.accountSubscriptionService.saveSubscription(
        subscription,
        subscriptionForm.id
      );
    } else {
      return this.accountSubscriptionService.createSubscription(subscription);
    }
  }
}
