import { Component, Input, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { AccountsService, GRANTS, IUserGrant } from '@accounts/index';
import { NotifierService } from 'angular-notifier';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'j-account-grants',
  templateUrl: './account-grants.component.html',
  styleUrls: ['./account-grants.component.scss'],
})
export class AccountGrantsComponent implements OnInit {
  @Input() companyId;
  @Input() accountId;
  users: IUserGrant[];
  error = null;
  grants = GRANTS;

  constructor(
    public modal: NgbActiveModal,
    public accountsService: AccountsService,
    private notifierService: NotifierService,
    private translateService: TranslateService
  ) {}

  ngOnInit(): void {
    this.getAccountGrants();
  }

  async getAccountGrants() {
    try {
      this.users = await this.accountsService.getAccountsGrants(
        this.companyId,
        this.accountId
      );
    } catch (error) {
      this.error = 'error';
    }
  }

  async onChange(event, user) {
    try {
      user.accountGrantType = this.getGrantType(event.target.value);
      await this.accountsService.setAccountGrant(
        this.companyId,
        this.accountId,
        [this.mapParams(user, user.accountGrantType)]
      );
      this.showNotificationForUser(user);
      this.accountsService.retrieveTotalLimitations(
        this.companyId,
        this.accountId
      );
    } catch (error) {
      this.notifierService.notify(
        'error',
        error.description ||
          error.code ||
          this.translateService.instant('SHARED.ACTION_ERROR')
      );
    }
  }

  async onChangeAll(event) {
    try {
      const accountGrantType = this.getGrantType(event.target.value);
      const users = this.users.map((user) =>
        this.mapParams(user, accountGrantType)
      );
      await this.accountsService.setAccountGrant(
        this.companyId,
        this.accountId,
        users
      );
      this.showNotification(accountGrantType);
      this.accountsService.retrieveTotalLimitations(
        this.companyId,
        this.accountId
      );
      this.users.forEach((el) => (el.accountGrantType = accountGrantType));
    } catch (error) {
      this.notifierService.notify(
        'error',
        error.description ||
          error.code ||
          this.translateService.instant('SHARED.ACTION_ERROR')
      );
    }
  }

  showNotificationForUser(user) {
    const message =
      user.accountGrantType === GRANTS.AVAILABLE
        ? this.translateService.instant(`ACCOUNTS.SETTINGS.USER_GRANTED`, {
            user: `${user.lastname || ''} ${user.firstname || ''} ${
              user.middlename || ''
            }`,
          })
        : this.translateService.instant('ACCOUNTS.SETTINGS.USER_LIMITED', {
            user: `${user.lastname || ''} ${user.firstname || ''} ${
              user.middlename || ''
            }`,
          });
    this.notifierService.notify('default', message);
  }

  showNotification(accountGrantType) {
    const message =
      accountGrantType === GRANTS.AVAILABLE
        ? this.translateService.instant(`ACCOUNTS.SETTINGS.GRANTED_ALL`)
        : this.translateService.instant('ACCOUNTS.SETTINGS.LIMITED_ALL');
    this.notifierService.notify('default', message);
  }

  mapParams(user, accountGrantType) {
    return {
      id: user.userId,
      userAuthorityId: user.userAuthorityId,
      accountGrantType: accountGrantType,
    };
  }

  getGrantType(value) {
    return value === 'true' ? GRANTS.UNAVAILABLE : GRANTS.AVAILABLE;
  }

  get isAllSelected() {
    return this.users?.every(
      (user) => user.accountGrantType === this.grants.UNAVAILABLE
    );
  }
}
