import {
  Component,
  Inject,
  Input,
  LOCALE_ID,
  OnDestroy,
  OnInit,
} from '@angular/core';
import { formatDate } from '@angular/common';

import { Subscription } from 'rxjs';

import { AccountsService } from '@accounts/services/accounts.service';

@Component({
  selector: 'j-account-migrated',
  templateUrl: './account-migrated.component.html',
  styleUrls: ['./account-migrated.component.scss'],
})
export class AccountMigratedComponent implements OnDestroy {
  @Input() isLarge: boolean = false;

  subscription = new Subscription();

  descriptionParams;
  titleParams;

  constructor(
    private accountsService: AccountsService,
    @Inject(LOCALE_ID) private locale: string
  ) {
    this.subscription.add(
      this.accountsService.getMergeDate().subscribe((mergeDate) => {
        this.descriptionParams = this.mapTranslateParams(mergeDate, 'd.MM.yy');
        this.titleParams = this.mapTranslateParams(mergeDate, 'd MMMM');
      })
    );
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  private mapTranslateParams(mergeDate: string, format: string) {
    return {
      date: formatDate(mergeDate, 'd.MM.yy', this.locale),
    };
  }
}
