import { Component, OnInit } from '@angular/core';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

import { Recipient } from '@company/recipient/recipient.model';
import { RecipientsService } from '@company/recipients.service';

import { ISelectedRecipient } from './foreign-recipient/foreign-recipient.component';

@Component({
  selector: 'j-foreign-recipients-dictionary',
  templateUrl: './foreign-recipients-dictionary.component.html',
  styleUrls: ['./foreign-recipients-dictionary.component.scss'],
})
export class ForeignRecipientsDictionaryComponent implements OnInit {
  recipients: Recipient[];
  filteredRecipients: Recipient[];
  searchTerm: string = '';

  constructor(
    public modal: NgbActiveModal,
    private recipientsService: RecipientsService
  ) {
    this.loadRecipients();
  }

  ngOnInit() {}

  async loadRecipients() {
    this.recipients = await this.recipientsService.getForeignRecipients();
    this.filteredRecipients = [...this.recipients];
  }

  async onQueryChange(query: string) {
    this.searchTerm = query;
    this.filteredRecipients = await this.recipientsService.getForeignRecipients(
      query
    );
  }

  onRecipientSelect(selectedRecipient: ISelectedRecipient) {
    this.modal.close(selectedRecipient);
  }

  onClose() {
    this.modal.dismiss();
  }
}
