import {
  Component,
  OnInit,
  Input,
  ChangeDetectionStrategy,
  Output,
  EventEmitter,
  ContentChild,
  TemplateRef,
} from '@angular/core';
import { ISettingsInfo } from '@settings/settings.service';

@Component({
  selector: 'j-settings-info',
  templateUrl: './settings-info.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styleUrls: ['./settings-info.component.scss'],
})
export class SettingsInfoComponent implements OnInit {
  @Input() info: ISettingsInfo;
  @Output() requisitesClicked = new EventEmitter();

  @ContentChild('footerTemplate', { static: false })
  footerTemplate: TemplateRef<any>;

  constructor() {}

  ngOnInit(): void {}

  openRequisites() {
    this.requisitesClicked.emit();
  }
}
