const dateMessages = {
  primaryAny: 'PAYMENTS.DATE_MESSAGES.PRIMARY_ANY',
  secondaryDateAuto: 'PAYMENTS.DATE_MESSAGES.SECONDARY_DATE_AUTO',
  secondaryDate: 'PAYMENTS.DATE_MESSAGES.SECONDARY_DATE',
};

const amountMessages = {
  primaryDateAutoBalanceLower: 'PAYMENTS.AMOUNT_MESSAGES.AUTO',
  primaryDateBalanceLower: 'PAYMENTS.AMOUNT_MESSAGES.DEFAULT',
};

@Directive()
export abstract class BasePaymentContainer<T extends BasePaymentData>
  implements OnDestroy, OnInit
{
  isRestricted: boolean = false;

  abstract accountStore: SimpleStore<RegularAccount>;

  abstract acceptableAccounts$?: Observable<RegularAccount[]>;
  PAYMENT_STATUS = PAYMENT_STATUS;
  SPINNER = 'BasePaymentContainer';

  knp$?: Observable<KNP[]>;
  kbk$?: Observable<KBK[]>;

  abstract masks: {
    [k in keyof T]?: any;
  };

  dateLimit?: {
    minDate: NgbDateStruct;
    maxDate: NgbDateStruct;
  };
  ugdStore: EntitiesStore<UGD>;
  knpStore: EntitiesStore<KNP>;
  kbkStore: EntitiesStore<KBK>;
  // form: FormGroupTypeSafe<T>;
  controlsStream: {
    [k in keyof T]: Observable<T[k]>;
  };

  restoredData: {
    status?: PAYMENT_STATUS;
    signatures?: string[];
    companyId?: string;
  } = {};

  errors: PaymentsService['errors'];

  rules: PaymentsService['rules'];

  amountMessage$ = new BehaviorSubject('');
  periodMessage$ = new BehaviorSubject('');
  // paymentType$ = new BehaviorSubject<PAYMENT_TYPE>(null);
  commission$ = new BehaviorSubject<number>(null);
  vat$ = new BehaviorSubject(0);
  total$ = new BehaviorSubject(0);
  purposeMessage$ = new BehaviorSubject('');
  dateMessage$ = new BehaviorSubject('');
  purposeLength$ = new BehaviorSubject(0);
  workingHours$ = new BehaviorSubject(null);
  workingDays$ = new BehaviorSubject<string[]>([]);
  processingPayment$ = new BehaviorSubject<{
    id?: string;
    number?: string | number;
  }>(null);

  /**
   * @todo check unused isImported
   */
  isImported = false;
  @ViewChild('formDirective', { static: false })
  formDirective: NgForm;

  // SenderAccount
  @ViewChild('senderAccountDropdown')
  senderAccountDropdown: AccountDropdownComponent;

  readonly subs = new Subscription();

  isPermissionsUpdating = false;
  isPermissionsUpdateAllowed = !environment.production;

  constructor(
    public form: FormGroupTypeSafe<T>,
    protected api: PaymentApiService,
    protected route: ActivatedRoute,
    protected router: Router,
    protected companyService: CompanyService,
    protected paymentsService: PaymentsService,
    protected maskService: MaskedService,
    protected modals: NgbModal,
    protected dictionaries: DictionariesService,
    protected pipeline: PaymentsPipelineService,
    protected translateService: TranslateService,
    protected authorityPermissionsService: AuthorityPermissionsService,
    protected featureFlagsService: FeatureFlagsService,
    protected bankFromAccount: BankFromAccountPipe
  ) {
    this.errors = this.paymentsService.errors;
    this.rules = this.paymentsService.rules;
    this.ugdStore = this.dictionaries.UGDictionary;
    this.knpStore = this.dictionaries.KNPDictionary;
    this.kbkStore = this.dictionaries.KBKDictionary;

    this.getWorkingHours();
  }

  get bankAccountErrorParams() {
    if (!this.form.controls.bankAccount?.errors?.bankRestricted) {
      return null;
    }

    const value = this.form.controls.bankAccount.value;

    const normalized = ibanPrefix(value, CURRENCY.KZT);

    if (normalized.length < 7 || !value) {
      return null;
    }

    return {
      bankName: this.bankFromAccount.transform(normalized, 'name'),
    };
  }

  get bankReceiverErrorParams() {
    if (!this.form.controls.bankReceiver?.errors?.bankRestricted) {
      return null;
    }

    const bank = this.dictionaries.bankDictionary.getItemsByIds(
      this.form.controls.bankReceiver.value
    );

    if (!bank) {
      return null;
    }

    return {
      bankName: (bank as any).name,
    };
  }

  async getPermission() {
    this.isRestricted = await this.authorityPermissionsService.isRestricted(
      this.paymentType
    );
  }

  async getWorkingHours() {
    const workingHours = await this.api.getWorkingHours();
    this.workingHours$.next(workingHours);
  }

  public ngOnInit() {
    this.getPermission();
    this.listenRouterParams();
    this.createControlsStream(this.form);
    this.combineUrgentStream();
    this.subs.add(
      this.processingPayment$
        .pipe(distinctUntilChanged(), delay(0))
        .subscribe((processing) => {
          if (processing) {
            // this.form.disable();
            this.paymentsService.spinner.show(this.SPINNER);
          } else {
            // this.form.disabled && this.form.enable();
            this.paymentsService.spinner.hide(this.SPINNER);
          }
        })
    );
  }

  public ngOnDestroy() {
    this.subs.unsubscribe();
    this.paymentsService.spinner.hide(this.SPINNER);
  }

  get paymentId(): string | undefined {
    return this.route.snapshot.params.paymentId;
  }

  // Contracts rules
  mapContractLabel(contract: IFxContract) {
    const savedContract = this.form.getRawValue().contract;
    if (contract?.contractNumber || contract?.contractor?.name) {
      return `${contract.contractNumber} - ${contract.contractor?.name}`;
    } else if (savedContract) {
      return `${savedContract.contractNumber} - ${savedContract.contractor?.name}`;
    }
    return '';
  }

  public contractComparator(query: string, contract: IFxContract) {
    if (!query) {
      return true;
    }

    const foundInName = contract.contractor.name
      .toLowerCase()
      .includes(query.toLowerCase());
    const foundInContractNumber = contract.contractNumber
      .toLowerCase()
      .includes(query.toLowerCase());

    return foundInName || foundInContractNumber;
  }

  public get dateHelper(): string {
    return this.form.controls.date.value ? '' : 'PAYMENTS.FORM.DATE_HELPER';
  }

  public get senderAccountLabel(): string {
    return this.form.controls.senderAccount
      ? 'PAYMENTS.FORM.SENDER_ACCOUNT.SELECTED'
      : 'PAYMENTS.FORM.SENDER_ACCOUNT.EMPTY';
  }

  public get submitLabel(): string {
    return (
      'SHARED.' +
      (this.paymentsService.isSignatureRequired ? 'TO_SIGN' : 'FOR_SIGN')
    );
  }

  public get paymentType(): PAYMENT_TYPE {
    return this.form.controls.type.value;
  }
  public set paymentType(t: PAYMENT_TYPE) {
    this.form.controls.type.patchValue(t);
  }

  public onSenderAccountSelect(account: RegularAccount) {
    this.form.controls.senderAccount.setValue(account);
  }

  hasError(component, control: AbstractControl): boolean {
    return (
      control.invalid &&
      ((control.dirty && !component?.isFocused) ||
        (this.formDirective?.submitted && control.touched))
    );
  }

  sendPayment(body?: any) {
    this.formDirective.form.markAsPristine();
    const paymentNumberStore = this.paymentsService.paymentNumber;
    const rawBody = body || {
      ...this.form.value,
      purpose: [this.form.value.purpose, this.purposeMessage$.getValue()]
        .filter((v) => !!v)
        .join('. '),
    };
    this.processingPayment$.next(rawBody);
    this.paymentsService.paymentNumber.entity = null;

    return this.pipeline
      .processPayment({
        paymentId: this.paymentId,
        // no commission in total JSN-1624
        total: this.total$.getValue() - this.commission$.getValue(),
        commission: this.commission$.getValue(),
        status: this.restoredData?.status,
        userPurpose: this.form.value.purpose,
        rawBody,
      })
      .then((r) => {
        if (r) {
          this.restoredData = {
            status: r.status.code,
            signatures: r.signatures,
            companyId: r.companyId,
          };
        } else {
          this.restoredData = null;
          if (!paymentNumberStore.entity && !paymentNumberStore.fetching) {
            paymentNumberStore.entity = rawBody.number;
          }
        }
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        // this.formDirective.resetForm();
        this.processingPayment$.next(null);
      });
  }

  public onSubmit(e: Event) {
    e && e.preventDefault();

    if (this.form.valid) {
      this.processPayment();
    } else {
      this.form.markAllAsTouched();
    }
  }

  public async updatePermissions() {
    this.isPermissionsUpdating = true;

    try {
      await this.authorityPermissionsService.updatePermissions();
      this.getPermission();
    } catch (error) {
    } finally {
      this.isPermissionsUpdating = false;
    }
  }

  public async processPayment() {
    const formValue = this.form.getRawValue();

    this.processingPayment$.next(formValue);

    try {
      const isWorkingTimeCheckPassed = await this.api.checkWorkingTime(
        formValue.date
      );

      let isAmountCheckPassed;

      if (
        this.featureFlagsService.isDisabled(FEATURE_FLAG.PAYMENT_PERMISSIONS)
      ) {
        isAmountCheckPassed = true;
      } else {
        const amountCheck = await this.api.checkAmountLimit(
          formValue.amount,
          this.paymentType
        );
        isAmountCheckPassed = amountCheck.isPassed;
      }

      if (isWorkingTimeCheckPassed && isAmountCheckPassed) {
        this.sendPayment();
      } else {
        this.processingPayment$.next(null);
      }
    } catch (error) {
      this.processingPayment$.next(null);
    }
  }

  public openKnpDictionary(select: JSelectComponent, event: Event) {
    select.close();
    select.blur();
    setTimeout(() => {
      const modalRef = this.modals.open(KnpDictionaryComponent, {
        backdropClass: 'backdrop_light',
        windowClass: 'modal_light modal_max-height',
        centered: true,
      });

      modalRef.componentInstance.paymentType = this.paymentType;
      modalRef.result
        .then((item: KNP) => {
          this.form.controls.knp.patchValue(item.code);
          // trigger cd to update dynamic bottom form field slot
          // this.form.controls.purpose.patchValue(
          //   this.form.controls.purpose.value
          // );
        })
        .catch(() => {
          /*  no selection or dismiss */
        });
    }, 0);
  }

  // onAmountPaste(event: ClipboardEvent, formControl) {
  //   event.preventDefault();

  //   const clipboardData = event.clipboardData || (window as any).clipboardData;

  //   const pastedText = clipboardData.getData('text');
  //   const toPasteRaw = this.maskService.getMaskedAmount(pastedText);

  //   formControl.setValue(toPasteRaw);
  // }

  public knpComparator(a: KNP, code: string) {
    return a.code === code;
  }

  protected combineUrgentStream() {
    const { date: date$, bankAccount, bankReceiver } = this.controlsStream;
    const { urgent } = this.form.controls;

    if (!urgent) {
      return;
    }

    // start time stream from current status, also start timer for switch
    const astanaTime = new Date();
    const offset = getTimeZoneOffset(new Date(), 'Asia/Almaty');
    astanaTime.setHours(astanaTime.getHours() + -offset / 60);
    const min = new Date(astanaTime);
    min.setHours(9, 0, 0);
    const max = new Date(astanaTime);
    max.setHours(16, 0, 0);
    let trigger$: Observable<boolean>;
    let currentStatus = true;

    if (astanaTime >= min && astanaTime <= max) {
      // turn off
      trigger$ = timer(+max - +astanaTime).pipe(mapTo(false));
    } else if (astanaTime < min) {
      // turn on later today
      trigger$ = timer(+min - +astanaTime).pipe(
        mapTo((currentStatus = !currentStatus))
      );
    } else {
      // turn on tomorrow
      trigger$ = timer(min.setDate(min.getDate() + 1) - +astanaTime).pipe(
        mapTo((currentStatus = !currentStatus))
      );
    }
    trigger$ = trigger$.pipe(take(1), startWith(currentStatus));

    let bankAccount$: Observable<string>;
    if (bankReceiver) {
      bankAccount$ = combineLatest([
        bankAccount,
        bankReceiver,
        this.dictionaries.bankDictionary.entities$,
      ]).pipe(
        map(([bankAccountV, bankReceiverV, bankDict]) => {
          let selectedBank: Bank;
          if (
            bankReceiverV &&
            (selectedBank = bankDict[bankReceiverV]) &&
            selectedBank.salaryTransitIban
          ) {
            return selectedBank.salaryTransitIban;
          } else if (
            selectedBank &&
            !selectedBank.salaryTransitIban &&
            bankAccountV
          ) {
            return ibanPrefix(bankAccountV, CURRENCY.KZT);
          }
          return '';
        })
      );
    } else if (bankAccount) {
      bankAccount$ = bankAccount.pipe(
        map((iban) => ibanPrefix(iban, CURRENCY.KZT))
      );
    }
    bankAccount$ =
      bankAccount$?.pipe(
        map((iban) => this.paymentsService.bankFromAccount.transform(iban))
      ) || of('');
    this.subs.add(
      combineLatest([
        date$.pipe(map((d) => !d)),
        bankAccount$.pipe(
          debounceTime(300),
          map((bic) => bic !== 'TSESKZKA')
        ),
        trigger$,
      ])
        .pipe(map((statuses) => statuses.every((s) => s)))
        .subscribe((s) => {
          if (s && urgent.disabled) {
            urgent.enable();
          } else if (!s && urgent.enabled) {
            urgent.disable();
            urgent.setValue(false);
          }
        })
    );
  }

  protected listenRouterParams() {
    this.subs.add(
      this.route.params
        .pipe(
          map((params) => ({
            state: window.history.state,
            params,
            route: this.route,
          })),
          debounceTime(0),
          switchMap(({ params, state }) => {
            // silent passed for redirect with id without reloading in addition with custom url state matcher after payment creation
            if (state?.silent) {
              if (this.restoredData.status === PAYMENT_STATUS.DRAFT) {
                this.paymentsService.rootTitle = PaymentRootTitle.EDIT_DRAFT;
              } else if (this.restoredData.status === PAYMENT_STATUS.CREATED) {
                this.paymentsService.rootTitle = PaymentRootTitle.EDIT_PAYMENT;
              }
              // only drafts can be edited on redirect after creation
              return EMPTY;
            } else if (state?.resolvedPayment) {
              return of(state.resolvedPayment);
            } else if (params.paymentId || state?.repeatPaymentId) {
              const paymentId = params.paymentId || state.repeatPaymentId;
              return from(this.api.getPaymentById(paymentId)).pipe(
                // wrong id
                catchError((e) => {
                  this.router.navigate([
                    this.router.url.replace('/' + paymentId, ''),
                  ]);
                  return EMPTY;
                })
              );
            }
            this.setAutoNumeration();
            this.paymentsService.rootTitle = PaymentRootTitle.NEW;
            return EMPTY;
          })
        )
        .subscribe((restorePayment) => {
          this.restorePayment(restorePayment);
        })
    );
  }

  protected getMaskConfig() {
    Object.entries(this.masks).forEach(
      ([k, v]) => (this.masks[k] = this.maskService.getConfig(v))
    );
  }

  protected checkPersonalAccounts(accounts: RegularAccount[]) {
    if (accounts.length === 0) {
      // no accounts
      this.paymentsService.infoMessage.entity = {
        type: MessageType.info,
        message: messages.noAccounts,
      };
      return;
    }
    if (accounts.every((a) => a.isClosed || a.isLocked)) {
      // no selectable accounts
      this.paymentsService.infoMessage.entity = {
        type: MessageType.info,
        message: messages.allLocked,
      };
      return;
    }
    if (accounts.length === 1) {
      this.form.patchValue({ senderAccount: accounts[0] });
      this.senderAccountDropdown.selectAccount(accounts[0]);
      this.form.markAsPristine();
    }
    this.paymentsService.infoMessage.entity = null;
  }

  protected createControlsStream(form: FormGroupTypeSafe<T>) {
    this.controlsStream = Object.entries(form.controls).reduce(
      (acc, [k, c], index, fg) => {
        acc[k] = (c as any).valueChanges.pipe(startWith((c as any).value));
        return acc;
      },
      {} as { [k in keyof T]: Observable<T[k]> }
    );
  }

  protected listenCommission() {
    const { amount, date, urgent, knp, senderAccount, kbk } =
      this.controlsStream;

    let streams = [amount, date, urgent, knp, senderAccount, kbk];
    streams = streams.filter((stream$) => !!stream$);

    const streamsSubscription$ = combineLatest(streams).pipe(
      debounceTime(300),
      distinctUntilChanged((prev, current) => {
        return prev.every((c, i) => c === current[i]);
      }),
      filter(() => {
        const { type, amount, date, urgent, knp, senderAccount, kbk } =
          this.form.controls;

        return [type, amount, date, urgent, knp, senderAccount, kbk]
          .filter((control) => control?.enabled)
          .every((control) => control.valid);
      })
    );

    const companySubscription$ = this.companyService.currentCompany$.pipe(
      filter((c) => !!c),
      distinctUntilChanged()
    );

    this.subs.add(
      combineLatest([streamsSubscription$, companySubscription$])
        .pipe(
          switchMap(([, company]) => {
            const { type, amount, date, urgent, knp, senderAccount, kbk } =
              this.form.getRawValue();

            const body = {
              amount: this.maskService.getNumberedAmount(amount),
              date,
              urgent,
              currency: CURRENCY.KZT,
              category: PAYMENT_CATEGORY.DOMESTIC,
              type,
              companyId: company.id,
              kbk,
              knp,
              senderAccount,
            };

            return this.paymentsService
              .calculateCommission$(body)
              .pipe(catchError(() => of(null)));
          })
        )
        .subscribe(this.commission$)
    );
  }

  protected listenVAT() {
    if (this.controlsStream.vat && this.controlsStream.amount) {
      this.subs.add(
        combineLatest([this.controlsStream.vat, this.controlsStream.amount])
          .pipe(
            map(([v, a]) => {
              return v
                ? Math.round(
                    ((this.maskService.getNumberedAmount(a) * 12.0) / 112.0 +
                      Number.EPSILON) *
                      100
                  ) / 100
                : 0;
            })
          )
          .subscribe(this.vat$)
      );
    }
  }

  protected listenTotal() {
    if (this.form.controls.amount) {
      this.subs.add(
        combineLatest(this.commission$, this.controlsStream.amount)
          .pipe(
            map(([c, a]) => {
              return c + this.maskService.getNumberedAmount(a);
            })
          )
          .subscribe(this.total$)
      );
    }
  }

  protected combineEmployeesAmount() {
    this.subs.add(
      this.controlsStream.employees
        .pipe(
          debounceTime(100),
          map((arr: EmployeeItemModel[]) => {
            return (arr || []).reduce((acc, item: EmployeeItemModel) => {
              return acc + item.amount;
            }, 0);
          })
        )
        .subscribe((v) => {
          this.form.controls.amount.patchValue(v);
        })
    );
  }

  protected combineKNP<S1, S2 = any, S3 = any>(
    filters: Array<(knps: KNP[], ...rest) => KNP[]> = [
      (knps) =>
        this.dictionaries.selectByPaymentType(knps, {
          type: this.paymentType,
          sortFavourite: true,
        }),
    ],
    streams?: [Observable<S1>, Observable<S2>?, Observable<S3>?]
  ): Observable<KNP[]> {
    const dataStreams = [this.knpStore.items$];
    if (streams) {
      dataStreams.push(...(streams as any));
    }
    return combineLatest(dataStreams).pipe(
      map((data) =>
        filters.reduce((acc, filterItem) => {
          return filterItem(acc, data.slice(1));
        }, data[0])
      ),
      share()
      // map(([knps, code]) => {
      //   if (!code) {
      //     return knps.filter((knp) => knp.favourite);
      //   } else {
      //     return knps;
      //   }
      // }),
    );
  }

  protected combineKBK(type: PAYMENT_TYPE) {
    this.kbk$ = this.kbkStore.items$;
    combineLatest([
      this.kbkStore.items$.pipe(
        map((kbks) =>
          this.dictionaries.selectByPaymentType(kbks, {
            type,
            sortFavourite: true,
          })
        )
      ),
      // this.controlsStream.kbk,
    ]).pipe(
      map(([kbks]) => kbks)
      // map(([kbks, code]) => {
      //   if (!code) {
      //     return kbks.filter((kbk) => kbk.favourite);
      //   } else {
      //     return kbks;
      //   }
      // })
    );
  }

  saveToDraft(body?: any) {
    (this.formDirective as any).submitted = true;
    this.formDirective.form.markAsPristine();
    const paymentNumberStore = this.paymentsService.paymentNumber;
    const rawBody = body || {
      ...this.form.value,
      purpose: [this.form.value.purpose, this.purposeMessage$.getValue()]
        .filter((v) => !!v)
        .join('; '),
    };
    paymentNumberStore.entity = null;
    this.processingPayment$.next({ id: this.paymentId, ...rawBody });

    this.pipeline
      .saveToDrafts(rawBody, {
        paymentId: this.paymentId,
        type: this.paymentType,
        commission: this.commission$.value,
        userPurpose: this.form.value.purpose,
        category: PAYMENT_CATEGORY.DOMESTIC,
      })
      .then((payment) => {
        if (payment.id !== this.paymentId) {
          if (payment) {
            this.restoredData = {
              status: payment.status,
              signatures: payment.signatures,
              companyId: payment.companyId,
            };
          }
          this.router.navigate(
            [this.route.snapshot.params.paymentId ? '../' : '.', payment.id],
            {
              replaceUrl: true,
              relativeTo: this.route,
              state: { silent: true },
            }
          );
        }
      })
      .catch((e) => {
        if (!paymentNumberStore.entity && !paymentNumberStore.fetching) {
          paymentNumberStore.entity = rawBody.number;
        }
      })
      .finally(() => this.processingPayment$.next(null));
  }

  setAutoNumeration() {
    this.subs.add(
      this.paymentsService.paymentNumber$
        .pipe(withLatestFrom(this.controlsStream.number))
        .subscribe(([numberFromStore, numberValue]) => {
          if (!numberValue) {
            this.form.controls.number.setValue(numberFromStore);
          }
        })
    );
  }

  protected provideAmountMessage() {
    if (this.form.controls.amount) {
      const { senderAccount, date } = this.controlsStream;
      this.subs.add(
        combineLatest([
          this.companyService.authorityLevel$,
          this.total$,
          senderAccount.pipe(filter((a) => !!a)),
          date,
          this.workingHours$.asObservable(),
        ])
          .pipe(
            switchMap(([authority, total, account, date, workingHours]) => {
              if (authority !== AUTHORITY_TYPE.PRIMARY_SIGNATURE) {
                return of('');
              }

              const edge = workingHours?.endTime || edgeTimeLabel;
              const diff = account.balance.amount - total;

              if (diff < 0) {
                const amount = this.maskService.formatAmount(Math.abs(diff));
                return date
                  ? this.translateService.get(
                      amountMessages.primaryDateBalanceLower,
                      {
                        amount,
                        edge: `${formatNgbDate(
                          date,
                          this.translateService.currentLang
                        )} ${edge}`,
                      }
                    )
                  : this.translateService.get(
                      amountMessages.primaryDateAutoBalanceLower,
                      { amount }
                    );
              } else {
                return of('');
              }
            })
          )
          .subscribe(this.amountMessage$)
      );
    }
  }

  protected listenWorkingDays() {
    this.subs.add(
      this.dictionaries.workingDays.entity$
        .pipe(
          map((workDays) => {
            return this.dictionaries.selectDaysTimeline(
              moment(ngbDateToDate(this.dateLimit.minDate)).format(ISO_8061),
              moment(ngbDateToDate(this.dateLimit.maxDate)).format(ISO_8061),
              workDays
            );
          })
        )
        .subscribe(this.workingDays$)
    );
  }

  protected listenDateMessageStream() {
    this.subs.add(
      combineLatest([
        this.companyService.authorityLevel$,
        this.controlsStream.date,
        this.controlsStream.senderAccount.pipe(filter((a) => !!a)),
      ])
        .pipe(
          map(([authority, date, account]) => {
            if (authority === AUTHORITY_TYPE.PRIMARY_SIGNATURE) {
              return dateMessages.primaryAny;
            }
            if (!date) {
              return dateMessages.secondaryDateAuto;
            }
            return dateMessages.secondaryDate;
          })
        )
        .subscribe(this.dateMessage$)
    );
  }

  protected listenPurposeMessageStream(
    messages: Observable<any[]>[] = [],
    commands: string[] | [string, (...rest) => string][] = ['vat', 'kbk', 'knp']
  ) {
    const formatters = {
      vat: (vat) => {
        if (vat) {
          return this.translateService.instant('PAYMENTS.FORM.INCLUDING_VAT', {
            amount: this.maskService.formatAmount(vat),
          });
        }
        return '';
      },
      kbk: (kbk) => {
        let kbkIem: KBK;
        if (
          kbk &&
          (kbkIem = this.kbkStore.items.find((item) => item.code === kbk))
        ) {
          return `${kbkIem.name}.`;
        }
        return '';
      },
      knp: (knp) => {
        let knpIem: KNP;
        if (
          knp &&
          (knpIem = this.knpStore.items.find((item) => item.code === knp))
        ) {
          return `${knpIem.name}.`;
        }
        return '';
      },
    };

    const streams = [];

    streams.push(
      ...[
        ((this.controlsStream.vat && this.vat$) || of(0)).pipe(
          map((vat) => ['vat', vat])
        ),
        this.controlsStream.knp.pipe(map((knp) => ['knp', knp])),
      ]
    );

    streams.push(...messages);

    this.subs.add(
      combineLatest(streams)
        .pipe(
          debounceTime(100),
          map((entries) =>
            entries.reduce((acc, item) => {
              acc[item[0]] = item[1];
              return acc;
            }, {} as Dictionary<string>)
          ),
          map((dictionary) => {
            const result = (commands as any[]).reduce((acc, command) => {
              if (Array.isArray(command)) {
                acc.push(command[1](dictionary[command[0]], dictionary));
              } else if (formatters[command]) {
                acc.push(formatters[command](dictionary[command], dictionary));
              } else {
                acc.push(dictionary[command]);
              }
              return acc;
            }, []);

            return result.filter((r) => !!r).join(' ');
          }),
          tap((v) => {
            this.form.controls.purpose.patchValue(
              this.form.controls.purpose.value
            );
            if (v) {
              this.form.controls.purpose.markAllAsTouched();
            }
          })
        )
        .subscribe(this.purposeMessage$)
    );

    this.subs.add(
      combineLatest(this.purposeMessage$, this.controlsStream.purpose)
        .pipe(map(([a, b]) => (a || '').length + (b || '').length))
        .subscribe(this.purposeLength$)
    );
    this.validatePurpose();
  }

  protected validatePurpose() {
    this.subs.add(
      combineLatest(
        this.purposeMessage$,
        this.controlsStream.purpose
      ).subscribe(([m, p]) => {
        const l = (p || '').length + (m || '').length;
        const errors: { [key: string]: any } = this.form.touched
          ? this.form.controls.purpose.errors
          : {};
        if (errors && l > this.rules.purposeLength) {
          errors.maxlength = l;
        }
        this.form.controls.purpose.setErrors(isEmpty(errors) ? null : errors, {
          emitEvent: true,
        });
      })
    );
  }

  protected restorePayment(data) {
    this.subs.add(
      combineLatest([
        this.knpStore.fetched$,
        this.kbkStore.fetched$,
        this.ugdStore.fetched$,
        this.dictionaries.bankDictionary.fetched$,
        this.accountStore.fetched$,
      ])
        .pipe(
          filter((loaded) => loaded.every((l) => l)),
          tap(() => {
            let model;

            try {
              model = this.pipeline.flattenPaymentToModel(data);
            } catch (e) {
              model = {};
            }
            this.restoredData = {
              companyId: data.companyId,
              status: data.status?.code,
              signatures: data.signatures,
            };
            // resolved payment full data but no id in router - going further as 'repeat payment' branch
            if (data.id && !this.paymentId) {
              model.number = '';
              model.date = null;
              this.restoredData.status = null;
              this.paymentsService.rootTitle = PaymentRootTitle.NEW;
            } else {
              this.paymentsService.rootTitle =
                this.restoredData.status === PAYMENT_STATUS.DRAFT
                  ? PaymentRootTitle.EDIT_DRAFT
                  : PaymentRootTitle.EDIT_PAYMENT;
            }

            if (model.emailOnSuccess) {
              model.notify = true;
            }

            this.form.patchValue({
              ...model,
              purpose: data.details.associatedField?.parameters?.paymentPurpose,
              // input prefix for bankAccount
              // bankAccount: model.bankAccount?.replace('KZ', ''),
            });

            this.senderAccountDropdown?.selectByIban(data?.details?.payerIban);

            // this.senderAccountDropdown.selectByIban(this.form.value.senderAccount.iban);

            // mark filled amount to trigger commission request;
            // reworked commission
            // this.form.controls.amount.value &&
            //   this.form.controls.amount.markAsDirty();
            this.isImported = data.imported;
            if (!model.number) {
              this.setAutoNumeration();
            }
          }),
          debounceTime(10),
          // tap(() => {
          //   // remove duplicated purpose additional message as vat or knp name
          //   this.form.controls.purpose.patchValue(
          //     this.form.controls.purpose.value.replace(
          //       this.purposeMessage$.getValue(),
          //       ''
          //     )
          //   );
          // }),
          take(1)
        )
        .subscribe()
    );
  }
}

