import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { PackageCalculation, PackageRest, PackageStatus, PackageTerms } from '@settings/models/packages.model';
import { options, themes } from '@settings/data/packages.data';
import { Subscription } from 'rxjs';
import { CompanyService } from '@company/company.service';
import { PackageRequestsService } from '@settings/services/package-requests.service';
import { IUserAuthority, User } from '@shared/models';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AlertPackageComponent } from '@settings/containers/settings-package/alert-package/alert-package.component';
import { AnalyticsManagerAbstract } from '@analytics/services/manager';

@Component({
  selector: 'j-settings-package',
  templateUrl: './settings-package.component.html',
  encapsulation: ViewEncapsulation.None,
})
export class SettingsPackageComponent implements OnInit, OnDestroy {
  public packageResponseStatus: PackageStatus; // Loading | Ready | Success | NotFound | Error | Forbidden;
  public packageCalculation: PackageCalculation; // calculate company package response
  public packageRest: PackageRest;  // get company package response
  //
  private subscription: Subscription = new Subscription();
  private companyId: string; // Selected companyID in menu
  //
  public themes: Array<any> = themes;   // ngx-skeleton style array object
  constructor(private packageRequestsService: PackageRequestsService,
              private companyService: CompanyService,
              private analytics: AnalyticsManagerAbstract,
              private modalService: NgbModal) {
  }

  ngOnInit() {
    this.subscription = this.companyService.refresh$
      .subscribe((data: [User | null, IUserAuthority | null, boolean | null]) => {
        this.companyId = data[1].companyId;
        this.getPackageRest();
      });
  }

  getPackageRest() {
    this.packageCalculation = undefined;
    this.packageResponseStatus = PackageStatus.Loading;
    this.packageRequestsService.getCompanyCurrentPackage(this.companyId)
      .then((data: PackageRest) => {
        this.packageRest = data;
        !!this.packageRest?.totalCost ?
          this.packageResponseStatus = PackageStatus.Success :
          this.packageResponseStatus = PackageStatus.NotFound;
        this.analytics.logEvent('JBundleview_Show', {version: 1, Status: '204'});
      }).catch(e => {
        if (e.status === 403) {
          this.packageResponseStatus = PackageStatus.Forbidden;
          this.analytics.logEvent('JBundleview_Show', {version: 1, Status: '403'});
        } else {
          this.packageResponseStatus = PackageStatus.Error;
          this.analytics.logEvent('JBundleview_Show', {version: 1, Status: '50x'});
      }
    });
  }

  calculatePackageTerms(packageTerms: PackageTerms) {
    this.packageRequestsService.calculatePackageCost(this.companyId, packageTerms)
      .then((data: PackageCalculation) => {
        this.packageCalculation = data;
      }).catch(() => {
      this.packageResponseStatus = PackageStatus.Error;
    });
  }

  changePackageTerms(alertMessageType: 'connect' | 'connect_standart' | 'change' | 'change_transfer' | 'change_account',  packageTerms: PackageTerms) {
    const modalRef = this.modalService.open(AlertPackageComponent, options);
    modalRef.componentInstance.alertMessageType = alertMessageType;
    modalRef.componentInstance.packageTerms = packageTerms;
    modalRef.componentInstance.packageRest = this.packageRest;
    modalRef.componentInstance.companyId = this.companyId;
    modalRef.componentInstance.isSameDay = this.packageRest.isSameDay;
    modalRef.componentInstance.isWorkingDay = this.packageRest.isWorkingDay;
    modalRef.componentInstance.iban = packageTerms.iban;
    modalRef.componentInstance.refresh.subscribe(({ typeUser, statusCode } ) => {
      this.getPackageRest();
      const event = alertMessageType === 'change_transfer'
        ? 'JBundle_AddPack_Result'
        : 'JBundleConnect_Result';
      this.analytics.logEvent(event, {version: 1, TypeUser: typeUser, Status: statusCode});
    });
    this.analytics.logEvent('JBundleEdit_Connect_Click', {version: 1});
  }

  resetPackageTerms(event: 'change' | 'reset' | 'cancel') {
    this.packageCalculation = undefined;
    if (event === 'change') {this.packageResponseStatus = PackageStatus.Change; } else
    if (event === 'cancel') {this.packageResponseStatus = PackageStatus.Success; } else
    if (event === 'reset') {
      const packageTermsStandart = new PackageTerms(null, 0, 0, false, false);
      this.changePackageTerms('connect_standart', packageTermsStandart);
    }
  }

  openModalByAction(event) {
    const {alert: alertMessageType, event: packageTerms} = event;
    if(alertMessageType === 'change_transfer' || alertMessageType === 'change_account') {
      this.changePackageTerms(alertMessageType, packageTerms);
    }
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
    this.packageCalculation = undefined;
  }

}
