import { Component, OnInit, Input } from '@angular/core';

import { CurrencyService } from '@shared/currency/currency.service';
import { RegularAccount } from '@accounts/index';
import { MaskedService } from '@shared/masked/masked.service';

@Component({
  selector: 'j-account-box',
  templateUrl: './account-box.component.html',
  styleUrls: ['./account-box.component.scss'],
})
export class AccountBoxComponent implements OnInit {
  @Input() account: RegularAccount;
  @Input() isRoutable: boolean = true;

  constructor(private maskedService: MaskedService) {}

  ngOnInit(): void {}

  onClick(event) {
    // console.log(event);
  }

  get isLargeAmount() {
    return (
      this.account?.balance &&
      this.maskedService.getMaskedAmount(this.account.balance.amount).length >
        16
    );
  }
}
