import { Component, OnInit, ViewEncapsulation, Input } from '@angular/core';
import { PaymentsService } from '@payments/services/payments.service';
import { KNP } from "@shared/models/dictionary";
import { Observable, combineLatest } from 'rxjs';
import {
  filter,
  map,
  debounceTime,
  distinctUntilChanged,
  startWith,
  tap,
} from 'rxjs/operators';
import { FormControl } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { SimpleStore } from '@utils/index';

@Component({
  selector: 'j-generic-dictionary',
  templateUrl: './generic-dictionary.component.html',
  styleUrls: ['./generic-dictionary.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class GenericDictionaryComponent<T extends {[key: string]: string, id: string}>
  implements OnInit {
  @Input() store: SimpleStore<T>;
  @Input() searchFn: (term: string, item: T) => boolean;
  @Input() searchPlaceholder = 'PAYMENTS.DICTIONARY.SEARCH';
  @Input() title = 'PAYMENTS.DICTIONARY.TITLE';
  list$: Observable<T[]>;
  search: FormControl;
  constructor(
    private paymentsS: PaymentsService,
    public modal: NgbActiveModal
  ) {}

  ngOnInit(): void {
    this.search = new FormControl('');

    this.list$ = combineLatest(
      this.store.items$,
      this.search.valueChanges.pipe(
        debounceTime(100),
        distinctUntilChanged(),
        startWith(this.search.value)
      )
    ).pipe(
      map(([list, search]) => {
        return list.filter((item) =>
          this.searchFn
            ? this.searchFn(search, item)
            : this.paymentsS.genericSearchTerm(search, item as any)
        );
      })
    );
  }

  closeModal(item: T) {
    this.modal.close(item);
  }
}
