import { HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApiService } from '@api/api.service';
import { CompanyService } from '@company/company.service';
import { currencyCode } from '@shared/currencies-list/currencies-list.service.abstract';

@Injectable()
export class AdditionalAccountsService {

    constructor(
        private apiService: ApiService,
        private companyService: CompanyService
    ) {}

    private getUrl(): string {
        const companyId = this.companyService.currentId;
        return `/companies/${companyId}/additional-accounts`;
    }

    check() {
        const url = this.getUrl();
        const headers = new HttpHeaders().set("client-type", "WEB");
        return this.apiService.postObservable(url, null, headers); 
    }

    apply(currencyCode: currencyCode, requestedAccountId: string, signature: string) {
        const url = `${this.getUrl()}/${requestedAccountId}:confirm`;
        const params = new HttpParams().set("currency", currencyCode);
        const headers = new HttpHeaders().set("PhoneAuthorization", signature);
        return this.apiService.postObservable(url, null, headers, params)
    }

    getAdditionalAccountsInProgress() {
        const url = `${this.getUrl()}/in-progress`;
        return this.apiService.getObservable(url);
    }

}
