import { ACCOUNT_TYPE, RegularAccount } from '@accounts/account.model';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Card } from '@cards/models';

@Component({
  selector: 'j-all-requisites-item',
  templateUrl: './all-requisites-item.component.html',
  styleUrls: ['./all-requisites-item.component.scss']
})
export class AllRequisitesItemComponent implements OnInit {
  @Input() isChecked: boolean;
  @Input() account: RegularAccount;
  @Output() selected = new EventEmitter<boolean>();

  constructor() { }

  ngOnInit(): void {
  }

  onSelect() {
    this.account.isSelected = !this.account.isSelected;
    this.selected.emit(this.account.isSelected);
  }

  mapSelectedAccountLabel(selectedAccount: RegularAccount | Card) {
    switch (selectedAccount?.accountType) {
      case ACCOUNT_TYPE.CARD:
        return 'ACCOUNTS.CARD';
      case ACCOUNT_TYPE.ACCOUNT:
        return 'ACCOUNTS.DROPDOWN.REGULAR';
      case ACCOUNT_TYPE.DEPOSIT:
        return 'ACCOUNTS.DEPOSIT';
      default:
        return null;
    }
  }
}
