import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  ViewChild,
} from '@angular/core';

import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';

import { Card, CARD_CATEGORY } from '@cards/models';
import { CardsService } from '@cards/services';

import { DropdownInputComponent } from '@shared/input';

import { ACCOUNT_TYPE, RegularAccount, Deposit, IAccountDropdownOption } from '../../account.model';
import { AccountsService } from '../../services';

@Component({
  selector: 'j-account-dropdown',
  templateUrl: './account-dropdown.component.html',
  styleUrls: ['./account-dropdown.component.scss'],
  animations: [
    trigger('smoothCollapse', [
      state(
        'initial',
        style({
          height: '0',
          overflow: 'hidden',
          opacity: '0.5',
          zIndex: 'unset',
        })
      ),
      state(
        'final',
        style({
          overflow: 'hidden',
          opacity: '1',
          zIndex: '50',
        })
      ),
      transition('initial=>final', animate('300ms ease-in-out')),
      transition('final=>initial', animate('300ms ease-in-out')),
    ]),
  ],
})
export class AccountDropdownComponent {
  @Input() label: string;
  @Input() inputClass: string;
  @Input() hasError: boolean = false;

  @Input() isLoading: boolean = false;
  @Input() isDisabled: boolean = false;
  @Input() isSmall: boolean = false;

  @Input() isFromArrowShown: boolean = false;
  @Input() isDisabledSelectable: boolean = false;

  @Input() accounts: IAccountDropdownOption[] = [];

  @Output() selected = new EventEmitter<IAccountDropdownOption>();
  public selectedAccount: IAccountDropdownOption;

  @ViewChild('accountsDropdown') accountsDropdown: DropdownInputComponent;

  ACCOUNT_TYPE = ACCOUNT_TYPE;

  blockedFn = this.isAccountBlocked.bind(this);

  constructor(
    private cardsService: CardsService,
    private accountsService: AccountsService
  ) {}

  public clearSelected() {
    this.accountsDropdown.clearSelected();
  }

  public selectAccount(account: IAccountDropdownOption) {
    if (!account) {
      return;
    }

    if (
      (account.accountType === ACCOUNT_TYPE.CARD &&
        account.hasLockedState &&
        !this.isDisabledSelectable) ||
      (account as RegularAccount).isMigrated
    ) {
      return;
    }

    this.selectedAccount = account;
    this.accountsDropdown.selectItem(account);
  }

  public selectByIban(iban: string) {
    const found = this.accounts?.find((account) => {
      return account?.iban === iban;
    });
    this.onSelect(found);
  }

  isAccountBlocked(account: IAccountDropdownOption) {
    if ((account as RegularAccount).isMigrated) {
      return true;
    }

    return false;
  }

  onSelect(account: IAccountDropdownOption) {
    this.selectAccount(account);
    this.selected.emit(account);
  }

  mapSelectedAccountLabel(selectedAccount: IAccountDropdownOption) {
    switch (selectedAccount?.accountType) {
      case ACCOUNT_TYPE.CARD:
        return this.cardsService.mapCardLabel(selectedAccount as Card);
      case ACCOUNT_TYPE.ACCOUNT:
        return 'ACCOUNTS.REGULAR';
      case ACCOUNT_TYPE.DEPOSIT:
        return this.accountsService.getDepositName(selectedAccount as Deposit);
      default:
        return null;
    }
  }

  getMaskedCardNumber(selectedAccount: IAccountDropdownOption) {
    if (selectedAccount.accountType != ACCOUNT_TYPE.CARD) {
      return null;
    }

    const cardNumber = (selectedAccount as Card).cardNumber;

    if (cardNumber) {
      return `${cardNumber.substring(0, 4)}   ${cardNumber.substring(
        cardNumber.length - 4,
        cardNumber.length
      )}`;
    }

    return '';
  }

  getMaskedIban(selectedAccount: IAccountDropdownOption) {
    const iban = selectedAccount.iban;

    if (iban) {
      return `${iban.substring(0, 4)}  ${iban.substring(
        iban.length - 4,
        iban.length
      )}`;
    }

    return '';
  }

  getCardImage(selectedAccount: IAccountDropdownOption) {
    if (selectedAccount.accountType != ACCOUNT_TYPE.CARD) {
      return null;
    }

    const prefix = '/assets/icons/accounts';

    switch ((selectedAccount as Card).category) {
      case CARD_CATEGORY.PRIVATE:
        return `${prefix}/card-private.svg`;

      case CARD_CATEGORY.COMMERCIAL:
      default:
        return `${prefix}/card-commercial.svg`;
    }
  }

  getCardMps(selectedAccount: IAccountDropdownOption) {
    if (selectedAccount.accountType != ACCOUNT_TYPE.CARD) {
      return null;
    }

    const prefix = '/assets/icons/cards/mps';

    if ((selectedAccount as Card).cardNumber[0] === '4') {
      return `${prefix}/visa.svg`;
    }

    return `${prefix}/mastercard.svg`;
  }

  getIsBalanceShown(selectedAccount: IAccountDropdownOption) {
    if (selectedAccount.accountType === ACCOUNT_TYPE.ALL) {
      return false;
    }

    if (
      selectedAccount.accountType === ACCOUNT_TYPE.CARD &&
      (selectedAccount as Card).category === CARD_CATEGORY.PRIVATE
    ) {
      return false;
    }
    return true;
  }
}
