import { Component, EventEmitter, Input, Output } from '@angular/core';
import { PackageStatus } from '@settings/models';

@Component({
  selector: 'j-settings-package-header',
  templateUrl: './settings-package-header.component.html',
  styleUrls: ['./settings-package-header.component.scss'],
})
export class SettingsPackageHeaderComponent {
  @Input() status: PackageStatus = PackageStatus.Loading;
  @Output() resetPackageTerms: EventEmitter<'change' | 'reset' | 'cancel'> =
    new EventEmitter<'change' | 'reset' | 'cancel'>();
  public packageTitle = {
    not_found: 'SETTINGS.PACKAGE.TITLE.NOT_FOUND',
    change: 'SETTINGS.PACKAGE.TITLE.CHANGE',
    success: 'SETTINGS.PACKAGE.TITLE.SUCCESS',
  };
}
