import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';

export type AdditionalAccountModalResultType = 'success'|'fail';

export type AdditionalAccountModalResultTypeConfig = {
    iconName: AdditionalAccountModalResultType,
    header: string,
    label: string,
    btnText: string,
};

@Component({
  selector: 'j-open-additional-account-result',
  templateUrl: './result.component.html',
  styleUrls: ['./result.component.scss']
})
export class ResultComponent implements OnInit, OnChanges {

  @Output() close = new EventEmitter();
  @Input() result: AdditionalAccountModalResultTypeConfig;

  public src: string;

  constructor() { }

  ngOnInit(): void {
  }

  ngOnChanges(): void {
      this.src = `/assets/additional-account/${this.result.iconName}.svg`;
  }

  onCloseClick() {
    this.close.emit();
  }

}
