import { Component, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DatePipe } from '@angular/common';

import { Subscription } from 'rxjs';
import { take } from 'rxjs/operators';

import { IStatement, STATEMENT_MODE } from '@statement/index';

import {
  IFilterScheme,
  FILTER_MODE,
  FilterService,
  FILTER_PERIOD_TYPE,
} from '@filter/index';

import { CardsService } from '../../services';
import { Card } from '../../models';
import { FilesService } from '@shared/services';
import { NavService } from '@shared/nav/nav.service';

const PAGE_START = 1;
const PAGE_SIZE = 10;

@Component({
  selector: 'j-card-statement',
  templateUrl: './card-statement.component.html',
  styleUrls: ['./card-statement.component.scss'],
})
export class CardStatementComponent implements OnDestroy {
  card: Card;

  statement: IStatement;
  statementMode = STATEMENT_MODE.CARDS;

  errorText: string;

  isLoading: boolean = true;

  filter: IFilterScheme;
  filterMode = FILTER_MODE.CARDS;

  hasMorePages: boolean = true;
  cardId: string;
  private pageNumber = PAGE_START;
  private pageSize = PAGE_SIZE;

  readonly subscription = new Subscription();

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private cardsService: CardsService,
    private filterService: FilterService,
    private datePipe: DatePipe,
    private filesService: FilesService,
    private navService: NavService
  ) {
    const routeParamSubscription = this.route.params.subscribe((params) => {
      this.cardId = params.id;
      this.loadStatement();
    });

    const routeQuerySubscription = this.route.queryParams
      .pipe(take(1))
      .subscribe((queryParams) => {
        this.filter = this.mapQueryParams(queryParams);
        this.filterService.updateExtra(this.filter);
      });

    const filterSubscription = this.filterService
      .getScheme()
      .subscribe((scheme) => {
        this.filter = Object.assign({}, scheme);
        this.pageNumber = 1;
        this.hasMorePages = true;
        this.isLoading = true;
        this.updateQueryParams();
        this.loadStatement();
      });

    this.subscription.add(routeParamSubscription);
    this.subscription.add(routeQuerySubscription);
    this.subscription.add(filterSubscription);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  onClose() {
    this.navigateToHome();
  }

  onScroll() {
    if (this.hasMorePages && !this.isLoading) {
      this.pageNumber++;
      this.loadStatement();
    }
  }

  async onDownload(exportType: string) {
    const downloadStatement = await this.cardsService.getStatement(
      this.cardId,
      this.filter,
      this.pageNumber,
      this.pageSize,
      exportType
    );

    let extension = exportType.substring(
      exportType.lastIndexOf('/') + 1,
      exportType.length
    );

    if (extension == '1c') {
      extension += '.txt';
    }

    let filename = `${this.cardId}.${extension}`;

    this.filesService.downloadFile(downloadStatement, filename);
  }

  private async loadStatement() {
    this.errorText = '';
    this.isLoading = true;
    
    try {
      
      if (
        this.filter.periodType &&
        this.filter.periodType != FILTER_PERIOD_TYPE.CUSTOM
      ) {
        const period = this.filterService.PERIOD_LIST.find((item) => {
          return item.type == this.filter.periodType;
        });
        this.filter.periodFrom = period.dates.from;
        this.filter.periodTo = period.dates.to;
      }
      const retrievedStatement = await this.cardsService.getStatement(
        this.cardId,
        this.filter,
        this.pageNumber,
        this.pageSize
      );

      this.hasMorePages =
        this.pageSize <= retrievedStatement.totalTransactionsCount;

      this.statement = retrievedStatement;
    } catch (error) {
      this.errorText = error.description || error.code || error;
    } finally {
      this.isLoading = false;
    }
  }

  private navigateToHome() {
    this.navService.retrieveContractBadges();
    this.navService.retrieveLetterBadges();

    this.router.navigate(['/']);
  }

  private mapQueryParams(queryParams: any) {
    let filter = Object.assign(
      {},
      this.filterService.getDefaults(FILTER_MODE.ACCOUNTS)
    );

    for (const key in queryParams) {
      if (Object.prototype.hasOwnProperty.call(queryParams, key)) {
        const param = queryParams[key];

        if (param && param != null && param != 'null') {
          if (key == 'periodFrom' || key == 'periodTo') {
            filter[key] = new Date(param);
          } else {
            filter[key] = param;
          }
        }
      }
    }

    return filter;
  }

  private updateQueryParams() {
    const queryParams: any = Object.assign({}, this.filter);

    const newFilter = {};

    for (const key in queryParams) {
      if (Object.prototype.hasOwnProperty.call(queryParams, key)) {
        const param = queryParams[key];

        if (param && param != null && param != 'null') {
          if (key == 'periodFrom' || key == 'periodTo') {
            newFilter[key] = this.datePipe.transform(param, 'yyyy-MM-dd');
          } else {
            newFilter[key] = param;
          }
        }
      }
    }

    this.router.navigate([], {
      queryParams: newFilter,
      relativeTo: this.route,
    });
  }
}
