import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { Subscription } from 'rxjs';
import { filter, map } from 'rxjs/operators';

import { SimpleStore } from '@utils/simple-store';

import {
  AccountsService,
  RegularAccount,
  Deposit,
  ACCOUNT_TYPE,
} from '@accounts/index';

import { CompanyService } from '@company/company.service';

import { CARD_CATEGORY, ICard } from '@cards/models';
import { CardsService } from '@cards/services/cards.service';

import { AuthService } from '@auth/auth.service';

import { LoanService } from '@loans/services';
import { ILoan } from '@loans/models';

import { AdditionalAccountsService } from '@home/modules/open-additional-account/services/additional-accounts.service';

import { FeatureFlagsService, FEATURE_FLAG } from '@feature-flags/index';

import { PackageRequestsService } from '@settings/services/package-requests.service';
import { PackageRest, PackageStatus } from '@settings/models/packages.model';

import { IUserAuthority, User } from '@shared/models';

@Component({
  selector: 'j-accounts-list',
  templateUrl: './accounts-list.component.html',
  styleUrls: ['./accounts-list.component.scss'],
})
export class AccountsListComponent implements OnInit, OnDestroy {
  accounts: RegularAccount[];
  cards: ICard[];
  deposits: Deposit[];
  loans: ILoan[];
  currentCompanyId: string;

  accountsError = '';
  cardsError = '';

  subscription = new Subscription();

  accountsStore: SimpleStore<RegularAccount>;

  isAccountsLoading = false;
  // isCardsLoading = false;

  pendingCardRequest = null;
  pendingLoanRequest = null;
  pendingAccountsRequest = null;

  mergeDate: string;
  isExchangeRatesVisible = false;
  isLoansVisible = false;

  accountsInProgress;
  public packageResponseStatus: PackageStatus; // Loading | Ready | Success | NotFound | Error | Forbidden;
  private companyId: string; // Selected companyID in menu
  public packageRest: PackageRest; // get company package response
  public showMainScreenPackage: boolean = false;

  constructor(
    public companyService: CompanyService,
    private accountsService: AccountsService,
    private cardsService: CardsService,
    private loanService: LoanService,
    private router: Router,
    private route: ActivatedRoute,
    private authService: AuthService,
    private featureFlagsService: FeatureFlagsService,
    private additionalAccountsService: AdditionalAccountsService,
    private packageRequestsService: PackageRequestsService
  ) {
    this.getExchangeRatesFlag();
  }

  getExchangeRatesFlag() {
    this.isExchangeRatesVisible = !this.featureFlagsService.isDisabled(
      FEATURE_FLAG.CURRENCY_RATES
    );
    this.isLoansVisible = !this.featureFlagsService.isDisabled(
      FEATURE_FLAG.LOANS_VIEW
    );
    this.subscription.add(
      this.featureFlagsService.featureFlags$.subscribe((res) => {
        this.isExchangeRatesVisible =
          res.find(
            (featureFlag) => featureFlag.key === FEATURE_FLAG.CURRENCY_RATES
          )?.active === true || false;
        this.isLoansVisible =
          res.find((featureFlag) => featureFlag.key === FEATURE_FLAG.LOANS_VIEW)
            ?.active === true || false;
        if (this.isLoansVisible) {
          this.loadLoans();
        }
        this.showMainScreenPackage = !this.featureFlagsService.isDisabled(
          FEATURE_FLAG.PACKAGES_MAIN_SCREEN
        );
      })
    );
  }

  ngOnInit() {
    this.accountsStore = this.accountsService.store;
    this.accountsStore.items$.subscribe(
      (accounts) => {
        this.accountsError = '';
        this.mapAccountsAndDeposits(accounts);
      },
      (error) => {
        this.accountsError = error.desciption || error.code || error;
        // console.log(error);
      }
    );

    this.subscription.add(
      this.companyService.refresh$
        .pipe(filter(([, company, isAuthorized]) => isAuthorized))
        .subscribe((data) => {
          this.loadLists();
          // console.log(data);
          const [, company, isAuthorized] = data;

          this.companyId = company?.companyId;
          this.getPackageRest();

          // if (isAuthorized) {
          // } else {
          //   this.authService.logout();
          //   this.accounts = null;
          //   this.cards = null;
          //   this.accountsInProgress = null;
          // }
        })
    );
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  mapAccountsAndDeposits(accounts: (RegularAccount | Deposit)[]) {
    this.applyMigratedAccountsRule(accounts);

    this.accounts = accounts.filter(
      (account) => account.accountType === ACCOUNT_TYPE.ACCOUNT
    );

    this.deposits = (accounts as Deposit[]).filter(
      (account) => account.accountType === ACCOUNT_TYPE.DEPOSIT
    );

    if (this.accounts.filter((account) => account.hasAtfIban).length > 0) {
      this.applyFirstAtfAccountsRule(this.accounts);
    } else if (
      this.deposits.filter((deposit) => deposit.hasAtfIban).length > 0
    ) {
      this.applyFirstAtfAccountsRule(this.deposits);
    }
  }

  async loadLists() {
    if (this.isLoansVisible) {
      this.loadLoans();
    }
    this.loadCards();
    // this.loadAccountsAndDeposits();
    this.loadAccountsInProgress();
  }

  loadAccountsInProgress() {
    this.accountsInProgress =
      this.additionalAccountsService.getAdditionalAccountsInProgress();
  }

  async loadAccountsAndDeposits() {
    this.accounts = null;
    this.deposits = null;
    this.isAccountsLoading = true;

    try {
      const combinedAccounts = await this.accountsService.getAccounts(
        this.companyService.currentId
      );
      this.applyMigratedAccountsRule(combinedAccounts);
      this.accounts = combinedAccounts.filter(
        (account) => account.accountType === ACCOUNT_TYPE.ACCOUNT
      );

      this.deposits = (combinedAccounts as Deposit[]).filter(
        (account) => account.accountType === ACCOUNT_TYPE.DEPOSIT
      );

      if (this.accounts.filter((account) => account.hasAtfIban).length > 0) {
        this.applyFirstAtfAccountsRule(this.accounts);
      } else if (
        this.deposits.filter((deposit) => deposit.hasAtfIban).length > 0
      ) {
        this.applyFirstAtfAccountsRule(this.deposits);
      }
    } catch (error) {
      this.accountsError = error.desciption || error.code || error;
    } finally {
      this.isAccountsLoading = false;
    }
  }

  navigateToAccount(account: RegularAccount) {
    this.router.navigate([`/accounts/${account.iban}`], {
      relativeTo: this.route,
    });
  }

  navigateToDeposit(deposit: Deposit) {
    this.router.navigate([`/deposits/${deposit.iban}`], {
      relativeTo: this.route,
    });
  }

  navigateToCorporateCard(card: ICard) {
    this.router.navigate([`/cards/${card.cardId}`]);
  }

  get accounts$() {
    return this.accountsStore.items$.pipe(
      map((items: RegularAccount[]) =>
        items.filter((item) => item.accountType === ACCOUNT_TYPE.ACCOUNT)
      )
    );
  }

  private applyFirstAtfAccountsRule(accounts: RegularAccount[] | Deposit[]) {
    accounts.find((account) => account.hasAtfIban).isFirstAtfInTheList = true;
  }

  private applyMigratedAccountsRule(accounts: RegularAccount[] | Deposit[]) {
    if (accounts.find((account) => account.isMigrated)) {
      this.accountsService.retrieveMergeDate();
    }
  }

  private loadCards() {
    if (this.pendingCardRequest) {
      this.pendingCardRequest.unsubscribe();
    }

    this.cards = null;
    this.pendingCardRequest = this.cardsService
      .getCorporateCardsAsObservable()
      .subscribe((retrievedCards) => {
        this.cards = retrievedCards.filter(
          (card) => card.category === CARD_CATEGORY.COMMERCIAL
        );
        this.pendingCardRequest = null;
      });
  }

  private loadLoans() {
    if (this.pendingLoanRequest) {
      this.pendingLoanRequest.unsubscribe();
    }

    this.loans = null;
    this.pendingLoanRequest = this.loanService
      .getLoansAsObservable()
      .subscribe((retrievedLoans) => {
        this.loans = retrievedLoans;
        this.pendingLoanRequest = null;
      });
  }

  navigateToLoan(loan: ILoan) {
    this.router.navigate([`/loans/${loan.id}`]);
  }

  getPackageRest() {
    this.packageResponseStatus = PackageStatus.Loading;
    this.packageRequestsService
      .getCompanyCurrentPackage(this.companyId)
      .then((data: PackageRest) => {
        this.packageRest = data;
        this.packageResponseStatus = !!this.packageRest?.totalCost
          ? PackageStatus.Success
          : PackageStatus.NotFound;
      });
  }
}
