import { Component, Input } from '@angular/core';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

import { Transaction } from '../../models/transaction.model';

@Component({
  selector: 'j-transaction-sign-error',
  templateUrl: './transaction-sign-error.component.html',
  styleUrls: ['./transaction-sign-error.component.scss'],
})
export class TransactionSignErrorComponent {
  @Input() transaction: Transaction;
  @Input() errorText: string;

  constructor(public modal: NgbActiveModal) {}

  dismissModal() {
    this.modal.dismiss();
  }

  closeModal() {
    this.modal.close();
  }
}
