import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormControl } from '@angular/forms';

import { Observable, combineLatest } from 'rxjs';
import {
  map,
  debounceTime,
  distinctUntilChanged,
  startWith,
} from 'rxjs/operators';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

import { PaymentsService } from '@payments/services/payments.service';
import { UGD } from '@shared/models/dictionary';
import { DictionariesService } from '@shared/services/dictionaries.service';

@Component({
  selector: 'j-ugd-dictionary',
  templateUrl: './ugd-dictionary.component.html',
  styleUrls: ['./ugd-dictionary.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class UgdDictionaryComponent implements OnInit {
  list$: Observable<UGD[]>;
  search: FormControl;
  constructor(
    private paymentsS: PaymentsService,
    protected dictionaries: DictionariesService,
    public modal: NgbActiveModal
  ) {}

  ngOnInit(): void {
    this.search = new FormControl('');

    this.list$ = combineLatest(
      this.dictionaries.UGDictionary.items$,
      this.search.valueChanges.pipe(
        debounceTime(100),
        distinctUntilChanged(),
        startWith(this.search.value)
      )
    ).pipe(
      map(([list, search]) => {
        return list.filter((item) => this.paymentsS.ugdSearchFn(search, item));
      })
    );
  }

  closeModal(item: UGD) {
    this.modal.close(item);
  }
}
