import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { currencyCode } from '@shared/currencies-list/currencies-list.service.abstract';

@Component({
  selector: 'j-account-box-in-progress',
  templateUrl: './account-box-in-progress.component.html',
  styleUrls: ['./account-box-in-progress.component.scss']
})
export class AccountBoxInProgressComponent implements OnInit, OnChanges {

  // TODO maybe extract currency icon to standalone component
  @Input() currencyCode: currencyCode;

  src: string;

  constructor() { }

  ngOnInit(): void {
  }

  ngOnChanges(): void {
    this.src = `/assets/currency/${this.currencyCode}.svg`
  }

}
