import { Injectable } from '@angular/core';
import { HttpHeaders, HttpParams } from '@angular/common/http';

import { TranslateService } from '@ngx-translate/core';

import { ApiService } from '@api/index';
import { ICard, Card, CARD_STATUS, CARD_CATEGORY } from '@cards/models';

import {
  IStatement,
  StatementService,
} from '@statement/index';

import { CompanyService } from '@company/company.service';

import { IFilterScheme } from '@filter/index';

import { NotifierService } from 'angular-notifier';
import { Subject } from 'rxjs';
import { map } from 'rxjs/operators';
import { COMPANY_TYPE } from '@company/company.model';
import { ACCOUNT_TYPE } from '@accounts/account.model';

@Injectable({
  providedIn: 'root',
})
export class CardsService {
  private cardsList: Card[];
  cardsUpdated$ = new Subject();

  private cashedStatement: IStatement;
  private cashedFilter: IFilterScheme;
  private cashedPageNumber: number;
  private cashedPageSize: number;
  private cardId: string;

  constructor(
    private apiService: ApiService,
    private companyService: CompanyService,
    private statementService: StatementService,
    private translateService: TranslateService,
    private notifierService: NotifierService,
  ) {}

  async getStatement(
    cardId: string, 
    filterParams: IFilterScheme,
    pageNumber: number = 1,
    pageSize: number = 50,
    exportType: string = null
  ): Promise<IStatement> {
    if (
      this.cashedStatement &&
      this.cashedFilter === filterParams &&
      this.cashedPageNumber === pageNumber &&
      this.cashedPageSize === pageSize &&
      this.cardId === cardId &&
      !exportType
    ) {
      return this.cashedStatement;
    }

    const url = `companies/${this.companyService.currentId}/corporate-cards/${cardId}/statements`;

    let isNextPage;

    if (exportType) {
      isNextPage = false;
    } else {
      isNextPage = this.cashedFilter === filterParams && pageNumber > this.cashedPageNumber
    }

    try {
      const cardStatement = await this.statementService.getStatement(
        url,
        filterParams,
        this.cashedStatement,
        isNextPage,
        pageNumber,
        pageSize,
        false,
        exportType,
        ACCOUNT_TYPE.CARD
      );
      
      if (exportType) {
        return cardStatement;
      }
      this.cashedFilter = filterParams;
      this.cashedPageNumber = pageNumber;
      this.cashedPageSize = pageSize;

      this.cashedStatement = cardStatement;
      this.cardId = cardId;

      return this.cashedStatement;
    } catch (error) {
      throw error;
    }
  }

  mapCardLabel(card: Card) {
    if (card.alias) {
      return card.alias;
    }

    switch (card.category) {
      case CARD_CATEGORY.PRIVATE:
        return 'ACCOUNTS.PRIVATE_CARD';

      case CARD_CATEGORY.COMMERCIAL:
        return this.companyService.currentCompany.type == COMPANY_TYPE.CORPORATE
          ? 'ACCOUNTS.CORPORATE_CARD'
          : 'ACCOUNTS.ENTREPRENEUR_CARD';

      default:
        return 'ACCOUNTS.CARD';
    }
  }

  async getCorporateCards() {
    const url = `companies/${this.companyService.currentId}/corporate-cards`;

    let params = new HttpParams().set('includePrivate', 'true');

    const retrievedList = await this.apiService.get<ICard[]>(url, null, params);

    this.cardsList = retrievedList.map((retrievedCard) => {
      return new Card(retrievedCard);
    });

    return this.cardsList;
  }

  getCorporateCardsAsObservable() {
    const url = `companies/${this.companyService.currentId}/corporate-cards`;

    let params = new HttpParams().set('includePrivate', 'true');

    return this.apiService.getObservable<ICard[]>(url, null, params).pipe(
      map((retrievedList) => {
        this.cardsList = retrievedList.map((retrievedCard) => {
          return new Card(retrievedCard);
        });
        return this.cardsList;
      })
    );
  }

  async getCardById(cardId: string) {
    if (!this.cardsList) {
      await this.getCorporateCards();
    }
    return this.findCardById(cardId);
  }

  async getQrImg(cardId: string, cardIban: string) {
    const url = `companies/${this.companyService.currentId}/corporate-cards/${cardId}/qr`;

    let params = new HttpParams();
    params = params.append('iban', cardIban);

    return this.apiService.get<{ base64: string }>(url, null, params);
  }

  async getQrPrint(cardId: string, cardIban: string) {
    const url = `companies/${this.companyService.currentId}/corporate-cards/${cardId}/qr/print`;

    let headers = new HttpHeaders();
    headers = headers.set('Content-Type', 'application/octet-stream');

    let params = new HttpParams();

    params = params.append('iban', cardIban);

    const extras = {
      responseType: 'blob',
    };

    return this.apiService.get(url, headers, params, extras);
  }

  async getPdf(cardId: string) {
    const url = `companies/${this.companyService.currentId}/corporate-cards/${cardId}/requisites`;
    let headers = new HttpHeaders();
    headers = headers.set('Accept-File', 'application/pdf');
    headers = headers.set('Content-Type', 'application/octet-stream');

    const params = new HttpParams();
    const extras = {
      responseType: 'blob',
    };

    return this.apiService.get(url, headers, params, extras);
  }

  async changeCardStatus(cardId: string, value: boolean) {
    const url = `companies/${this.companyService.currentId}/corporate-cards/${cardId}/status`;
    const body = {
      status: value ? 'BLOCK' : 'UNBLOCK',
    };

    try {
      const lockedResult = await this.apiService.post(url, body);
      this.cardsList.find((card) => card.cardId === cardId).status = value
        ? CARD_STATUS.TEMPORARILY_LOCK
        : CARD_STATUS.ACTIVE;

      this.cardsUpdated$.next(this.cardsList);

      this.showCardStatusChangeSuccessNotification(value);
      return lockedResult;
    } catch (error) {
      this.showCardStatusChangeErrorNotification(value);
      throw error;
    }
  }

  async changeCardVisibility(cardId: string, value: boolean) {
    const action = value ? 'hide' : 'unhide';
    const url = `companies/${this.companyService.currentId}/corporate-cards/${cardId}:${action}`;

    try {
      const lockedResult = await this.apiService.patch(url);

      this.cardsList.find((card) => card.cardId === cardId).hidden = value;
      this.cardsUpdated$.next(this.cardsList);

      this.showCardVisibilityChangeSuccessNotification(value);
      return lockedResult;
    } catch (error) {
      this.showCardVisibilityChangeErrorNotification(value);
      throw error;
    }
  }

  mapCardStatus(cardStatus: CARD_STATUS) {
    const prefix = `CARDS.STATUS.TITLE.`;

    switch (cardStatus) {
      case CARD_STATUS.EXPIRED:
        return `${prefix}EXPIRED`;
      case CARD_STATUS.TEMPORARILY_LOCK:
        return `${prefix}TEMPORARILY_LOCK`;
      case CARD_STATUS.FRAUD:
      case CARD_STATUS.LOST_LOCK:
      case CARD_STATUS.THEFT_LOCK:
        return `${prefix}LOCK`;
      default:
        return `${prefix}ACTIVE`;
    }
  }

  findCardById(cardId: string) {
    return this.cardsList.find((card) => {
      return card.cardId === cardId;
    });
  }

  renameCard(cardId: string, alias: string) {
    const url = `companies/${this.companyService.currentId}/corporate-cards/${cardId}/alias`;
    const body = {
      alias,
    };

    return this.apiService.put(url, body);
  }

  private showCardStatusChangeSuccessNotification(switchValue: boolean) {
    const key = switchValue
      ? 'ACTIVATED_NOTIFICATION'
      : 'DEACTIVATED_NOTIFICATION';
    const message = this.translateService.instant(`CARDS.SETTINGS.LOCK.${key}`);

    this.notifierService.hideAll();
    this.notifierService.notify('default', message);
  }

  private showCardStatusChangeErrorNotification(switchValue: boolean) {
    const key = switchValue ? 'ACTIVATED_FAILED' : 'DEACTIVATED_FAILED';
    const message = this.translateService.instant(`CARDS.SETTINGS.LOCK.${key}`);

    this.notifierService.hideAll();
    this.notifierService.notify('error', message);
  }

  private showCardVisibilityChangeSuccessNotification(switchValue: boolean) {
    const key = switchValue ? 'HIDDEN_NOTIFICATION' : 'SHOWN_NOTIFICATION';
    const message = this.translateService.instant(`CARDS.SETTINGS.HIDE.${key}`);

    this.notifierService.hideAll();
    this.notifierService.notify('default', message);
  }

  private showCardVisibilityChangeErrorNotification(switchValue: boolean) {
    const key = switchValue ? 'HIDDEN_FAILED' : 'SHOWN_FAILED';
    const message = this.translateService.instant(`CARDS.SETTINGS.HIDE.${key}`);

    this.notifierService.hideAll();
    this.notifierService.notify('error', message);
  }
}
