import { Injectable } from '@angular/core';


import { ApiService } from '@api/index';

import { CompanyService } from '@company/company.service';

import { map } from 'rxjs/operators';
import { ILoan, ILoanSchedule, Loan } from '../models';
import { HttpHeaders, HttpParams } from '@angular/common/http';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class LoanService {
  private loanList: Loan[];
  cardsUpdated$ = new Subject();

  constructor(
    private apiService: ApiService,
    private companyService: CompanyService,
  ) {}

  async getCorporateLoans() {
    const url = `companies/${this.companyService.currentId}/loans`;

    const params = new HttpParams().set('includePrivate', 'true');

    const retrievedList = await this.apiService.get<ILoan[]>(url, null, params);

    this.loanList = retrievedList.map((retrievedLoan) => {
      return new Loan(retrievedLoan);
    });

    return this.loanList;
  }

  getLoansAsObservable() {
    const url = `companies/${this.companyService.currentId}/loans`;

    return this.apiService.getObservable<ILoan[]>(url, null).pipe(
      map((retrievedList) => {
        this.loanList = retrievedList.map((retrievedLoan) => {
          return new Loan(retrievedLoan);
        });
        return this.loanList;
      })
    );
  }

  async getLoanById(cardId: string) {
    if (!this.loanList) {
      await this.getCorporateLoans();
    }
    return this.findLoanById(cardId);
  }

  async getPdf(loanId: string) {
    const url = `companies/${this.companyService.currentId}/loans/${loanId}/download`;
    let headers = new HttpHeaders();
    headers = headers.set('Accept-File', 'application/pdf');
    headers = headers.set('Content-Type', 'application/octet-stream');

    const params = new HttpParams();
    const extras = {
      responseType: 'blob',
    };

    return this.apiService.get(url, headers, params, extras);
  }

  findLoanById(loanId: string) {
    return this.loanList.find((loan) => {
      return loan.id === loanId;
    });
  }

  getSchedule(loanId: string) {
    const url = `companies/${this.companyService.currentId}/loans/${loanId}/schedule`;
    return this.apiService.get<ILoanSchedule>(url);
  }
}
