import { Component, Input, OnInit } from '@angular/core';
import { CardsService } from '@cards/services';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'j-card-status-modal',
  templateUrl: './card-status-modal.component.html',
  styleUrls: ['./card-status-modal.component.scss'],
})
export class CardStatusModalComponent {
  @Input() img: string;
  @Input() title: string;
  @Input() description: string;
  @Input() cardId: string;
  @Input() canUnlock: boolean;

  link = `https://jysanbank.kz/ru/find/offices/almaty`;

  constructor(
    public modal: NgbActiveModal,
    private cardsService: CardsService
  ) {}

  dismissModal(event: Event = null) {
    event?.preventDefault();
    this.modal.dismiss();
  }

  async unlockCard(event: Event = null) {
    event?.preventDefault();

    try {
      await this.cardsService.changeCardStatus(this.cardId, false);
      this.modal.close();
    } catch (error) {
      this.modal.dismiss();
    }
  }
}
