import { Component, Inject, Input, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { ILoanSchedule, IOperationType, Loan, LoanScroll } from '@loans/models';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'j-loan-statement',
  templateUrl: './loan-statement.component.html',
  styleUrls: ['./loan-statement.component.scss']
})
export class LoanStatementComponent implements OnDestroy{

  @Input() loan: Loan;
  @Input() schedule: ILoanSchedule;
  @Input()
  public set selected(val: IOperationType) {
    this.selectedOperation = val;
    this.scrollToView(val?.code);
  }
  @Input()
  public set scrollState(val: LoanScroll) {
    this.scrollStateVal = val;
  }
  scrollStateVal: LoanScroll;

  selectedOperation: IOperationType;
  payment;
  loanId: string;
  isLoading = true;
  wrapperClasses = '';
  detailsClasses = '';
  isDetailsShown = false;
  isAnimating = false;
  isSumsShown = true;

  constructor(
    private router: Router,
    @Inject(DOCUMENT) private document: Document,
  ) { }

  ngOnDestroy(): void {
    this.document.body.classList.remove('overflow-hidden');
  }

  scrollToView(code: string) {
    const scrollToElem = this.document.getElementById(code)?.nextSibling;
    const scrollContainer = this.document.querySelector('html');
    if (scrollToElem) {
      this.scrollToElm(scrollContainer, scrollToElem, 1);
    }
  }

  scrollToElm(container, elm, duration = 2) {
    if (elm) {
      const offsetVal = this.scrollStateVal.coord_y > 0 ? elm.offsetTop - 200 : elm.offsetTop - 350;
      this.scrollTo(container, offsetVal, duration);  // duration in seconds
    }
  }

  scrollTo(element, to, duration, onDone?) {
    const start = element.scrollTop;
    const change = to - start;
    const startTime = performance.now();
    let now = performance.now();
    let elapsed = (now - startTime) / 1000;
    let t;

    function easeInOutQuad(val) {
      return val < .5 ? 2 * val * val : -1 + (4 - 2 * val) * val;
    }

    function animateScroll() {
      now = performance.now();
      elapsed = (now - startTime) / 1000;
      t = (elapsed / duration);

      element.scrollTop = start + change * easeInOutQuad(t);

      if ( t < 1 ) { window.requestAnimationFrame(animateScroll); } else {
        if (onDone) {
          onDone();
        }
      }
    }

    animateScroll();
  }


  showDetails(payment) {
    this.payment = payment;
    if (!this.isAnimating) {
      this.isAnimating = true;
      this.isDetailsShown = true;
      this.wrapperClasses = 'animate__animated zoomInJ';

      setTimeout(() => {
        this.document.body.classList.add('overflow-hidden');
        this.detailsClasses = 'is-shown';
      }, 250);

      setTimeout(() => {
        this.wrapperClasses = '';
        this.isAnimating = false;
      }, 300);
    }
  }

  onCloseModal(event: Event) {
    event.preventDefault();
    this.hideDetails();
    this.isSumsShown = true;
  }


  toggleSumsDropdown() {
    this.isSumsShown = !this.isSumsShown;
  }

  hideDetails() {
    if (!this.isAnimating) {
      this.isAnimating = true;
      this.detailsClasses = '';

      setTimeout(() => {
        this.document.body.classList.remove('overflow-hidden');
        this.wrapperClasses = 'animate__animated zoomOutJ';
      }, 250);

      setTimeout(() => {
        this.wrapperClasses = '';
        this.isDetailsShown = false;
        this.isAnimating = false;
      }, 300);
    }
  }
}
