import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import {
  ItemCardSettingsPackage,
  ItemFormsSettingsSale,
  MyItemSettingsProgress,
  MyItemSettingsPrice,
  PackageRest,
} from '@settings/models';
import {
  myPriceData,
  myProgressRefillData,
  myProgressPaymentData,
  mySaleData,
} from '@settings/data';
import { TranslateService } from '@ngx-translate/core';
import { of } from 'rxjs';
import { AccountsService } from '@accounts/services/accounts.service';
import { CURRENCY } from '@shared/currency/currency.model';
import { RegularAccount } from '@accounts/account.model';
import { FeatureFlagsService, FEATURE_FLAG } from '@feature-flags/index';
import { AnalyticsManagerAbstract } from '@analytics/services/manager';

@Component({
  selector: 'j-my-package',
  templateUrl: './my-package.component.html',
  styleUrls: ['./my-package.component.scss'],
})
export class MyPackageComponent implements OnInit {
  @Input() isAnotherCompany = false;
  @Output() openModal: EventEmitter<any> = new EventEmitter();
  public tengeAccount: number;
  public currentAccount: RegularAccount;
  currentIban: string;
  public isDisabledAccount;
  public payment: ItemCardSettingsPackage = new ItemCardSettingsPackage(
    'SETTINGS.PACKAGE.CALCULATE.PAYMENTS',
    '',
    ''
  );
  public refill: ItemCardSettingsPackage = new ItemCardSettingsPackage(
    'SETTINGS.PACKAGE.CALCULATE.DEPOSITS',
    '',
    ''
  );
  public sale: ItemCardSettingsPackage = new ItemCardSettingsPackage(
    'SETTINGS.PACKAGE.CALCULATE.SALES',
    '',
    ''
  );

  public myPaymentData: MyItemSettingsProgress = { ...myProgressPaymentData };
  public myPaymentExtraData: MyItemSettingsProgress = {...myProgressPaymentData};
  public myRefillData: MyItemSettingsProgress = { ...myProgressRefillData };
  public myRefillExtraData: MyItemSettingsProgress = {...myProgressRefillData};
  public myPriceData: MyItemSettingsPrice = myPriceData;
  public mySaleData: ItemFormsSettingsSale[] = mySaleData;

  constructor(
    private translateService: TranslateService,
    private accountsService: AccountsService,
    private featureFlagsService: FeatureFlagsService,
    private analytics: AnalyticsManagerAbstract,
    ) {}

  ngOnInit(): void {
    this.defaultView();
    this.retrieveAccounts();

    this.isDisabledAccount = this.featureFlagsService.isDisabled(FEATURE_FLAG.PACKAGES_CHANGE_ACCOUNT);
  }

  defaultView() {
    // check object all keys (fals?)
    if (!this.isAnotherCompany) {
      this.payment = new ItemCardSettingsPackage(
        'SETTINGS.PACKAGE.CALCULATE.PAYMENTS',
        'payments',
        ''
      );
      this.refill = new ItemCardSettingsPackage(
        'SETTINGS.PACKAGE.CALCULATE.DEPOSITS',
        'deposits',
        ''
      );
      this.sale = new ItemCardSettingsPackage(
        'SETTINGS.PACKAGE.CALCULATE.SALES',
        'sales',
        ''
      );
    }
  }

  @Input() set data(packageRest: PackageRest) {

    // progressBar  set myPaymentData
    this.myPaymentData.current =
      packageRest.restOfPayments.currentNumberOfPayments;
    this.myPaymentData.total = packageRest.restOfPayments.totalNumberOfPayments;
    this.myPaymentData.description = this.translateService.get(
      'SETTINGS.PACKAGE.CALCULATE.MY_PAYMENT_DESCRIPTION',
      {
        totalNumberOfPayments: packageRest.restOfPayments.totalNumberOfPayments,
        payment: packageRest.restOfPayments.costNextPayment,
      }
    );
    this.myPaymentData.color  = packageRest.restOfPayments.colorIndicator;

    if (packageRest?.extraRestOfPayments) {
      this.myPaymentExtraData.current = packageRest.extraRestOfPayments.currentNumberOfPayments;
      this.myPaymentExtraData.total = packageRest.extraRestOfPayments.totalNumberOfPayments;
      this.myPaymentExtraData.description = this.translateService.get('SETTINGS.PACKAGE.ADDITIONAL.NO_EXPIRATION_DATE');
      this.myPaymentExtraData.color  = packageRest.extraRestOfPayments.colorIndicator;

    }
    if (packageRest?.extraDepositAndWithdrawals) {
      this.myRefillExtraData.current = packageRest.extraDepositAndWithdrawals.currentDepositAndWithdrawals;
      this.myRefillExtraData.total = packageRest.extraDepositAndWithdrawals.totalDepositAndWithdrawals;
      this.myRefillExtraData.description = this.translateService.get('SETTINGS.PACKAGE.ADDITIONAL.NO_EXPIRATION_DATE');
      this.myRefillExtraData.color = packageRest.extraDepositAndWithdrawals.colorIndicator;
    }
    // progressBar component set myRefillData
    this.myRefillData.current =
      packageRest.depositAndWithdrawals.currentDepositAndWithdrawals;
    this.myRefillData.total =
      packageRest.depositAndWithdrawals.totalDepositAndWithdrawals;

    this.myRefillData.description = this.translateService.get(
      'SETTINGS.PACKAGE.CALCULATE.DEPOSITS_DESCRIPTION',
      {
        deposit:
          packageRest.depositAndWithdrawals.totalDepositAndWithdrawals / 1e6,
        depositingOverLimit:
        packageRest.depositAndWithdrawals.depositingOverLimit,
        withdrawalOverLimit:
        packageRest.depositAndWithdrawals.withdrawalOverLimit,
      }
    );
    this.myRefillData.color  = packageRest.depositAndWithdrawals.colorIndicator;
    // FormSettings component  set myRefillData
    this.mySaleData.find((v, i) => {
      if (v.key === 'discountInRubles') {
        this.mySaleData[i] = {
          ...this.mySaleData[i],
          checked: packageRest.discountOnTransferInRubles.hasDiscount,
          disabled: this.isAnotherCompany
        };
      }
      if (v.key === 'discountInForeignCurrency') {
        this.mySaleData[i] = {
          ...this.mySaleData[i],
          checked: packageRest.discountOnTransferInCurrency.hasDiscount,
          disabled: this.isAnotherCompany
        };
      }
    });
    this.currentIban = packageRest.iban;
    //  My package component set PriceData
    this.myPriceData.totalCost = packageRest.totalCost;
    this.myPriceData.nextWriteOff = packageRest.nextWriteOff;
  }

  packageChange(alert, event) {
    Object.keys(event).forEach((key, value) => {
      if(key === 'discountInRubles') {
        const switchName = event.discountInRubles
          ? 'on'
          : 'off'
        this.analytics.logEvent('JBundle_Switcher_Click', {version: 1, currency: 'RUB', switch: switchName})
      } else if(key === 'discountInForeignCurrency') {
        const switchName = event.discountInForeignCurrency
          ? 'on'
          : 'off'
        this.analytics.logEvent('JBundle_Switcher_Click', {version: 1, currency: 'currency', switch: switchName})
      }
    })

    if(event?.iban && this.currentIban === event?.iban) {
      return;
    }
    this.openModal.emit({alert, event});
  }

  private async retrieveAccounts() {
    try {
      const accounts = await this.accountsService.getAccounts();
      this.tengeAccount = accounts.reduce((total, account) => {
        if(account.balance.currency === CURRENCY.KZT) total += 1;
        return total
      }, 0);

      this.currentAccount = [...accounts].find(ac => ac.iban === this.currentIban);

      return accounts;
    } catch (error) {
      return [];
    }
  }

  additionalPackage(event) {
     const typeName = event === 'payment'
      ? 'payments'
      : 'cash'
      this.analytics.logEvent('JBundle_AddPack_Click', {version: 1, type: typeName});
  }
}
