import { AbstractControl, AsyncValidatorFn } from '@angular/forms';
import { CURRENCY } from '@core/models';
import { IBank } from '@payments/services';
import { Bank } from '@shared/models';
import { ibanPrefix } from '@utils/misc';

import { of } from 'rxjs';
import { map, take } from 'rxjs/operators';

import { DictionariesService } from './dictionaries.service';

const isBankRestricted = (bank: IBank | Bank) => {
  if (bank?.transferAllowed === false) {
    return { bankRestricted: true };
  }

  return null;
};

export class PaymentAsyncValidators {
  static internalIbanRestricted(
    dictionaries: DictionariesService
  ): AsyncValidatorFn {
    return (control: AbstractControl) => {
      const normalized = ibanPrefix(control.value, CURRENCY.KZT);

      if (!normalized) {
        return of(null);
      }

      return dictionaries.bankDictionary.items$.pipe(
        take(1),
        map((banks) => {
          const bank = banks.find(
            (item) => item.ibanBankCode === normalized.substring(4, 7)
          );

          return isBankRestricted(bank);
        })
      );
    };
  }

  static externalIbanRestricted(
    dictionaries: DictionariesService
  ): AsyncValidatorFn {
    return (control: AbstractControl) => {
      if (
        !control.value ||
        control.value.substring(0, 2) != 'KZ' ||
        control.value.length != 20
      ) {
        return of(null);
      }

      return dictionaries.bankDictionary.items$.pipe(
        take(1),
        map((banks) => {
          const bank = banks.find(
            (item) => item.ibanBankCode === control.value.substring(4, 7)
          );

          return isBankRestricted(bank);
        })
      );
    };
  }

  static bankRestrictedById(
    dictionaries: DictionariesService
  ): AsyncValidatorFn {
    return (control: AbstractControl) => {
      if (!control.value) {
        return of(null);
      }

      return dictionaries.bankDictionary.items$.pipe(
        take(1),
        map((banks) => {
          const bank = banks.find((item) => item.id === control.value);
          return isBankRestricted(bank);
        })
      );
    };
  }

  static bankRestricted(dictionaries: DictionariesService): AsyncValidatorFn {
    return (control: AbstractControl) => {
      if (!control.value) {
        return of(null);
      }

      return dictionaries.bankDictionary.items$.pipe(
        take(1),
        map((banks) => {
          const bank = banks.find((item) => item.id === control.value.id);
          return isBankRestricted(bank);
        })
      );
    };
  }
}
