import { Component, OnInit, Input } from '@angular/core';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

import { Transaction } from '../../models/transaction.model';

@Component({
  selector: 'j-transaction-sign-success',
  templateUrl: './transaction-sign-success.component.html',
  styleUrls: ['./transaction-sign-success.component.scss'],
})
export class TransactionSignSuccessComponent {
  @Input() transaction: Transaction;

  constructor(public modal: NgbActiveModal) {}

  dismissModal() {
    this.modal.dismiss();
  }

  closeModal() {
    this.modal.close();
  }
}
