import { ACCOUNT_TYPE } from '@accounts/account.model';
import { IMoney } from '@shared/currency/currency.model';
import { defaults } from 'lodash';

export enum CARD_CATEGORY {
  COMMERCIAL = 'COMMERCIAL',
  PRIVATE = 'PRIVATE',
}

export interface ICard {
  balance: IMoney;
  blockedAmount: IMoney;

  cardNumber: string;
  firstName: string;
  middleName?: string;
  fullName: string;
  iban: string;
  lastName: string;
  openDate: Date;

  cardId: string;
  status: CARD_STATUS;

  accountId: string;
  category: CARD_CATEGORY;

  id: string;
  alias: string;
  companyId: string;
  created: Date;
  hidden: boolean;
}

export class Card implements ICard {
  accountType = ACCOUNT_TYPE.CARD;
  balance: IMoney;
  blockedAmount: IMoney;
  cardNumber: string;
  firstName: string;
  middleName?: string;
  fullName: string;
  iban: string;
  lastName: string;
  openDate: Date;
  cardId: string;
  status: CARD_STATUS;

  accountId: string;
  category: CARD_CATEGORY;

  id: string;
  alias: string;
  companyId: string;
  created: Date;
  hidden: boolean;

  // Client Fields
  maskedNumber: string;
  formattedName: string;
  formattedFullName: string;

  constructor(card: ICard) {
    defaults(this, card);

    this.maskedNumber = this.getMaskedNumber();
    this.formattedName = this.getFormattedName();
    this.formattedFullName = this.getFormattedFullName();
  }

  get hasLockedState() {
    return this.status !== CARD_STATUS.ACTIVE;
  }

  private getMaskedNumber() {
    return ` ${this.cardNumber.substr(this.cardNumber?.length - 4)}`;
  }

  private getFormattedName() {
    if (!this.firstName && !this.lastName && this.fullName) {
      return this.fullName;
    } else if (this.firstName || this.lastName) {
      return `${this.capitalized(this.lastName)} ${this.capitalized(
        this.firstName
      )}`;
    }
    return '';
  }

  private getFormattedFullName() {
    return this.fullName;
    // .split(' ')
    // .map((word) => this.capitalized(word))
    // .join(' ');
  }

  private capitalized(word: string) {
    if (!word) {
      return '';
    }
    return `${word[0]?.toUpperCase()}${word.substr(1).toLowerCase()}`;
  }
}

export enum CARD_STATUS {
  ACTIVE = 'ACTIVE',
  CLOSED = 'CLOSED',
  FRAUD = 'FRAUD',
  EXPIRED = 'EXPIRED',
  LOST_LOCK = 'LOST_LOCK',
  THEFT_LOCK = 'THEFT_LOCK',
  TEMPORARILY_LOCK = 'TEMPORARILY_LOCK',
}
