import { Component, Input, OnInit } from '@angular/core';
import { CARD_STATUS } from '@cards/models';
import { CardsService } from '@cards/services';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CardStatusModalComponent } from '../card-status-modal/card-status-modal.component';

@Component({
  selector: 'j-card-status',
  templateUrl: './card-status.component.html',
  styleUrls: ['./card-status.component.scss'],
})
export class CardStatusComponent implements OnInit {
  @Input() cardId: string;
  @Input() isSmall: boolean;
  @Input() status: CARD_STATUS;
  CARD_STATUS = CARD_STATUS;

  constructor(
    private modalService: NgbModal,
    private cardsService: CardsService
  ) {}

  ngOnInit() {}

  onClick(event: Event) {
    event.preventDefault();
    if (!this.isSmall) {
      this.showModal();
    }
  }

  mapIcon(isLarge: boolean = false) {
    const prefix = `/assets/img/cards/status/`;

    let suffix = isLarge ? '-large.svg' : this.isSmall ? '-small.svg' : '.svg';

    switch (this.status) {
      case CARD_STATUS.EXPIRED:
        return `${prefix}expire${suffix}`;
      case CARD_STATUS.FRAUD:
      case CARD_STATUS.LOST_LOCK:
      case CARD_STATUS.THEFT_LOCK:
      case CARD_STATUS.TEMPORARILY_LOCK:
      default:
        return `${prefix}locked${suffix}`;
    }
  }

  mapTitle() {
    return this.cardsService.mapCardStatus(this.status);
  }

  mapDescription() {
    return `CARDS.STATUS.DESCRIPTION.${this.status}`;
  }

  private showModal() {
    const modalRef = this.modalService.open(CardStatusModalComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_sm',
      centered: true,
    });

    Object.assign(modalRef.componentInstance as CardStatusModalComponent, {
      title: this.mapTitle(),
      img: this.mapIcon(true),
      description: this.mapDescription(),
      cardId: this.cardId,
      canUnlock: this.status === CARD_STATUS.TEMPORARILY_LOCK,
    });

    return modalRef.result;
  }
}
