import {
  AfterViewInit,
  Component,
  Input,
  OnDestroy,
  OnInit,
} from '@angular/core';

import { Subscription } from 'rxjs';

import { MaskPipe } from 'ngx-mask';
import { TranslateService } from '@ngx-translate/core';
import { NotifierService } from 'angular-notifier';

import { CompanyService } from '@company/company.service';

import { RegularAccount, Deposit, AccountsService } from '@accounts/index';

import { Account } from '../../models/account';

import { Masks } from '@shared/masked/masked';
import { BankFromAccountPipe, FormatIbanPipe } from '@shared/pipes';
import { FilesService } from '@shared/services/files.service';
import {
  FeatureFlagsService,
  FEATURE_FLAG,
  IFeatureFlag,
} from '@feature-flags/index';


interface IRequisiteRow {
  label: string;
  value: string;
  valueToCopy?: string;
}

@Component({
  selector: 'j-requisites',
  templateUrl: './requisites.component.html',
  styleUrls: ['./requisites.component.scss'],
})
export class RequisitesComponent implements OnInit, OnDestroy {
  @Input() account: RegularAccount | Deposit | Account;
  isLoaded: boolean = false;
  knp: string = '';
  qrFile;
  errorText: string;

  requisites: IRequisiteRow[];
  list: IRequisiteRow[];
  oldList: IRequisiteRow[];

  textToCopy: string;

  isQrVisible = false;

  subscription = new Subscription();
  featureFlags: IFeatureFlag[];

  constructor(
    private companyService: CompanyService,
    private bankFromAccount: BankFromAccountPipe,
    private maskPipe: MaskPipe,
    private formatIban: FormatIbanPipe,
    private translateService: TranslateService,
    private notifierService: NotifierService,
    private accountsService: AccountsService,
    private filesService: FilesService,
    private featureFlagsService: FeatureFlagsService 
  ) {}

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  ngOnInit() {
    this.generateList();
    this.generateOldList();
    this.generateTextToCopy();
    this.isLoaded = true;
    this.initFeatureFlagsSubscription();
  }

  get hasAtfIban() {
    return (this.account as RegularAccount)?.hasAtfIban || false;
  }

  onAccountLoad(account: Account) {
    this.init(account);
  }

  onAccountFail() {
    this.isLoaded = true;
    this.errorText = this.appendTranslationPrefix(`FAILED`);
  }

  onCopy() {
    this.notifierService.notify(
      'default',
      this.translateService.instant(this.appendTranslationPrefix('COPIED'))
    );
  }

  async onDownload() {
    try {
      const pdf = await this.accountsService.getPdf(
        this.companyService.currentId,
        this.account.iban
      );
      this.filesService.downloadFile(pdf, `${this.account.iban}.pdf`);
    } catch (error) {
      console.error(error);
    }
  }

  private generateList() {
    this.list = [
      {
        label: this.appendTranslationPrefix('COMPANY'),
        value: this.companyService.currentCompany.name,
      },
      {
        label: this.appendTranslationPrefix('IIN'),
        valueToCopy: this.companyService.currentCompany.bin,
        value: this.maskPipe.transform(
          this.companyService.currentCompany.bin,
          Masks.iin.mask
        ),
      },
      {
        label: this.appendTranslationPrefix('KBE'),
        value: this.companyService.currentCompany.kod,
      },
      {
        label: this.appendTranslationPrefix('IBAN'),
        valueToCopy: this.account?.iban,
        value: this.formatIban.transform(this.account?.iban),
      },
      {
        label: this.appendTranslationPrefix('BANK'),
        value: this.bankFromAccount.transform(this.account?.iban, 'name'),
      },
      {
        label: this.appendTranslationPrefix('BIC'),
        value: this.bankFromAccount.transform(this.account?.iban, 'bic'),
      },
    ];
  }

  private generateOldList() {
    const oldIban = (this.account as RegularAccount)?.alterCode;

    this.oldList = [
      {
        label: this.appendTranslationPrefix('IBAN'),
        valueToCopy: oldIban,
        value: this.formatIban.transform(oldIban),
      },
      {
        label: this.appendTranslationPrefix('BANK'),
        value: this.bankFromAccount.transform(oldIban, 'name'),
      },
      {
        label: this.appendTranslationPrefix('BIC'),
        value: this.bankFromAccount.transform(oldIban, 'bic'),
      },
    ];
  }

  private generateTextToCopy() {
    this.textToCopy = this.list
      .map((item) => {
        if (!(item.value || item.valueToCopy)) {
          return;
        }
        return `${this.translateService.instant(item.label)}: ${
          item.valueToCopy || item.value
        }`;
      })
      .join('\n');
  }

  private appendTranslationPrefix(key: string) {
    return `ACCOUNTS.REQUISITES.${key}`;
  }

  async init(account: Account) {
    this.account = account;
    this.generateRequisites();
    this.generateTextToCopy();
    this.isLoaded = true;
  }

  async loadQr() {
    const kbe = this.companyService.currentCompany.kod;
    this.qrFile = await this.accountsService.getQrPrint(
      this.account.id,
      this.account.iban,
      kbe,
      this.knp
    );
  }

  onQrService(event: Event) {
    event.preventDefault();
    this.knp = '859';
    this.loadQr();
    this.filesService.downloadFile(
      this.qrFile,
      `${this.companyService.currentCompany.shortName} 
      ${this.translateService.instant(
        this.appendTranslationPrefix('QR.PRINT_SERVICE')
      )}.jpg`
    );
  }

  onQrProduct(event: Event) {
    event.preventDefault();
    this.knp = '710';
    this.loadQr();
    this.filesService.downloadFile(
      this.qrFile,
      `${this.companyService.currentCompany.shortName} 
      ${this.translateService.instant(
        this.appendTranslationPrefix('QR.PRINT_PRODUCT')
      )}`
    );
  }

  initFeatureFlagsSubscription() {
    this.featureFlagsService.featureFlags$.subscribe(() => {
      this.isQrVisible =
        !this.featureFlagsService.isDisabled(FEATURE_FLAG.ACCOUNT_QR) &&
        this.account.accountType === 'ACCOUNT' &&
        this.account.balance?.currency &&
        this.account.balance?.currency === 'KZT';
    });
  }

  private generateRequisites() {
    this.requisites = [
      {
        label: this.appendTranslationPrefix('OWNER'),
        value: this.account.iban,
      },
      {
        label: this.appendTranslationPrefix('KBE'),
        value: this.companyService.currentCompany.kod,
      },
      {
        label: this.appendTranslationPrefix('IBAN'),
        value: this.formatIban.transform(this.account.iban),
        valueToCopy: this.account.iban,
      },
      {
        label: this.appendTranslationPrefix('BANK'),
        value: this.bankFromAccount.transform(this.account.iban, 'name'),
      },
      {
        label: this.appendTranslationPrefix('BIC'),
        value: this.bankFromAccount.transform(this.account.iban, 'bic'),
      },
    ];
  }
}
