import { Injectable } from '@angular/core';
import { HttpParams } from '@angular/common/http';

import { ApiService } from '@api/index';

import { CompanyService } from '@company/company.service';

import { dateToNgbStruct, ngbDateToUTCStartDay } from '@utils/index';

import { CURRENCY } from '@shared/currency/currency.model';
import { MaskedService } from '@shared/masked/masked.service';
import { ICodeWord, ICountry, IFxContract, PAYMENT_TYPE } from '@shared/models';
import { DictionariesService } from '@shared/services/dictionaries.service';

import { CurrencyPaymentFormModel } from './form-model';

export enum COUNTRY {
  RU = 'RU',
  US = 'US',
  CN = 'CN',
  AU = 'AU',
  CA = 'CA',
  AE = 'AE',
}

export interface IBank {
  bic: string;
  bin: string;
  cityName: string;
  countryId: string;
  ibanBankCode: string;
  id: string;
  name: string;
  salaryTransitIban: string;
  transferAllowed?: boolean;
}

export interface IRusDictionary {
  code: string;
  description: string;
}

@Injectable({
  providedIn: 'root',
})
export class CurrencyPaymentService {
  cashedCodeWordsCountry;
  cashedCodeWordsList;
  cashedCodeWordsRetrieving;

  cashedBankQuery;
  cashedBankCountryCode;
  cashedBankList;

  constructor(
    private api: ApiService,
    private companyService: CompanyService,
    private maskedService: MaskedService,
    protected dictionaries: DictionariesService
  ) {}

  getCountries() {
    const url = `countries`;
    return this.api.get<ICountry[]>(url);
  }

  getContractById(id: string) {
    const url = `companies/${this.companyService.currentId}/fx-contracts/${id}`;
    return this.api.get<IFxContract>(url);
  }

  async getContracts() {
    const url = `companies/${this.companyService.currentId}/fx-contracts`;
    return this.api.get<IFxContract[]>(url);
  }

  async getCodeWords(countryCode: COUNTRY) {
    if (
      countryCode &&
      this.cashedCodeWordsList &&
      countryCode === this.cashedCodeWordsCountry
    ) {
      return this.cashedCodeWordsList;
    }

    const url = `dictionaries/codewords`;

    let params = new HttpParams();
    params = params.append('countryCode', countryCode);

    const retrieved = await this.api.get<ICodeWord[]>(url, null, params);

    this.cashedCodeWordsCountry = countryCode;
    this.cashedCodeWordsList = retrieved;

    return this.cashedCodeWordsList;
  }

  async getBanks(query: string = null, countryCode: string = null) {
    if (
      query === this.cashedBankQuery &&
      countryCode === this.cashedBankCountryCode
    ) {
      return this.cashedBankList;
    }

    this.cashedBankQuery = query;
    this.cashedBankCountryCode = countryCode;

    const url = `dictionaries/banks`;

    let params = new HttpParams();
    if (query) {
      params = params.append('search', query);
    }

    if (countryCode) {
      params = params.append('countryCode', countryCode);
    }

    const list = await this.api.get<IBank[]>(url, null, params);

    this.cashedBankList = list;

    return list;
  }

  getKbkList() {
    const url = `dictionaries/kbk`;
    return this.api.get<IRusDictionary[]>(url);
  }

  getStatuses() {
    const url = `dictionaries/payer-status`;
    return this.api.get<IRusDictionary[]>(url);
  }

  mapForm(formValue: CurrencyPaymentFormModel) {
    let details: any = {
      commission: {
        amount: formValue.comission,
        currency: formValue.comissionCurrency,
      },
      description: formValue.purpose,
      documentId: formValue.number,
      foreignPaymentDetails: {
        commissionWithdrawalIban: formValue.commissionAccount?.iban,
        intermediaryBankBic: formValue.intermediaryBank?.bic,
        intermediaryBankName: formValue.intermediaryBank?.name,
        type: formValue.payComission ? 'OUR' : 'SHA',
      },
      valueDate:
        (formValue.date &&
          ngbDateToUTCStartDay(formValue.date).toISOString()) ||
        (formValue.date as null),
      payerIban: formValue.senderAccount?.iban,
      paymentAmount: {
        amount: this.maskedService.getNumberedAmount(formValue.amount),
        currency: formValue.senderAccount?.balance?.currency,
      },
    };

    if (formValue.knp) {
      let knpValue = '';
      if (formValue.knpObject) {
        knpValue = formValue.knpObject.code;
      } else {
        knpValue = formValue.knp;
      }
      const foundKnp = this.dictionaries.KNPDictionary.getItemByKey(
        knpValue,
        'code'
      );
      details.knp = {
        code: foundKnp.code,
        name: foundKnp.name,
      };
    }

    if (formValue.kbk) {
      details.kbk = {
        code: formValue.kbk.code,
        name: formValue.kbk.description,
      };
    }

    if (formValue.withinContract) {
      details.foreignPaymentDetails.fxContractId = formValue.contractId;
      details.foreignPaymentDetails.contract = formValue.contract;
      details.foreignPaymentDetails.fxContractRecipient =
        formValue.fxContractRecipient;
      details.foreignPaymentDetails.fxContractRecipientId =
        formValue.fxContractRecipientId;
    }

    if (formValue.codeWord) {
      details.foreignPaymentDetails.codeWord = formValue.codeWord;
    }

    if (formValue.isRubNonRussia) {
      details.foreignPaymentDetails.intermediaryBankBic =
        formValue.rubNonRussiaCorrespondentBank.bic;
      details.foreignPaymentDetails.intermediaryBankIban =
        formValue.rubNonRussiaCorrespondentIban;
    }

    let bank = formValue.bank;

    if (formValue.isRubNonRussia) {
      bank = formValue.rubNonRussiaBank;
    }

    const countryCode =
      formValue.countryCode ||
      formValue.country?.code ||
      formValue.fxContractRecipient?.country?.code ||
      formValue.fxContractRecipient?.countryCode;

    let mapped: any = {
      category: PAYMENT_TYPE.FOREIGN,
      companyId: this.companyService.currentId,
      details: details,
      emailOnSuccess: formValue.emailOnSuccess, //    ?
      paymentRecipient: {
        cityName: formValue.cityName,
        countryCode: countryCode,
        kbe: {
          code: formValue.kbeCode,
        },
        kpp: formValue.kpp,
        name: formValue.name,
        recipientAccount: {
          bankName: bank?.name,
          bic: bank?.bic,
          iban: formValue.bankAccount
            ? this.removeSpaces(formValue.bankAccount)
            : formValue.bankAccount,
        },
      },
      type: PAYMENT_TYPE.FOREIGN,
    };

    if (countryCode == COUNTRY.AU) {
      if (formValue.senderAccount.balance.currency === CURRENCY.AUD) {
        mapped.paymentRecipient.address = {
          index: formValue.audIndex,
          state: formValue.audState,
          street: formValue.audStreet,
          buildingNumber: formValue.audBuildingNumber,
        };
      } else {
        mapped.paymentRecipient.address = {
          text: formValue.audAddress,
        };
      }
    }
    if (countryCode == COUNTRY.CA) {
      mapped.paymentRecipient.address = {
        text: formValue.canAddress,
      };
    }

    if (formValue.kio) {
      mapped.paymentRecipient.iinOrBin = formValue.kio;
    }

    if (formValue.kvo) {
      details.foreignPaymentDetails.currencyTransactionCode = formValue.kvo;
    }

    if (formValue.isRussianBudget) {
      details.foreignPaymentDetails.oktmo = formValue.oktmo;
      details.foreignPaymentDetails.payerInn = formValue.payerInn;
      details.foreignPaymentDetails.payerKpp = formValue.payerKpp;
      details.foreignPaymentDetails.payerStatus = formValue.payerStatus;
      details.foreignPaymentDetails.paymentBasis = formValue.paymentBasis;
      details.foreignPaymentDetails.period = formValue.budgetPeriod;
      details.foreignPaymentDetails.uip = formValue.uip;
      details.foreignPaymentDetails.budgetDocNumber = formValue.budgetDocNumber;
    }

    return mapped;
  }

  mapPayment(payment) {
    return {
      type: PAYMENT_TYPE.FOREIGN,
      senderAccount: null,

      commissionAccount: null,
      comission: payment.details.comission,
      payComission:
        payment.details.foreignPaymentDetails.type == 'OUR' ? true : false,

      countryCode: payment.paymentRecipient?.countryCode || '',
      cityName: payment.paymentRecipient?.cityName || '',

      isNewPayment: payment.isNewPayment || false,
      contract: payment.details.foreignPaymentDetails.fxContract,
      contractId: payment.details.foreignPaymentDetails.fxContractId,
      fxContractRecipient:
        payment.details.foreignPaymentDetails.fxContractRecipient,
      fxContractRecipientId:
        payment.details.foreignPaymentDetails.fxContractRecipientId,
      withinContract: payment.details.foreignPaymentDetails.fxContractId
        ? true
        : false,

      name: payment.paymentRecipient?.name || '',

      bankAccount: payment.paymentRecipient?.recipientAccount.iban || '',

      kbeCode: payment.paymentRecipient?.kbe?.code || '',

      number: payment.details.documentId,

      date:
        payment.details.valueDate &&
        dateToNgbStruct(new Date(payment.details.valueDate)),

      knp: payment.details.knp?.code,

      purpose: payment.details.description,

      amount: payment.details.paymentAmount?.amount,

      bankLabel: '',
      bank: null,

      hasIntermediaryBank: payment.details.foreignPaymentDetails
        .intermediaryBankBic
        ? true
        : false,

      intermediaryBankLabel: '',
      intermediaryBank: null,

      notify: payment.emailOnSuccess ? true : false,

      emailOnSuccess: payment.emailOnSuccess,

      kpp: payment.paymentRecipient?.kpp || '',
      kvo: payment.details.foreignPaymentDetails.currencyTransactionCode || '',

      kio: payment.paymentRecipient?.iinOrBin || '',

      isRussianBudget: null,

      isRussianTaxPayer:
        payment.details.foreignPaymentDetails.payerInn ||
        payment.details.foreignPaymentDetails.payerKpp
          ? true
          : false,
      payerInn: payment.details.foreignPaymentDetails.payerInn || '',
      payerKpp: payment.details.foreignPaymentDetails.payerKpp || '',

      kbk: payment.details.kbk
        ? {
            code: payment.details.kbk.code,
            description: payment.details.kbk.name,
          }
        : '',
      oktmo: payment.details.foreignPaymentDetails.oktmo || '',

      payerStatus: payment.details.foreignPaymentDetails.payerStatus || '',

      hasRussianBudgetExtras:
        payment.details.foreignPaymentDetails.paymentBasis ||
        payment.details.foreignPaymentDetails.budgetDocNumber ||
        payment.details.foreignPaymentDetails.period ||
        payment.details.foreignPaymentDetails.uip
          ? true
          : false,
      paymentBasis: payment.details.foreignPaymentDetails.paymentBasis || '',
      budgetDocNumber:
        payment.details.foreignPaymentDetails.budgetDocNumber || '',
      budgetPeriod: payment.details.foreignPaymentDetails.period || '',
      uip: payment.details.foreignPaymentDetails.uip || '',

      codeWord: payment.details.foreignPaymentDetails.codeWord,

      audIndex: payment.paymentRecipient?.address?.index || '',
      audState: payment.paymentRecipient?.address?.state || '',
      audStreet: payment.paymentRecipient?.address?.street || '',
      audBuildingNumber:
        payment.paymentRecipient?.address?.buildingNumber || '',

      audAddress: payment.paymentRecipient?.address?.text || '',
      canAddress: payment.paymentRecipient?.address?.text || '',
    };
  }

  private removeSpaces(value: string) {
    return value.replace(/ /g, '');
  }
}
