import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { CurrencyService } from '@shared/currency/currency.service';
import { IMoney } from '@shared/currency/currency.model';

import { IStatement } from '../../models/statement.model';

@Component({
  selector: 'j-statement-balances',
  templateUrl: './statement-balances.component.html',
  styleUrls: ['./statement-balances.component.scss'],
})
export class StatementBalancesComponent implements OnInit {
  @Input() statement: IStatement;
  @Input() selectedCount = 0;
  @Input() showCount = false;
  @Output() exportSelected = new EventEmitter();

  balanceList;

  constructor(private currency: CurrencyService) {}

  ngOnInit(): void {
    this.balanceList = [
      {
        label: 'STATEMENT.BALANCES.INCOME',
        textClass: 'text-success',
        showSign: true,
        isCredit: true,
        isDebit: false,
        property: 'creditTotalSum',
      },
      {
        label: 'STATEMENT.BALANCES.OUTCOME',
        textClass: 'text-danger',
        showSign: true,
        isCredit: false,
        isDebit: true,
        property: 'debitTotalSum',
      },
      {
        label: 'STATEMENT.BALANCES.BALANCE_IN',
        textClass: '',
        showSign: false,
        property: 'balanceIn',
      },
      {
        label: 'STATEMENT.BALANCES.BALANCE_OUT',
        textClass: '',
        showSign: false,
        property: 'balanceOut',
      },
    ];
  }

  public formatBalance(balance: IMoney, showSign: boolean) {
    return this.currency.formatBalance(balance, showSign);
  }

  public getMoney(property: string) {
    return this.statement && this.statement[property];
  }

  exportSelectedTransactions(event: Event = null) {
    if (event) {
      event.preventDefault();
    }
    this.exportSelected.emit();
  }
}
