import { Component, OnDestroy, Inject } from '@angular/core';
import { ISettingsInfo, SettingsService } from '@settings/settings.service';
import { CompanyService } from '@company/company.service';
import { combineLatest, Subscription } from 'rxjs';
import { DOCUMENT } from '@angular/common';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'j-settings-main',
  templateUrl: './settings-main.component.html',
  styleUrls: ['./settings-main.component.scss'],
})
export class SettingsMainComponent implements OnDestroy {
  mainInfo: ISettingsInfo;
  contactsInfo: ISettingsInfo;
  privateInfo: ISettingsInfo;
  userLastSessionInfo: ISettingsInfo;
  subscription = new Subscription();

  isAnimating: boolean = false;
  wrapperClasses = '';
  allRequisitesClasses = '';
  isAllRequisitesShown: boolean = false;

  constructor(
    private settingsService: SettingsService,
    private translateService: TranslateService,
    private companyService: CompanyService,
    @Inject(DOCUMENT) private document: Document
  ) {
    this.loadData();

    const companySubscription = combineLatest([
      this.translateService.onLangChange,
      this.companyService.refresh$,
    ]).subscribe(() => {
      this.loadData();
    });

    this.subscription.add(companySubscription);
  }

  ngOnDestroy(): void {
    this.document.body.classList.remove('overflow-hidden');
    this.subscription.unsubscribe();
  }

  private loadData() {
    this.retrieveMainInfo();
    this.retrieveContactsInfo();
    this.retrievePrivateInfo();
    this.retrieveUserLastSessionInfo();
  }

  private retrieveMainInfo() {
    this.mainInfo = this.settingsService.generateMainInfo();
  }

  private retrieveContactsInfo() {
    this.contactsInfo = this.settingsService.generateContactsInfo();
  }

  private retrievePrivateInfo() {
    this.privateInfo = this.settingsService.generatePrivateInfo();
  }

  private async retrieveUserLastSessionInfo() {
    this.userLastSessionInfo =
      await this.settingsService.generateUserLastSessionInfo();
  }

  showSelector() {
    if (!this.isAnimating) {
      this.isAnimating = true;
      this.isAllRequisitesShown = true;
      this.wrapperClasses = 'animate__animated zoomInJ';

      setTimeout(() => {
        this.document.body.classList.add('overflow-hidden');
        this.allRequisitesClasses = 'is-shown';
      }, 250);

      setTimeout(() => {
        this.wrapperClasses = '';
        this.isAnimating = false;
      }, 300);
    }
  }

  hideSelector() {
    if (!this.isAnimating) {
      this.isAnimating = true;
      this.allRequisitesClasses = '';

      setTimeout(() => {
        this.document.body.classList.remove('overflow-hidden');
        this.wrapperClasses = 'animate__animated zoomOutJ';
      }, 250);

      setTimeout(() => {
        this.wrapperClasses = '';
        this.isAllRequisitesShown = false;
        this.isAnimating = false;
      }, 300);
    }
  }
}
