import { ChangeDetectionStrategy, Component } from '@angular/core';

import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { AmlWarningModalComponent } from '../aml-warning-modal/aml-warning-modal.component';

@Component({
  selector: 'j-aml-alert',
  templateUrl: './aml-alert.component.html',
  styleUrls: ['./aml-alert.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AmlAlertComponent {
  constructor(private modalService: NgbModal) {}

  showMainModal() {
    this.modalService.open(AmlWarningModalComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_sm',
      backdrop: 'static',
      centered: true,
    });
  }
}
