import { Component, Input, OnInit } from '@angular/core';
import { MyItemSettingsProgress } from '@settings/models';

@Component({
  selector: 'j-package-progressbar',
  templateUrl: './package-progressbar.component.html',
  styleUrls: ['./package-progressbar.component.scss']
})
export class PackageProgressbarComponent implements OnInit {

  @Input() item: MyItemSettingsProgress;
  @Input() packageTitle: string;

  style = {
    width: '0%'
  };

  constructor() { }

  ngOnInit(): void {
    this.calculateRemaining(this.item);
  }

  private calculateRemaining(item): void {
    const value: number = Math.round((item?.current / item?.total) * 100);
    this.style.width = `${value || 1}%`;
  }
}
