import { AccountsService } from '@accounts/index';
import { Deposit, RegularAccount } from '@accounts/account.model';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Card } from '@cards/models';
import { CompanyService } from '@company/company.service';
import { ISettingsInfo } from '@settings/settings.service';
import { FilesService } from '@shared/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NotifierService } from 'angular-notifier';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'j-all-requisites',
  templateUrl: './all-requisites.component.html',
  styleUrls: ['./all-requisites.component.scss'],
})
export class AllRequisitesComponent implements OnInit {
  @Output() closed = new EventEmitter();

  accountsListInfo: ISettingsInfo;
  accounts: RegularAccount[] = [];
  isAllSelected = false;
  isLoading = true;
  account: RegularAccount | Card | Deposit;

  constructor(
    private accountsService: AccountsService,
    private filesService: FilesService,
    private companyService: CompanyService,
    private spinner: NgxSpinnerService,
    private notifier: NotifierService,
    private translateService: TranslateService
  ) {}

  async ngOnInit() {
    this.isLoading = true;
    this.spinner.show();
    this.accounts = await this.accountsService.getAccounts();
    this.accounts.forEach((el) => {
      el.isSelected = true;
    });
    this.isAllSelected = true;
    this.spinner.hide();
    this.isLoading = false;
  }

  selectAll() {
    this.isAllSelected = !this.isAllSelected;
    this.accounts = this.accounts.map((el) => {
      el.isSelected = this.isAllSelected;
      return el;
    });
  }

  onSelect() {
    this.isAllSelected = this.accounts.every((el) => el.isSelected);
  }

  onDownload() {
    const ibans = this.accounts
      .filter((el) => el.isSelected)
      .map((el) => el.iban);
    if (ibans.length > 0) {
      this.download(ibans);
    } else {
      this.notifier.show({
        type: 'error',
        message: this.translateService.instant('SETTINGS.REQUISITES.ERROR'),
      });
    }
  }

  async download(ibans) {
    try {
      const pdf = await this.accountsService.getPdfWithMultipleIbans(
        this.companyService.currentId,
        ibans
      );
      this.filesService.downloadFile(
        pdf,
        `${this.companyService.currentCompany.bin}.pdf`
      );
    } catch (error) {
      console.error(error);
    }
  }

  onClose() {
    this.closed.emit();
  }
}
