import { Component, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { IFxContractRecipient } from '@shared/models';

@Component({
  selector: 'j-foreign-contracts-recipients-dictionary',
  templateUrl: './foreign-contracts-recipients-dictionary.component.html',
  styleUrls: ['./foreign-contracts-recipients-dictionary.component.scss'],
})
export class ForeignContractsRecipientsDictionaryComponent implements OnInit {
  recipients: IFxContractRecipient[];
  filteredRecipients: IFxContractRecipient[];
  searchTerm: string = '';

  constructor(public modal: NgbActiveModal) {}

  ngOnInit() {
    this.filteredRecipients = [...this.recipients];
  }

  async onQueryChange(query: string) {
    this.searchTerm = query;

    if (!query) {
      this.filteredRecipients = [...this.recipients];
    }

    this.filteredRecipients = this.recipients.filter((recipient) => {
      return (
        this.includesQuery(recipient.name, query) ||
        this.includesQuery(recipient.bankBic, query)
      );
    });
  }

  onRecipientSelect(selectedRecipient: IFxContractRecipient) {
    this.modal.close(selectedRecipient);
  }

  onClose() {
    this.modal.dismiss();
  }

  private includesQuery(src: string, query: string) {
    return src?.toLowerCase().includes(query?.toLowerCase());
  }
}
