import { Component, ViewChild } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { ERRORS } from '@api/index';

import { UserService } from '@shared/services/user.service';
import {
  INewPasswordResult,
  NewPasswordComponent,
} from '@shared/components/new-password/new-password.component';

import { ChangePasswordSuccessComponent } from '../../modals/change-password-success/change-password-success.component';

@Component({
  selector: 'j-settings-password',
  templateUrl: './settings-password.component.html',
  styleUrls: ['./settings-password.component.scss'],
})
export class SettingsPasswordComponent {
  isLoading: boolean = false;
  errorText: string;

  @ViewChild('newPassword') newPassword: NewPasswordComponent;

  constructor(
    private userService: UserService,
    private modalService: NgbModal
  ) {}

  async onSubmit(newPasswordResult: INewPasswordResult) {
    this.isLoading = true;
    this.errorText = null;

    try {
      await this.userService.changePassword(
        newPasswordResult.oldPassword,
        newPasswordResult.newPassword
      );

      this.newPassword.clearForm();
      this.showSuccessModal();
    } catch (error) {
      switch (error.code) {
        case ERRORS.INVALID_PASSWORD:
          this.errorText = 'SETTINGS.PASSWORD.OLD_ERROR';
          break;

        case ERRORS.IDENTICAL_PASSWORDS:
          this.errorText = 'SETTINGS.PASSWORD.IDENTICAL';
          break;

        case ERRORS.USED_PASSWORD:
          this.errorText = 'SETTINGS.PASSWORD.USED';
          break;

        default:
          this.errorText = error.description;
          break;
      }
    } finally {
      this.isLoading = false;
    }
  }

  private async showSuccessModal() {
    const modal = this.modalService.open(ChangePasswordSuccessComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_sm',
      backdrop: 'static',
      centered: true,
    });
    try {
      await modal.result;
    } catch (error) {}
  }
}
