import { Component, ElementRef, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'j-package-not-connected',
  templateUrl: './package-not-connected.component.html',
  styleUrls: ['./package-not-connected.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class PackageNotConnectedComponent implements OnInit {
  @Input() isCollapsed = false;
  @ViewChild('collapse') collapse: ElementRef<HTMLElement>;

  constructor(private router: Router) { }

  ngOnInit(): void {
  }

  connectPackage() {
    this.router.navigate(['/settings/package']);
  }

  onCollapse() {
    this.isCollapsed = !this.isCollapsed;
  }

}
