import {
  Component,
  Input,
  OnDestroy,
  OnInit,
  Output,
  EventEmitter,
  Inject,
  ViewChild,
  ElementRef,
  TemplateRef,
  ContentChild,
} from '@angular/core';
import { DOCUMENT } from '@angular/common';

import { of, Subscription } from 'rxjs';
import { catchError, filter, take } from 'rxjs/operators';

import { generateUniqueId } from '@utils/index';

import { DictionariesService } from '@shared/services/dictionaries.service';
import { PAYMENT_STATUS, PAYMENT_TYPE } from '@shared/models';

import { CompanyService } from '@company/company.service';

import { STATEMENT_MODE } from '@statement/models/statement.model';
import {
  Transaction,
  TRANSACTION_CATEGORY,
} from '@statement/models/transaction.model';
import { StatementService } from '@statement/statement.service';

import { FilesService } from '@shared/services/files.service';
import { TranslateService } from '@ngx-translate/core';
import { MaskIbanPipe } from '@shared/pipes';

@Component({
  selector: 'j-transaction',
  templateUrl: './transaction.component.html',
  styleUrls: ['./transaction.component.scss'],
})
export class TransactionComponent implements OnDestroy, OnInit {
  id = generateUniqueId();

  TRANSACTION_CATEGORY = TRANSACTION_CATEGORY;

  @Input() mode: STATEMENT_MODE;
  @Input() depositName: string;

  @Input() transaction: Transaction;
  @Input() hasExtraPadding: boolean = false;
  @Input() hasLargeTransactions: boolean = false;

  @Input() isSelectable: boolean = false;

  @Output() deleted = new EventEmitter();
  @Output() selected = new EventEmitter();
  @Output() signed = new EventEmitter();
  @Output() sentForSign = new EventEmitter();
  @Output() exported = new EventEmitter();
  @Output() downloaded = new EventEmitter();
  @Output() revoked = new EventEmitter();

  signers = [];
  authorizedPersons;

  readonly PAYMENT_STATUS = PAYMENT_STATUS;

  isDetailsShown: boolean = false;
  isAnimating: boolean = false;
  isEditable: boolean = false;
  isSignable: boolean = false;
  isSignableByOthers: boolean = false;

  wrapperClasses = '';
  detailsClasses = '';

  subscription = new Subscription();

  paymentStatus: {
    label: string;
    classList: string;
  };

  @ViewChild('title') titleRef: ElementRef;
  @ViewChild('description') descriptionRef: ElementRef;

  @ContentChild('signersTooltipTemplate', { read: TemplateRef })
  signersTooltipTemplate: TemplateRef<any>;

  statusHistory: {
    label: string;
    iconClassList: string;
    labelClassList: string;
    time?: Date;
  }[];

  actions;

  title: string;
  description: string;

  constructor(
    private dictionariesService: DictionariesService,
    private statementService: StatementService,
    private companyService: CompanyService,
    private filesService: FilesService,
    private maskIbanPipe: MaskIbanPipe,
    @Inject(DOCUMENT) private document: Document
  ) {}

  ngOnDestroy() {
    this.document.body.classList.remove('overflow-hidden');
    this.subscription.unsubscribe();
  }

  ngOnInit() {
    this.paymentStatus = this.statementService.generatePaymentStatusLabel(
      this.transaction
    );

    const knpSubscription = this.dictionariesService.KNPDictionary.items$
      .pipe(
        filter((arr) => arr.length > 0),
        take(1)
      )
      .subscribe((list) => {
        this.transaction.knp = list.find((knp) => {
          return knp?.code === this.transaction?.knp?.code;
        });
      });

    const authorizedPersonsSubscription = this.companyService
      .getAuthorizedPersons()
      .pipe(
        catchError((err, caught) => {
          return of([]);
        })
      )
      .subscribe((authorizedPersons) => {
        this.authorizedPersons = authorizedPersons;
        this.generateSignersList();
      });

    this.subscription.add(authorizedPersonsSubscription);
    this.subscription.add(knpSubscription);

    // this.loadStatusHistory();
    this.mapPaymentActions();
    this.mapTransactionContent();
    // this.loadDetails();
  }

  onClick() {
    this.showDetails();
  }

  onSelect() {
    this.selected.emit(this.transaction.isSelected);
  }

  repeatPayment(
    event: Event = null,
    transaction: Transaction = this.transaction
  ) {
    if (event) {
      event.preventDefault();
      event.stopPropagation();
    }

    this.statementService.repeatPayment(transaction);
  }

  editPayment(
    event: Event = null,
    transaction: Transaction = this.transaction
  ) {
    if (event) {
      event.preventDefault();
      event.stopPropagation();
    }

    this.statementService.editPayment(transaction);
  }

  async exportPayment(
    event: Event = null,
    transaction: Transaction = this.transaction
  ) {
    if (event) {
      event.preventDefault();
      event.stopPropagation();
    }

    this.exported.emit(transaction);
    const exportedTransaction = await this.statementService.exportTransaction(
      transaction
    );
    let filename = `${transaction.documentNumber}.pdf`;
    this.filesService.downloadFile(exportedTransaction, filename);
  }

  async downloadIncomingPayment(
    event: Event = null,
    transaction: Transaction = this.transaction
  ) {
    if (event) {
      event.preventDefault();
      event.stopPropagation();
    }
    this.downloaded.emit(transaction);
    const downloadIncomingPayment =
      await this.statementService.exportIncomingPayment(transaction);
    const filename = `${transaction.documentNumber}.pdf`;
    this.filesService.downloadFile(downloadIncomingPayment, filename);
  }

  deletePayment(
    event: Event = null,
    transaction: Transaction = this.transaction
  ) {
    event?.preventDefault();
    event?.stopPropagation();

    this.hideDetails();
    this.deleted.emit(transaction);
  }

  signPayment(
    event: Event = null,
    transaction: Transaction = this.transaction
  ) {
    event?.preventDefault();
    event?.stopPropagation();

    this.signed.emit(transaction);
    this.hideDetails();
  }

  sendForSign(
    event: Event = null,
    transaction: Transaction = this.transaction
  ) {
    event?.preventDefault();
    event?.stopPropagation();

    this.sentForSign.emit(transaction);
    this.hideDetails();
  }

  revokePayment(
    event: Event = null,
    transaction: Transaction = this.transaction
  ) {
    event?.preventDefault();
    this.revoked.emit(transaction);
    this.hideDetails();
  }

  // async loadDetails() {
  //   const details = await this.statementService.getTransactionDetails(
  //     this.transaction
  //   );
  // }

  showDetails() {
    if (!this.isAnimating) {
      this.isAnimating = true;
      this.isDetailsShown = true;
      this.wrapperClasses = 'animate__animated zoomInJ';

      setTimeout(() => {
        this.document.body.classList.add('overflow-hidden');
        this.detailsClasses = 'is-shown';
      }, 250);

      setTimeout(() => {
        this.wrapperClasses = '';
        this.isAnimating = false;
      }, 300);
    }
  }

  hideDetails() {
    if (!this.isAnimating) {
      this.isAnimating = true;
      this.detailsClasses = '';

      this.wrapperClasses = 'animate__animated zoomOutJ';

      setTimeout(() => {
        this.document.body.classList.remove('overflow-hidden');
      }, 250);

      setTimeout(() => {
        this.wrapperClasses = '';
        this.isDetailsShown = false;
        this.isAnimating = false;
      }, 300);
    }
  }

  private generateSignersList() {
    this.signers = this.companyService.generateSignersList(
      this.transaction.paymentStatus?.code,
      this.transaction.signatureScheme,
      this.transaction.signatures
    );
  }

  private async loadStatusHistory() {
    if (!this.transaction.isPayment || !this.transaction.paymentId) {
      return;
    }

    const statusHistory = await this.statementService.getStatusHistory(
      this.transaction
    );

    if (!statusHistory || statusHistory.length === 0) {
      return;
    }

    const lastStatus = statusHistory[statusHistory.length - 1].status;

    this.statusHistory = statusHistory.map((item) => {
      let labelClassList;
      let iconClassList;

      switch (item.status) {
        case PAYMENT_STATUS.BANK_COMPLETED:
          labelClassList = 'text-success';
          iconClassList = 'text-success';
          break;
        case PAYMENT_STATUS.TRANSFER_ROLLBACK:
        case PAYMENT_STATUS.REJECTED:
          labelClassList = 'text-danger';
          iconClassList = 'text-danger';
          break;

        case PAYMENT_STATUS.BANK_PROCESSING:
          labelClassList =
            lastStatus === PAYMENT_STATUS.BANK_PROCESSING ? 'text-warning' : '';
          iconClassList =
            lastStatus === PAYMENT_STATUS.BANK_PROCESSING
              ? 'text-warning'
              : 'text-success';
          break;

        default:
          labelClassList = '';
          iconClassList = 'text-success';
          break;
      }

      return {
        time: item.time,
        label: `HISTORY.STATUS_HISTORY.${item.status}`,
        iconClassList,
        labelClassList,
      };
    });

    const isCompleted =
      lastStatus === PAYMENT_STATUS.REJECTED ||
      lastStatus === PAYMENT_STATUS.BANK_COMPLETED ||
      lastStatus === PAYMENT_STATUS.TRANSFER_ROLLBACK;

    if (lastStatus === PAYMENT_STATUS.CREATED) {
      this.statusHistory.push({
        label: `HISTORY.STATUS_HISTORY.${PAYMENT_STATUS.READY_TO_SEND}`,
        iconClassList: 'text-muted',
        labelClassList: 'text-muted',
      });
    }

    if (!isCompleted) {
      switch (this.transaction.paymentStatus.code) {
        case PAYMENT_STATUS.READY_TO_SEND:
          this.statusHistory.push({
            label: `HISTORY.STATUS_HISTORY.READY_TO_SEND_FROM_BANK`,
            iconClassList: 'text-warning',
            labelClassList: 'text-warning',
          });
          this.statusHistory.push({
            label: `HISTORY.STATUS_HISTORY.${PAYMENT_STATUS.BANK_COMPLETED}`,
            iconClassList: 'text-muted',
            labelClassList: 'text-muted',
          });
          break;

        case PAYMENT_STATUS.REJECTED:
        case PAYMENT_STATUS.TRANSFER_ROLLBACK:
          this.statusHistory.push({
            label: `HISTORY.STATUS_HISTORY.${this.transaction.paymentStatus.code}`,
            iconClassList: 'text-danger',
            labelClassList: 'text-danger',
          });
          break;

        case PAYMENT_STATUS.BANK_COMPLETED:
          this.statusHistory.push({
            label: `HISTORY.STATUS_HISTORY.${this.transaction.paymentStatus.code}`,
            iconClassList: 'text-success',
            labelClassList: 'text-success',
          });
          break;

        default:
          this.statusHistory.push({
            label: `HISTORY.STATUS_HISTORY.${PAYMENT_STATUS.BANK_COMPLETED}`,
            iconClassList: 'text-muted',
            labelClassList: 'text-muted',
          });
          break;
      }
    }
  }

  private mapTransactionContent() {
    this.title = this.statementService.getTransactionTitle(this.transaction, {
      name: this.transaction?.associatedField?.depositName || '',
    });
    this.description = this.statementService.mapTransactionDescription(
      this.transaction,
      {
        from: this.maskIbanPipe.transform(this.transaction.iban),
        to: this.maskIbanPipe.transform(this.transaction.correspondentAccount),
      }
    );
  }

  private mapPaymentActions() {
    const translatePrefix = 'STATEMENT.TRANSACTION';
    const iconsPrefix = '/assets/icons/transaction';

    this.actions = [
      {
        isShown: this.transaction.isRemovable,
        tooltip: `${translatePrefix}.DELETE`,
        icon: `${iconsPrefix}/delete.svg`,
        clickHandler: this.deletePayment.bind(this),
      },
      {
        isShown: this.transaction.isRevokable,
        tooltip: `${translatePrefix}.REVOKE`,
        icon: `${iconsPrefix}/revoke.svg`,
        clickHandler: this.revokePayment.bind(this),
      },
      {
        isShown: this.transaction.isExportable,
        tooltip: `${translatePrefix}.DOWNLOAD`,
        icon: `${iconsPrefix}/pdf.svg`,
        clickHandler: this.exportPayment.bind(this),
      },
      {
        isShown: this.transaction.isEditable,
        tooltip: `${translatePrefix}.EDIT`,
        icon: `${iconsPrefix}/edit.svg`,
        clickHandler: this.editPayment.bind(this),
      },
      {
        isShown: this.transaction.isRepeatable,
        tooltip: `${translatePrefix}.REPEAT`,
        icon: `${iconsPrefix}/repeat.svg`,
        clickHandler: this.repeatPayment.bind(this),
      },
      // {
      //   isShown: this.transaction.isSignable,
      //   tooltipTemplate: this.signersTooltipTemplate,
      //   icon: `${iconsPrefix}/sign.svg`,
      //   extraClasses: 'transaction__action--sign',
      //   clickHandler: this.signPayment.bind(this),
      // },
      {
        isShown:
          !this.transaction.isSignable && this.transaction.isSendableForSign,
        tooltip: `${translatePrefix}.SEND_FOR_SIGN`,
        icon: `${iconsPrefix}/for-sign.svg`,
        extraClasses: 'transaction__action--sign',
        clickHandler: this.sendForSign.bind(this),
      },
    ];
  }
}
