import {
  Component,
  ElementRef,
  Input,
  OnDestroy,
  OnInit,
  ViewChild,
} from '@angular/core';
import { FormControl } from '@angular/forms';

import { Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';

import {
  CurrencyPaymentService,
  IBank,
} from '@payments/services/currency-payment.service';

@Component({
  selector: 'j-foreign-bank-select',
  templateUrl: './foreign-bank-select.component.html',
  styleUrls: ['./foreign-bank-select.component.scss'],
})
export class ForeignBankSelectComponent implements OnDestroy, OnInit {
  @Input() labelControl: FormControl;
  @Input() bankControl: FormControl;
  @Input() countryCode: string;
  @Input() errorMessages;
  @Input() labelText: string = 'CURRENCY_PAYMENT.RECIPIENT.BIC';
  @Input() placeholderText: string =
    'CURRENCY_PAYMENT.RECIPIENT.NO_BIC_ENTERED';

  isLoading: boolean = false;

  banks: IBank[];
  isBicFocused: boolean = false;

  subscription = new Subscription();

  @ViewChild('input') input: ElementRef;

  constructor(private currencyPaymentService: CurrencyPaymentService) {}

  public async selectByBic(bic: string) {
    await this.retrieveBanks(bic);
    const foundBank = this.banks.find((bank) => bank.bic == bic);
    if (foundBank) {
      return this.onBankSelect(foundBank);
    }
  }

  get errorParams() {
    if (this.labelControl.errors?.bankRestricted) {
      return {
        bankName: this.bankControl.value?.name,
      };
    }

    return null;
  }

  ngOnInit() {
    this.listenBankSearch();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  focus() {
    setTimeout(() => {
      this.input.nativeElement.focus();
    }, 100);
  }

  onBicFocus(event: FocusEvent) {
    this.isBicFocused = true;
    this.labelControl.setValue('');
    this.banks = [];
  }

  onBicBlur(event: Event) {
    setTimeout(() => {
      this.isBicFocused = false;
    }, 300);
  }

  mapBankLabel(bank: IBank) {
    return `${bank.bic} - ${bank.name}`;
  }

  onBankSelect(selectedBank: IBank) {
    this.labelControl.setValue(this.mapBankLabel(selectedBank), {
      emitEvent: false,
    });
    this.bankControl.setValue(selectedBank);
  }

  private listenBankSearch() {
    this.subscription.add(
      this.labelControl?.valueChanges
        .pipe(debounceTime(300))
        .pipe(distinctUntilChanged())
        .subscribe((v) => {
          if (v?.length >= 1) {
            this.retrieveBanks(v, this.countryCode);
          }
        })
    );
  }

  private async retrieveBanks(query: string, countryCode: string = null) {
    this.isLoading = true;
    const countryCodeValue = countryCode?.substring(0, 2) || null;

    try {
      this.banks = await this.currencyPaymentService.getBanks(
        query,
        countryCodeValue
      );
    } catch (error) {
    } finally {
      this.isLoading = false;
    }
  }
}
