import { Injectable } from '@angular/core';
import { PackageTerms, PackageCalculation, PackageRest } from '@settings/models/packages.model';
import { CompanyService } from '@company/company.service';
import { ApiService, HEADERS, IError } from '@api/api.service';
import { HttpHeaders } from '@angular/common/http';


@Injectable({
  providedIn: 'root'
})
export class PackageRequestsService {

  constructor(private apiService: ApiService) {}

  async getCompanyCurrentPackage(companyId: string): Promise<PackageRest> {
    const url = `companies/${companyId}/packages/current`;
    return await this.apiService.get<PackageRest>(url);
  }

  async calculatePackageCost(companyId: string, body: PackageTerms): Promise<PackageCalculation> {
    const url = `companies/${companyId}/packages/calculate`;
    return await this.apiService.post<PackageCalculation>(url, body);
  }

  async changeCompanyPackage(companyId: string, body: PackageTerms, phoneAuthorization: string): Promise<any> {
    const url = `companies/${companyId}/packages/current`;
    const headers = new HttpHeaders().set(HEADERS.PHONE, phoneAuthorization);
    return await this.apiService.put(url, body, headers);
  }

  async changeCompanyPackageTransfer(companyId: string, body: any, phoneAuthorization: string): Promise<any> {
    const url = `companies/${companyId}/packages/additional:change`;
    const headers = new HttpHeaders().set(HEADERS.PHONE, phoneAuthorization);
    return await this.apiService.put(url, body, headers);
  }

  async changeCompanyPackageAccount(companyId: string, body: object, phoneAuthorization: string): Promise<any> {
    const url = `/companies/${companyId}/packages/accounts:change`;
    const headers = new HttpHeaders().set(HEADERS.PHONE, phoneAuthorization);
    return await this.apiService.put(url, body, headers);
  }

  async addCompanyPackageExtra(companyId: string, data: any, phoneAuthorization: string): Promise<any> {
    const url = `companies/${companyId}/packages/extra`;
    const body = {[data.transferType]: data.value}; // https://onsolut.atlassian.net/wiki/spaces/JUS/pages/2665414707/
    const headers = new HttpHeaders().set(HEADERS.PHONE, phoneAuthorization);
    return await this.apiService.put(url, body, headers);
  }
}
