import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { FormControl } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';
import { FILTER_PAYMENT_STATUS } from '@filter/filter.model';
import { CompanyService } from '@company/company.service';
import { PAYMENT_CATEGORY, PAYMENT_TYPE } from '@shared/models';
import { MaskedService } from '@shared/masked/masked.service';
import { TranslateService } from '@ngx-translate/core';
import { NavService } from '@shared/nav/nav.service';

@Component({
  selector: 'j-payment-result',
  templateUrl: './payment-result.component.html',
  styleUrls: ['./payment-result.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class PaymentResultComponent implements OnInit {
  @Input() result: boolean;
  @Input() currency: string;
  @Input() amount: string;
  @Input() multySubscribers: boolean;
  @Input() hasRights = true;
  @Input() message: string;
  @Input() payment;
  @Input() updateCreated: boolean;

  PAYMENT_TYPE = PAYMENT_TYPE;

  subscribe = new FormControl(false);
  constructor(
    public modal: NgbActiveModal,
    private router: Router,
    private companyService: CompanyService,
    private maskedService: MaskedService,
    private translate: TranslateService,
    private navService: NavService
  ) {}

  ngOnInit(): void {}

  get money() {
    if (this.payment?.type == PAYMENT_TYPE.FOREIGN) {
      return this.payment.details.paymentAmount;
    }

    return {
      amount: Math.abs(this.maskedService.getNumberedAmount(this.amount)),
      currency: this.currency,
    };
  }

  get subtitle() {
    let resultKey = '';
    if (this.result) {
      if (this.updateCreated) {
        resultKey = 'PAYMENTS.PAYMENT_EDITED';
      } else if (!this.hasRights) {
        resultKey = 'PAYMENTS.CREATED_FOR_SIGN';
      } else if (this.multySubscribers) {
        if (this.companyService.isPrimaryAuthority()) {
          resultKey = 'PAYMENTS.PAYMENT_SENT_FOR_SIGN';
        } else {
          resultKey = 'PAYMENTS.PAYMENT_SIGNED';
        }
      } else {
        resultKey = 'PAYMENTS.PAYMENT_SIGNED_AND_SENT';
      }
    } else {
      resultKey = 'SHARED.ERROR';
    }
    return this.translate.instant(resultKey);
  }

  closeModal(createNew?: boolean, goToMain?: boolean) {
    this.navService.retrieveContractBadges();
    this.navService.retrieveLetterBadges();

    if (goToMain) {
      this.router.navigate(['/']);
    }

    this.modal.close({
      subscribe: this.subscribe.value,
      createNew,
    });
  }

  goToHistory() {
    this.navService.retrieveContractBadges();
    this.navService.retrieveLetterBadges();

    if (this.result) {
      let paymentCategory = 'tenge';

      if (this.payment?.category == PAYMENT_CATEGORY.FOREIGN) {
        paymentCategory = 'foreign';
      } else if (this.payment?.category == PAYMENT_CATEGORY.CONVERSION) {
        paymentCategory = 'conversion';
      }

      if (
        this.companyService.isPrimaryAuthority() &&
        this.companyService.isSingleSignScheme()
      ) {
        this.router.navigate([`/history/${paymentCategory}`], {
          queryParams: { paymentStatus: FILTER_PAYMENT_STATUS.IN_PROGRESS },
        });
      } else {
        this.router.navigate([`/history/${paymentCategory}`], {
          queryParams: { paymentStatus: FILTER_PAYMENT_STATUS.FOR_SIGN },
        });
      }
    }

    this.modal.close();
  }
}
