import { Component, EventEmitter, Input, OnChanges, SimpleChanges, OnInit, Output } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { AlertModalPackage, ExtraTerms, PackageRest, PackageStatus, PackageTerms } from '@settings/models';
import { ICountryCode, ISmsToken } from '@shared/models';
import { Dictionary } from 'lodash';
import { SMS_HEADERS } from '@api/api.service';
import { PackageRequestsService } from '@settings/services/package-requests.service';
import { alertMessage } from '@settings/data';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'j-alert-package',
  templateUrl: './alert-package.component.html',
  styleUrls: ['./alert-package.component.scss'],
})
export class AlertPackageComponent implements OnInit {
  @Input() companyId: string;
  @Input() packageTerms: PackageTerms;
  @Input() extraTerms: ExtraTerms;
  @Input() packageRest: PackageRest;
  @Input() alertMessageType: 'connect' | 'connect_standart' | 'change' | 'change_transfer' | 'additional_package' | 'change_account';
  @Input() isWorkingDay: boolean;
  @Input() isSameDay: boolean;
  @Input() iban: string;
  @Output() refresh = new EventEmitter();
  alertMessage: AlertModalPackage;
  smsAfterOpen: boolean;
  // j-sms data
  countryCode: ICountryCode = JSON.parse(localStorage.getItem('countryCode'));
  headerParams: Dictionary<any> = { smsHeader: SMS_HEADERS.SIGNING_PACKAGE, };
  isReady: boolean;
  phone: string;
  // when alertMessageType === 'change_transfer'
  transferBody = {
    transferType: '',
    isEnabled: false
  };

  constructor(public modal: NgbActiveModal,
              private packageRequestsService: PackageRequestsService,
              private translateService: TranslateService) {}
  ngOnInit() {
    this.smsAfterOpen = false;
    this.isReady = false;
    if (this.alertMessageType === 'change_transfer' ) {
      this.initTransferAlert();
    } else if (this.alertMessageType === 'additional_package') {
      this.initExtraAlert();
    } else if(this.alertMessageType === 'change_account') {
      this.smsAfterOpen = true;
    } else {
      this.initPackageAlert();
    }
  }

  initPackageAlert() {
    if (!this.isWorkingDay) {
      this.alertMessage = alertMessage.working_days;
    } else if (this.isSameDay) {
      this.alertMessage = alertMessage.same_day;
    } else {
      this.alertMessage = alertMessage[this.alertMessageType];
    }
  }

  initExtraAlert() {
    if (!this.isWorkingDay) {
      this.alertMessage = alertMessage.working_days;
    } else {
      this.alertMessage = alertMessage[this.alertMessageType];
      this.translateService.get('SETTINGS.PACKAGE.MESSAGE.CONNECT_EXTRA.DESCRIPTION',
        { currency: this.extraTerms.item.currency, }).subscribe(data => this.alertMessage.title = data);
    }
  }

  initTransferAlert() {
    if (!this.isWorkingDay) {
      this.alertMessage = alertMessage.working_days;
    } else if ('discountInRubles' in this.packageTerms && this.packageRest.discountOnTransferInRubles.isSameDay) {
      this.alertMessage = alertMessage.transfer_ruble_same_day;
    } else if ('discountInForeignCurrency' in this.packageTerms && this.packageRest.discountOnTransferInCurrency.isSameDay) {
        this.alertMessage = alertMessage.transfer_currency_same_day;
    } else {
      this.generateAlertMessage();
    }
  }

  generateAlertMessage() {
    const message: AlertModalPackage = {};
    if ('discountInRubles' in this.packageTerms ) {
      this.transferBody.transferType = 'DISCOUNT_RUBLES';
      this.transferBody.isEnabled = !this.packageRest.discountOnTransferInRubles.hasDiscount;
      if (this.packageRest.discountOnTransferInRubles.hasDiscount) {
        message.title = `SETTINGS.PACKAGE.MESSAGE.DISABLE_DISCOUNT_RUBLES.TITLE`;
        this.translateService.get('SETTINGS.PACKAGE.MESSAGE.DISABLE_DISCOUNT_RUBLES.DESCRIPTION',
{ currency: this.packageRest.totalCost - this.packageRest.discountOnTransferInRubles.costDiscount, })
                .subscribe(data => message.description = data);
        message.buttonOk = {
          text: `SETTINGS.PACKAGE.MESSAGE.DISABLE_DISCOUNT_RUBLES.BUTTON_CLOSE.TEXT`,
          key: 'smsAfter'
        };
      } else {
         this.translateService.get('SETTINGS.PACKAGE.MESSAGE.ADD_DISCOUNT_RUBLES.TITLE',
          { currency: this.packageRest.discountOnTransferInRubles.costCurrent, })
          .subscribe(data => message.title = data);
         this.translateService.get('SETTINGS.PACKAGE.MESSAGE.ADD_DISCOUNT_RUBLES.DESCRIPTION',
          { currency: this.packageRest.totalCost + this.packageRest.discountOnTransferInRubles.costDiscount, })
          .subscribe(data => message.description = data);
         message.buttonOk = {
          text: `SETTINGS.PACKAGE.MESSAGE.ADD_DISCOUNT_RUBLES.BUTTON_CLOSE.TEXT`,
          key: 'smsAfter'
        };
      }
    } else {
      this.transferBody.transferType = 'DISCOUNT_CURRENCY';
      this.transferBody.isEnabled = !this.packageRest.discountOnTransferInCurrency.hasDiscount;
      if (this.packageRest.discountOnTransferInCurrency.hasDiscount) {
        message.title = `SETTINGS.PACKAGE.MESSAGE.DISABLE_DISCOUNT_CURRENCY.TITLE`;
        this.translateService.get('SETTINGS.PACKAGE.MESSAGE.DISABLE_DISCOUNT_CURRENCY.DESCRIPTION',
          { currency: this.packageRest.totalCost - this.packageRest.discountOnTransferInCurrency.costDiscount, })
          .subscribe(data => message.description = data);
        message.buttonOk = {
          text: `SETTINGS.PACKAGE.MESSAGE.DISABLE_DISCOUNT_CURRENCY.BUTTON_CLOSE.TEXT`,
          key: 'smsAfter'
        };
      } else {
        this.translateService.get('SETTINGS.PACKAGE.MESSAGE.ADD_DISCOUNT_CURRENCY.TITLE',
          { currency: this.packageRest.discountOnTransferInCurrency.costCurrent, })
          .subscribe(data => message.title = data);
        this.translateService.get('SETTINGS.PACKAGE.MESSAGE.ADD_DISCOUNT_CURRENCY.DESCRIPTION',
          { currency: this.packageRest.totalCost + this.packageRest.discountOnTransferInCurrency.costDiscount, })
          .subscribe(data => message.description = data);
        message.buttonOk = {
          text: `SETTINGS.PACKAGE.MESSAGE.ADD_DISCOUNT_CURRENCY.BUTTON_CLOSE.TEXT`,
          key: 'smsAfter'
        };
      }

    }
    message.buttonClose = {
      text: ``,
      key: 'refresh'
    };
    this.alertMessage = message;
  }

  onSmsLoaded() {
    this.isReady = true;
  }

  onSubmit(smsToken: ISmsToken) {
    this.isReady = false;
    if (this.alertMessageType === 'change_transfer' ) {
      this.changeTransfer(smsToken.signature);
    } else if (this.alertMessageType === 'additional_package') {
      this.addExtra(smsToken.signature);
    } else if(this.alertMessageType === 'change_account') {
      this.changePackageAccount(smsToken.signature);
    } else {
      this.changePackage(smsToken.signature);
    }
  }

  changePackage(phoneAuthorization: string) {
    this.packageRequestsService.changeCompanyPackage(this.companyId, this.packageTerms, phoneAuthorization)
      .then(() => {
        this.alertMessageType === 'connect_standart' ?
          this.alertMessage = alertMessage.prepared_standart :
          this.alertMessage = alertMessage.prepared;
      }).catch((e) => {
        this.catchError(e);
    }).finally( () => {
      const typeUser = (this.alertMessageType === 'connect') ? 'new' : 'old';
      const statusCode = this.alertMessage?.code || '';
      this.smsAfterOpen = false;
      this.refresh.emit({ typeUser, statusCode });
    });
  };

  changeTransfer(phoneAuthorization: string) {
    this.packageRequestsService.changeCompanyPackageTransfer(this.companyId, this.transferBody, phoneAuthorization)
      .then(() => {
          if (this.transferBody.transferType === 'DISCOUNT_RUBLES' ) {
            this.transferBody.isEnabled ?
              this.alertMessage = alertMessage.transfer_ruble_prepared_on :
              this.alertMessage = alertMessage.transfer_ruble_prepared_off;
          }
          if (this.transferBody.transferType === 'DISCOUNT_CURRENCY' ) {
          this.transferBody.isEnabled ?
            this.alertMessage = alertMessage.transfer_currency_prepared_on :
            this.alertMessage = alertMessage.transfer_currency_prepared_off;
        }
      }).catch((e) => {
        this.catchError(e);
    }).finally( () => {
      const typeUser = 'old';
      const statusCode = this.alertMessage?.code || '';
      this.smsAfterOpen = false;
      this.refresh.emit({ typeUser, statusCode });
    });
  };

  addExtra(phoneAuthorization: string) {
    this.packageRequestsService.addCompanyPackageExtra(this.companyId,
      { transferType: this.extraTerms.transferType,
              value: this.extraTerms.value},
            phoneAuthorization)
      .then(() => {
        this.alertMessage = alertMessage.additional_success;
      }).catch((e) => {
        this.catchError(e);
    }).finally( () => {
      this.smsAfterOpen = false;
      const typeUser = 'old';
      const statusCode = this.alertMessage?.code || '';
      this.refresh.emit({ typeUser, statusCode });
    });
  };

  changePackageAccount(phoneAuthorization: string) {
    this.packageRequestsService.changeCompanyPackageAccount(this.companyId, {'iban': this.iban}, phoneAuthorization)
      .then(() => {
          this.alertMessage = alertMessage.change_account;
      }).catch((e) => {
        this.catchError(e);
    }).finally( () => {
      this.smsAfterOpen = false;
      this.refresh.emit();
    });
  };

  buttonClick(key) {
    if (key === 'smsAfter') { this.smsAfterOpen = true;     this.isReady = false; }
    if (key === 'returnMyPackage') { this.modal.close(); }
    if (key === 'close') { this.modal.close(); }
    if (key === 'refresh') {
      this.refresh.emit({});
      this.modal.close();
    }
  }

  catchError(e) {
    if (e.status === 402) {
      this.alertMessage = alertMessage.payment;
    } else
    if (e.status === 423) {
      this.alertMessage = alertMessage.payment_locked;
    } else {
      this.alertMessage = alertMessage.error;
    }
  }
}
