import {
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';
import {
  ItemCardSettingsPackage,
  ItemFormsSettingsSale,
  ItemSettingsPrice,
  ItemTabsSettingsPackage,
  PackageCalculation,
  PackageRest,
  PackageTerms,
} from '@settings/models';
import { paymentData, refillData, saleData } from '@settings/data';
import { of } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import { AccountsService } from '@accounts/services/accounts.service';
import { RegularAccount } from '@accounts/account.model';
import { CURRENCY } from '@shared/currency/currency.model';
import { FeatureFlagsService, FEATURE_FLAG } from '@feature-flags/index';

@Component({
  selector: 'j-calculate-package',
  templateUrl: './calculate-package.component.html',
  styleUrls: ['./calculate-package.component.scss'],
})
export class CalculatePackageComponent implements OnInit {
  @Output() calculatePackage: EventEmitter<PackageTerms> = new EventEmitter();
  @Output() openModal: EventEmitter<any> = new EventEmitter();
  @Input() isWorkingDay = true;
  @Input() packageRest: PackageRest = null;
  packageTerms: PackageTerms;
  // Card component information
  public payment: ItemCardSettingsPackage;
  public refill: ItemCardSettingsPackage;
  public sale: ItemCardSettingsPackage;
  // data set in TabsSettingsPackage component
  public paymentData: ItemTabsSettingsPackage[] = paymentData;
  public refillData: ItemTabsSettingsPackage[] = refillData;
  public saleData: ItemFormsSettingsSale[] = saleData;
  public price: ItemSettingsPrice;
  public disabledButton = true;
  public tengeAccount: number;
  public isSubmitted = false;
  public isDisabledAccount;

  constructor(
    private translateService: TranslateService,
    private accountsService: AccountsService,
    private featureFlagsService: FeatureFlagsService,
    ) {}

  ngOnInit(): void {
    this.packageTerms = new PackageTerms(null, 0, 0, false, false);
    this.defaultView();
    this.getPackageTerms(this.packageRest);
    this.retrieveAccounts();

    this.isDisabledAccount = this.featureFlagsService.isDisabled(FEATURE_FLAG.PACKAGES_CHANGE_ACCOUNT);
  }

  defaultView(): boolean {
    // check object all keys (fals?)
    this.disabledButton = true;
    if (Object.keys(this.packageTerms).every((k) => !this.packageTerms[k])) {
      this.payment = new ItemCardSettingsPackage(
        'SETTINGS.PACKAGE.CALCULATE.PAYMENTS',
        'payments',
        ''
      );
      this.refill = new ItemCardSettingsPackage(
        'SETTINGS.PACKAGE.CALCULATE.DEPOSITS',
        'deposits',
        ''
      );
      this.sale = new ItemCardSettingsPackage(
        'SETTINGS.PACKAGE.CALCULATE.SALES',
        'sales',
        ''
      );
      this.price = new ItemSettingsPrice(0, 0, 0);
      return false;
    }
    return true;
  }

  packageCalculate(event): void {
    // add millions to depositAndWithdrawal
    if ('depositAndWithdrawal' in event) {
      event.depositAndWithdrawal *= 1e6;
    }
    // create package terms
    this.packageTerms = { ...this.packageTerms, ...event };
    if (this.defaultView()) {
      this.calculatePackage.emit(this.packageTerms);
    }
  }

  @Input() set data(packageCalculation: PackageCalculation) {
    if (packageCalculation) {
      this.price.totalCost = packageCalculation.conditions.packageCost;
      this.price.saving = packageCalculation.saving;
      this.price.standartCost = packageCalculation.standartCost;
      packageCalculation.conditions.costNextPayment
        ? (this.payment.description = this.translateService.get(
            'SETTINGS.PACKAGE.CALCULATE.PAYMENT_DESCRIPTION',
            { payment: packageCalculation.conditions.costNextPayment }
          ))
        : (this.payment.description = of(''));
      packageCalculation.depositAndWithdrawal
        ? (this.refill.description = this.translateService.get(
            'SETTINGS.PACKAGE.CALCULATE.DEPOSITS_DESCRIPTION',
            {
              deposit: packageCalculation.depositAndWithdrawal / 1e6,
              depositingOverLimit:
                packageCalculation.conditions.depositingOverLimit,
              withdrawalOverLimit:
                packageCalculation.conditions.withdrawalOverLimit,
            }
          ))
        : (this.refill.description = of(''));
      this.isWorkingDay && packageCalculation?.saving !== 0
        ? (this.disabledButton = false)
        : (this.disabledButton = true);
    }
  }

  getPackageTerms(packageRest: PackageRest) {
    if (packageRest) {
      this.packageTerms.numberOfPayments =
        packageRest.restOfPayments.totalNumberOfPayments || 0;
      this.packageTerms.depositAndWithdrawal =
        packageRest.depositAndWithdrawals.totalDepositAndWithdrawals || 0;
      this.packageTerms.discountInRubles =
        packageRest.discountOnTransferInRubles.hasDiscount;
      this.packageTerms.discountInForeignCurrency =
        packageRest.discountOnTransferInCurrency.hasDiscount;
      this.calculatePackage.emit(this.packageTerms);
    }
  }

  private async retrieveAccounts() {
    try {
      const accounts = await this.accountsService.getAccounts();
      this.tengeAccount = accounts.reduce((total, account) => {
        if(account.balance.currency === CURRENCY.KZT) total += 1;
        return total
      }, 0);

      if(this.tengeAccount === 1) {
        const tenge = accounts.filter(ac => ac.balance.currency === CURRENCY.KZT);
        this.packageTerms.iban = tenge[0]?.iban;
      }

      return accounts;
    } catch (error) {
      return [];
    }
  }

  openAlertModal() {
    this.isSubmitted = true;
    if(this.isDisabledAccount){
      this.openModal.emit(this.packageTerms);
    } else {
      if(this.packageTerms.iban) {
        this.openModal.emit(this.packageTerms);
        this.isSubmitted = false;
      }
    }
  }
}
