import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IRefundDetails } from '@shared/models';

@Component({
  selector: 'j-refund-employee-selector',
  templateUrl: './refund-employee-selector.component.html',
  styleUrls: ['./refund-employee-selector.component.scss']
})
export class RefundEmployeeSelectorComponent {
  @Input() refundDetails: IRefundDetails[];
  @Output() closed = new EventEmitter();
  constructor() { }


  onClose() {
    this.closed.emit();
  }


}
