import { Injectable } from '@angular/core';
import {
  CanActivate,
  ActivatedRouteSnapshot,
  RouterStateSnapshot,
  UrlTree,
  Router,
} from '@angular/router';

import { Observable } from 'rxjs';

import { FeatureFlagsService } from '@feature-flags/index';
import { filter, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class IsFeatureEnabledGuard implements CanActivate {
  constructor(
    private router: Router,
    private featureFlagsService: FeatureFlagsService
  ) {}
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ):
    | Observable<boolean | UrlTree>
    | Promise<boolean | UrlTree>
    | boolean
    | UrlTree {
    return this.featureFlagsService.featureFlags$.pipe(
      filter((flags) => flags.length > 0),
      map((flags) => {
        if (!flags.find((flag) => flag.key === next.data.featureFlag).active) {
          this.router.navigate(['/']);
          return false;
        }
        return true;
      })
    );
  }
}
