import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IFxContractRecipient } from '@shared/models';

@Component({
  selector: 'j-foreign-contracts-recipient',
  templateUrl: './foreign-contracts-recipient.component.html',
  styleUrls: ['./foreign-contracts-recipient.component.scss'],
})
export class ForeignContractsRecipientComponent {
  @Input() searchTerm: string;
  @Input() recipient: IFxContractRecipient;
  @Output() selected = new EventEmitter<IFxContractRecipient>();

  constructor() {}

  get bic() {
    if (!this.recipient.bankBic || !this.recipient.bankName) {
      return;
    }
    
    return `${this.recipient.bankBic} - ${this.recipient.bankName}`;
  }

  onSelect() {
    this.selected.emit(this.recipient);
  }
}
