import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ItemTabsSettingsPackage } from '@settings/models';

@Component({
  selector: 'j-settings-package-tabs',
  templateUrl: './settings-package-tabs.component.html',
  styleUrls: ['./settings-package-tabs.component.scss'],
})
export class SettingsPackageTabsComponent implements OnInit {
  @Input() items: ItemTabsSettingsPackage[] | null;
  @Input() key: 'numberOfPayments' | 'depositAndWithdrawal' =
    'numberOfPayments';
  @Output() getValue = new EventEmitter();
  readonly textType = {
    numberOfPayments: 'SETTINGS.PACKAGE.TABS.NUMBER_OF_PAYMENTS',
    depositAndWithdrawal: 'SETTINGS.PACKAGE.TABS.DEPOSIT_AND_WITHDRAWAL',
  };
  constructor() {}

  ngOnInit(): void {
    // this.items.map(v => v.checked = false);
  }
  @Input() set select(value: number) {
    this.items.map((item) =>
      item.value === value ? (item.checked = true) : (item.checked = false)
    );
  }

  setValue(item, index) {
    item.checked = !item.checked;
    this.items.filter((v, i) => i !== index).map((v) => (v.checked = false));
    this.getValue.emit({ [this.key]: item.checked ? item.value : 0 });
  }
}
