import { Injectable } from '@angular/core';
import { ApiService } from '@api/index';
import { CompanyService } from '@company/company.service';

export enum ACCOUNT_SUBSCRIPTION_EXPORT_TYPE {
  PDF = 'PDF',
  XLSX = 'XLSX',
  ONE_C = 'ONE_C',
}

export enum ACCOUNT_SUBSCRIPTION_TYPE {
  DAILY = 'DAILY',
  MONTHLY = 'MONTHLY',
}

export interface IAccountSubscription {
  accountId: string;
  emails: string[];
  exportType: ACCOUNT_SUBSCRIPTION_EXPORT_TYPE;
  subscriptionType: ACCOUNT_SUBSCRIPTION_TYPE;

  id?: string;
  created?: string;
  userAuthorityId?: string;
}

@Injectable({
  providedIn: 'root',
})
export class AccountsSubscriptionService {
  constructor(
    private companyService: CompanyService,
    private api: ApiService
  ) {}

  getSubscriptions(accountId: string) {
    const url = this.getApiUrl(accountId);
    return this.api.get<IAccountSubscription[]>(url);
  }

  saveSubscription(subscription: IAccountSubscription, subscriptionId: string) {
    const url = this.getApiUrl(subscription.accountId, subscriptionId);
    return this.api.put<IAccountSubscription>(url, subscription);
  }

  createSubscription(subscription: IAccountSubscription) {
    const url = this.getApiUrl(subscription.accountId);
    return this.api.post<IAccountSubscription>(url, subscription);
  }

  deleteSubscription(accountId: string, subscriptionId: string) {
    const url = this.getApiUrl(accountId, subscriptionId);
    return this.api.delete(url);
  }

  private getApiUrl(accountId: string, subscriptionId: string = null) {
    return `companies/${
      this.companyService.currentId
    }/accounts/${accountId}/statements-subscriptions${
      subscriptionId ? '/' + subscriptionId : ''
    }`;
  }
}
