import { AnalyticsManagerAbstract } from '@analytics/services/manager';
import {
  Component,
  EventEmitter,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { SMS_HEADERS } from '@api/api.service';
import { CompanyService } from '@company/company.service';
import { FEATURE_FLAG } from '@feature-flags/feature-flags.model';
import { FeatureFlagsService } from '@feature-flags/feature-flags.service';
import { TranslateService } from '@ngx-translate/core';
import {
  CurrenciesListServiceAbstract,
  Currency,
  CurrencyWithName,
} from '@shared/currencies-list/currencies-list.service.abstract';
import { AUTHORITY_TYPE, ICountryCode, ISmsToken } from '@shared/models';
import { Dictionary } from 'lodash';
import { combineLatest } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';
import { ModalComponent } from '../../../shared/components/modal/modal.component';
import {
  AdditionalAccountModalResultType,
  AdditionalAccountModalResultTypeConfig,
} from './components/result/result.component';
import { AdditionalAccountsService } from './services/additional-accounts.service';

export type additionalAccountModalStep =
  | 'application'
  | 'sign'
  | AdditionalAccountModalResultType;

@Component({
  selector: 'j-open-additional-account',
  templateUrl: './open-additional-account.component.html',
  styleUrls: ['./open-additional-account.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class OpenAdditionalAccountComponent implements OnInit, OnDestroy {
  constructor(
    private currencyListService: CurrenciesListServiceAbstract,
    private additionalAccountService: AdditionalAccountsService,
    private translateService: TranslateService,
    private companyService: CompanyService,
    private analytics: AnalyticsManagerAbstract,
    private featureFlagsService: FeatureFlagsService
  ) {
    this.showOpenAdditionalAccountSubscription = combineLatest([
      this.featureFlagsService.featureFlags$,
      this.companyService.currentAuthority$,
    ]).subscribe(([_, authority]) => {
      this.showOpenAdditionalAccount =
        authority?.authority === AUTHORITY_TYPE.PRIMARY_SIGNATURE &&
        !this.featureFlagsService.isDisabled(
          FEATURE_FLAG.OPEN_ADDITIONAL_ACCOUNT
        );
    });
  }

  ngOnInit(): void {}

  ngOnDestroy(): void {
    this.showOpenAdditionalAccountSubscription.unsubscribe();
  }

  @ViewChild('applicationModal') private applicationModal: ModalComponent;
  @Output() added = new EventEmitter();

  openSidebar = false;
  currencies: CurrencyWithName[] = null;
  sidebarLabel = this.translateService.instant(
    'ADDITIONAL_ACCOUNTS.SIDEBAR_HEADER'
  );
  selectedCurrency;
  companyName;
  step: additionalAccountModalStep = 'application';
  requestedAccountId: string;
  checkInProgress = false;

  headerParams: Dictionary<any> = { smsHeader: SMS_HEADERS.OPENING_ACCOUNT };
  countryCode: ICountryCode = JSON.parse(localStorage.getItem('countryCode'));
  phone: string = JSON.parse(localStorage.getItem('user')).phone;
  isSmsLoaded: boolean = false;
  result: AdditionalAccountModalResultTypeConfig;

  showOpenAdditionalAccountSubscription = null;
  showOpenAdditionalAccount: boolean = false;

  setResult(result: AdditionalAccountModalResultType, errCode) {
    this.result =
      result === 'success'
        ? {
            iconName: 'success',
            header: 'ADDITIONAL_ACCOUNTS.SUCCESS_MODAL.HEADER',
            label: 'ADDITIONAL_ACCOUNTS.SUCCESS_MODAL.TEXT',
            btnText: 'ADDITIONAL_ACCOUNTS.SUCCESS_MODAL.BTN',
          }
        : {
            iconName: 'fail',
            header: 'ADDITIONAL_ACCOUNTS.FAIL_MODAL.HEADER',
            label: `ADDITIONAL_ACCOUNTS.FAIL_MODAL.TEXT.${errCode}`,
            btnText: 'ADDITIONAL_ACCOUNTS.FAIL_MODAL.BTN',
          };
  }

  toggleOpenSidebar() {
    this.openSidebar = !this.openSidebar;
  }

  async checkUser() {
    this.analytics.logEvent('JMain_Add_Click', { version: 1 });
    this.checkInProgress = true;
    this.additionalAccountService
      .check()
      .pipe(
        switchMap(({ id }) => {
          this.requestedAccountId = id;
          return this.currencyListService.loadCurrencyList();
        }),
        map((currencies) => {
          const currencyDictionary = this.translateService.instant(
            'SHARED.CURRENCY_DESCRIPTION'
          );
          return currencies.map((currency) => ({
            ...currency,
            name: currencyDictionary[currency.code],
          }));
        })
      )
      .subscribe(
        (currencies) => {
          this.currencies = currencies;
          this.toggleOpenSidebar();
        },
        (err) => {
          this.setStep(
            'fail',
            err.status ? `${String(err.status).slice(0, -2)}x` : '5x'
          );
          this.openModal();
        }
      )
      .add(() => (this.checkInProgress = false));
  }

  openModal() {
    this.applicationModal.open({
      centered: true,
      windowClass: 'additional-account-application-modal',
    });
  }

  onCurrencySelect(currency: Currency) {
    this.selectedCurrency = currency.code;
    this.companyName = this.companyService.currentAuthority.company.shortName;
    this.toggleOpenSidebar();
    this.openModal();
  }

  setStep(step: additionalAccountModalStep, errCode = '5x') {
    this.step = step;
    if (this.showResult) {
      this.setResult(<AdditionalAccountModalResultType>step, errCode);
    }
  }

  get showApplication() {
    return ['application', 'sign'].includes(this.step) && !this.isSmsLoaded;
  }

  get showResult() {
    return ['success', 'fail'].includes(this.step);
  }

  setSmsLoaded(isLoaded: boolean) {
    this.isSmsLoaded = isLoaded;
  }

  onSubmit({ signature }: ISmsToken) {
    this.additionalAccountService
      .apply(this.selectedCurrency, this.requestedAccountId, signature)
      .subscribe(
        () => {
          this.setStep('success');
          this.analytics.logEvent('JMain_accept_AccountAdd', { version: 1 });
        },
        () => {
          this.setStep('fail');
        }
      );
  }

  onSuccessClose() {
    this.applicationModal.close();
    this.added.emit();
    this.setStep('application');
  }

  resetModal() {
    this.setStep('application');
    this.setSmsLoaded(false);
    this.applicationModal.close();
  }

  onModalClose() {
    this.step === 'success' ? this.onSuccessClose() : this.resetModal();
  }
}
