import { IMoney } from '@shared/currency/currency.model';
import { defaults } from 'lodash';

export interface ILoan {
  amount: IMoney;
  colvirId: string;
  colvirReferenceId: string;
  companyId: string;
  dateFrom: string;
  dateTo: string;
  id: string;
  productCode: string;
  productName: string;
  rate: number;
}

export interface ILoanSchedule {
  loanSchedule: {
    [key: string]: [
      ILoanScheduleField
    ]
  };
}

export interface IOperationType {
  label: string;
  code: string;
}

export interface LoanScroll {
  coord_y: number;
  selectedTab: string;
}

export interface ILoanScheduleField {
  currentPayment: boolean;
  interestAmount: number;
  interestOverdueFee: number;
  mainDebtAmount: number;
  mainDebtOverdueFee: number;
  paymentDate: Date;
  paymentNumber: number;
  remainDebt: number;
  totalAmount: number;
  totalPayments: number;
}

export class Loan implements ILoan {
  amount: IMoney;
  colvirId: string;
  colvirReferenceId: string;
  companyId: string;
  dateFrom: string;
  dateTo: string;
  id: string;
  productCode: string;
  productName: string;
  rate: number;
  term: number;
  loanAcc: string;
  effRate: number;

  constructor(loan: ILoan) {
    defaults(this, loan);
  }
}

export enum Direction {
  Up = 'Up',
  Down = 'Down'
}
