import { Component, Input, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

import { Card } from '@cards/models';
import { CardsService } from '@cards/services';
import { NotifierService } from 'angular-notifier';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'j-card-rename-modal',
  templateUrl: './card-rename-modal.component.html',
  styleUrls: ['./card-rename-modal.component.scss'],
})
export class CardRenameModalComponent implements OnInit {
  @Input() card: Card;

  aliasMaxLength = 24;
  translatePrefix = 'CARDS.RENAME_MODAL';

  form: FormGroup;
  isSubmitted = false;
  isSubmitting = false;

  constructor(
    public modal: NgbActiveModal,
    private cardsService: CardsService,
    private notifierService: NotifierService,
    private translateService: TranslateService
  ) {}

  ngOnInit() {
    this.form = new FormGroup({
      alias: new FormControl(this.card.alias || '', [
        Validators.required,
        Validators.maxLength(this.aliasMaxLength),
      ]),
    });
  }

  get f() {
    return this.form.controls;
  }

  dismissModal() {
    this.modal.dismiss();
  }

  closeModal(alias: string) {
    this.modal.close(alias);
  }

  mapTranslate(key: string) {
    return `${this.translatePrefix}.${key}`;
  }

  async onSubmit() {
    this.isSubmitted = true;

    if (this.form.invalid) {
      return;
    }

    this.isSubmitting = true;
    let message = '';

    try {
      const { alias } = this.form.getRawValue();
      await this.cardsService.renameCard(this.card.cardId, alias);

      message = 'SUCCESS';

      this.closeModal(alias);
    } catch (error) {
      message = 'ERROR';
    } finally {
      this.isSubmitting = false;
      this.notifierService.notify(
        'default',
        this.translateService.instant(this.mapTranslate(message))
      );
    }
  }
}
