import { Component, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'j-payment-guard-modal',
  templateUrl: './payment-guard-modal.component.html',
  styleUrls: ['./payment-guard-modal.component.scss'],
})
export class PaymentGuardModalComponent implements OnInit {
  constructor(public modal: NgbActiveModal) {}

  ngOnInit(): void {}

  cancel() {
    this.modal.close(false);
  }
  continue() {
    this.modal.close(true);
  }
}
