import { Component, EventEmitter, Input, Output } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';

import {
  Recipient,
  RecipientAccount,
} from '@company/recipient/recipient.model';

export interface ISelectedRecipient {
  recipient: Recipient;
  account: RecipientAccount;
}

@Component({
  selector: 'j-foreign-recipient',
  templateUrl: './foreign-recipient.component.html',
  styleUrls: ['./foreign-recipient.component.scss'],
})
export class ForeignRecipientComponent {
  @Input() searchTerm: string;
  @Input() recipient: Recipient;
  @Output() selected = new EventEmitter<ISelectedRecipient>();

  constructor(private translateService: TranslateService) {}

  get countryName() {
    switch (this.translateService.currentLang) {
      case 'ru':
      default:
        return this.recipient?.country?.nameRu;
      case 'kk':
        return this.recipient?.country?.nameKk;
      case 'en':
        return this.recipient?.country?.nameEn;
    }
  }

  onSelect(account: RecipientAccount) {
    this.selected.emit({
      recipient: this.recipient,
      account: account,
    });
  }
}
