import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';
import { CurrencyWithName } from '@shared/currencies-list/currencies-list.service.abstract';

@Component({
  selector: 'j-currency-item',
  templateUrl: './currency-item.component.html',
  styleUrls: ['./currency-item.component.scss']
})
export class CurrencyItemComponent implements OnInit, OnChanges {

  @Input() currency: CurrencyWithName;
  
  @Output() onSelect = new EventEmitter();

  src;
  label;

  ngOnChanges() {
    const {
      code,
      name
    } = this.currency;
    this.label = `${code} - ${name}`;
    this.src = `/assets/currency/${code}.svg`
  }

  onSelectClick() {
    if (this.currency.isPossible) {
      this.onSelect.emit();
    }
  }

  constructor() { }

  ngOnInit(): void {
  }

}
