import {
  ACCOUNT_SUBSCRIPTION_EXPORT_TYPE,
  IAccountSubscription,
} from '@accounts/services/accounts-subscription.service';
import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  SimpleChanges,
} from '@angular/core';
import { FormArray, FormControl, FormGroup, Validators } from '@angular/forms';
import { generateUniqueId } from '@utils/index';

export interface IAccountSubscriptionForm {
  id?: string;
  emails: string[];
  exportType: ACCOUNT_SUBSCRIPTION_EXPORT_TYPE;
}

@Component({
  selector: 'j-account-subscription',
  templateUrl: './account-subscription.component.html',
  styleUrls: ['./account-subscription.component.scss'],
  animations: [
    trigger('smoothCollapse', [
      state(
        'initial',
        style({
          height: '0',
          overflow: 'hidden',
          opacity: '0',
        })
      ),
      state(
        'final',
        style({
          overflow: 'hidden',
          opacity: '1',
        })
      ),
      transition('initial=>final', animate('300ms')),
      transition('final=>initial', animate('300ms')),
    ]),
  ],
})
export class AccountSubscriptionComponent implements OnInit {
  @Input() title: string;
  @Input() isLoading: boolean;
  @Input() description: string;
  @Input() savedSubscription: IAccountSubscription;

  @Output() deleted = new EventEmitter();
  @Output() submitted = new EventEmitter();

  isSubmitted: boolean = false;

  subscriptionSwitch: FormControl;

  fileTypes = [
    { label: 'PDF', value: 'PDF', id: generateUniqueId() },
    { label: 'EXL', value: 'XLSX', id: generateUniqueId() },
    { label: '1C', value: 'ONE_C', id: generateUniqueId() },
    { label: 'XML', value: 'XML', id: generateUniqueId() },
    { label: 'MT940', value: 'MT940', id: generateUniqueId() },
  ];

  subscriptionForm: FormGroup;

  readonly FORM_DEFAULTS = {
    enabled: false,
    id: null,
    emails: [''],
    exportType: ACCOUNT_SUBSCRIPTION_EXPORT_TYPE.PDF,
  };

  constructor() {}

  ngOnChanges(changes: SimpleChanges) {
    if (changes.isLoading) {
      if (changes.isLoading.currentValue) {
        this.subscriptionForm?.disable();
      } else {
        this.subscriptionForm?.enable();
      }
    }
    if (changes.savedSubscription) {
      this.subscriptionForm?.patchValue({
        id:
          this.savedSubscription?.id ||
          this.f.id.value ||
          this.FORM_DEFAULTS.id,
        emails:
          this.savedSubscription?.emails ||
          this.f.emails.value ||
          this.FORM_DEFAULTS.emails,
        exportType:
          this.savedSubscription?.exportType ||
          this.f.exportType.value ||
          this.FORM_DEFAULTS.exportType,
      });
    }
  }

  ngOnInit() {
    const DEFAULTS = {
      enabled: this.savedSubscription?.id ? true : false,
      id: this.savedSubscription?.id || this.FORM_DEFAULTS.id,
      emails: this.savedSubscription?.emails || this.FORM_DEFAULTS.emails,
      exportType:
        this.savedSubscription?.exportType || this.FORM_DEFAULTS.exportType,
    };

    this.subscriptionForm = new FormGroup({
      switch: new FormControl(DEFAULTS.enabled),
      id: new FormControl(DEFAULTS.id),
      emails: new FormArray(
        DEFAULTS.emails.map((email) => {
          return new FormControl(email, [
            Validators.email,
            Validators.required,
          ]);
        })
      ),
      exportType: new FormControl(DEFAULTS.exportType),
    });

    if (this.isLoading) {
      this.subscriptionForm.disable();
    }

    this.subscriptionForm.valueChanges
      // .pipe(throttleTime(300))
      .subscribe((value) => {
        this.isSubmitted = false;

        if (value.switch == false) {
          this.deleted.emit(this.f.id.value);
          this.clearFormArray(this.f.emails as FormArray);
          this.subscriptionForm.reset(this.FORM_DEFAULTS);
        }
      });
  }

  onSubmit() {
    this.isSubmitted = true;

    if (this.subscriptionForm.invalid) {
      return;
    }

    this.submitted.emit(this.subscriptionForm.getRawValue());
  }

  addEmail(event: Event) {
    event.preventDefault();
    if (this.isLoading || this.emailControls.length == 3) {
      return;
    }

    this.isSubmitted = false;

    this.emailControls.push(
      new FormControl('', [Validators.email, Validators.required])
    );
  }

  deleteEmail(event: Event, index: number) {
    event.preventDefault();

    if (this.isLoading) {
      return;
    }

    this.emailControls.splice(index, 1);
  }

  mapEmailErrorText(errors) {
    if (errors?.email) {
      return 'ERRORS.WRONG_FORMAT';
    }
    return null;
  }

  clearFormArray(formArray: FormArray) {
    while (formArray.length !== 1) {
      formArray.removeAt(0);
    }
  }

  get isCollapsed() {
    return this.subscriptionForm?.controls?.switch?.value == true;
  }

  get emailControls() {
    return (this.f.emails as FormArray).controls;
  }

  get f() {
    return this.subscriptionForm?.controls;
  }
}
