import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CompanyService } from '@company/company.service';
import { AnalyticsManagerAbstract } from '@analytics/services/manager';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { of, Subscription } from 'rxjs';
import { IUserAuthority, User } from '@shared/models';
import { TranslateService } from '@ngx-translate/core';
import { myProgressPaymentData, options } from '../../data/packages.data';
import { PackageRequestsService } from '../../services/package-requests.service';
import { ExtraTerms, ItemTabsSettingsPackage, MyItemSettingsProgress, PackageRest, PackageStatus } from '../../models';
import { AlertPackageComponent } from '../../containers/settings-package/alert-package/alert-package.component';
@Component({
  selector: 'j-settings-package-additional',
  templateUrl: './settings-package-additional.component.html',
  styleUrls: ['./settings-package-additional.component.scss']
})
export class SettingsPackageAdditionalComponent implements OnInit {
  public packageResponseStatus: PackageStatus; // Loading | Ready | Success | NotFound | Error | Forbidden;
  public packageRest: PackageRest;  // get company package response
  //
  private subscription: Subscription = new Subscription();
  private companyId: string; // Selected companyID in menu
  //
  public theme = { height: '10rem', width: '100%', 'background-color': '#ECEEF1' };   // ngx-skeleton style array object
  public page = {
    payment: {
      title: 'SETTINGS.PACKAGE.ADDITIONAL.PAYMENT.TITLE',
      key: 'numberOfPayments',
      helpText: {
        title: 'SETTINGS.PACKAGE.ADDITIONAL.PAYMENT.HELP_TEXT.TITLE',
        description: 'SETTINGS.PACKAGE.ADDITIONAL.PAYMENT.HELP_TEXT.DESCRIPTION',
        descriptionDisabled: 'SETTINGS.PACKAGE.ADDITIONAL.PAYMENT.HELP_TEXT.DESCRIPTION_DISABLED'
      }
    },
    refill: {
      title: 'SETTINGS.PACKAGE.ADDITIONAL.REFILL.TITLE',
      key: 'depositAndWithdrawal',
      helpText: {
        title: 'SETTINGS.PACKAGE.ADDITIONAL.REFILL.HELP_TEXT.TITLE',
        description: 'SETTINGS.PACKAGE.ADDITIONAL.REFILL.HELP_TEXT.DESCRIPTION',
        descriptionDisabled: 'SETTINGS.PACKAGE.ADDITIONAL.REFILL.HELP_TEXT.DESCRIPTION_DISABLED'
      }
    },
  };
  public type: string;
  //
  public myData: MyItemSettingsProgress = { ...myProgressPaymentData };
  public myExtraData: MyItemSettingsProgress = { ...myProgressPaymentData };
  public data: ItemTabsSettingsPackage[];
  constructor(private route: ActivatedRoute,
              private packageRequestsService: PackageRequestsService,
              private companyService: CompanyService,
              private analytics: AnalyticsManagerAbstract,
              private modalService: NgbModal,
              private translateService: TranslateService) {
    this.type = route.snapshot.params?.type;
  }

  ngOnInit() {
    this.subscription = this.companyService.refresh$
      .subscribe((data: [User | null, IUserAuthority | null, boolean | null]) => {
        this.companyId = data[1].companyId;
        this.getPackageRest();
      });
  }
  getPackageRest() {
    this.packageResponseStatus = PackageStatus.Loading;
    this.packageRequestsService.getCompanyCurrentPackage(this.companyId)
      .then((data: PackageRest) => {
        this.packageRest = data;
        !!this.packageRest?.totalCost ?
          this.packageResponseStatus = PackageStatus.Success :
          this.packageResponseStatus = PackageStatus.Error;
        this.addData(this.packageRest);
        this.analytics.logEvent('JBundleview_Show', {version: 1, Status: '204'});
      }).catch(e => {
      if (e.status === 403) {
        this.packageResponseStatus = PackageStatus.Forbidden;
        this.analytics.logEvent('JBundleview_Show', {version: 1, Status: '403'});
      } else {
        this.packageResponseStatus = PackageStatus.Error;
        this.analytics.logEvent('JBundleview_Show', {version: 1, Status: '50x'});
      }
    });
  }

  addData(packageRest: PackageRest) {
    // progressBar  set myPaymentData
    if (this.type === 'payment') {
      this.myData.current = packageRest.restOfPayments.currentNumberOfPayments;
      this.myData.total = packageRest.restOfPayments.totalNumberOfPayments;
      this.myData.description = this.translateService.get(
        'SETTINGS.PACKAGE.CALCULATE.MY_PAYMENT_DESCRIPTION',
        {
          totalNumberOfPayments: packageRest.restOfPayments.totalNumberOfPayments,
          payment: packageRest.restOfPayments.costNextPayment,
        }
      );
      if (packageRest?.extraRestOfPayments) {
        this.myExtraData.current = packageRest.extraRestOfPayments.currentNumberOfPayments;
        this.myExtraData.total = packageRest.extraRestOfPayments.totalNumberOfPayments;
        this.myExtraData.description = this.translateService.get('SETTINGS.PACKAGE.ADDITIONAL.NO_EXPIRATION_DATE');
        this.myExtraData.color  = packageRest.extraRestOfPayments.colorIndicator;

      }

      this.data = [
        { checked: false, currency: 1000, value: 5 },
        { checked: false, currency: 2000, value: 10 },
        { checked: false, currency: 3000, value: 20 },
        { checked: false, currency: 5000, value: 50 },
      ];
    }
    if (this.type === 'refill') {
      this.myData.current = packageRest.depositAndWithdrawals.currentDepositAndWithdrawals;
      this.myData.total = packageRest.depositAndWithdrawals.totalDepositAndWithdrawals;

      this.myData.description = this.translateService.get(
        'SETTINGS.PACKAGE.CALCULATE.DEPOSITS_DESCRIPTION',
        {
          deposit: packageRest.depositAndWithdrawals.totalDepositAndWithdrawals / 1e6,
          depositingOverLimit: packageRest.depositAndWithdrawals.depositingOverLimit,
          withdrawalOverLimit: packageRest.depositAndWithdrawals.withdrawalOverLimit,
        }
      );
      if (packageRest?.extraDepositAndWithdrawals) {
        this.myExtraData.current = packageRest.extraDepositAndWithdrawals.currentDepositAndWithdrawals;
        this.myExtraData.total =    packageRest.extraDepositAndWithdrawals.totalDepositAndWithdrawals;
        this.myExtraData.description = this.translateService.get('SETTINGS.PACKAGE.ADDITIONAL.NO_EXPIRATION_DATE');
        this.myExtraData.color  = packageRest.extraDepositAndWithdrawals.colorIndicator;
      }
      this.data = [
        { checked: false, value: 2000000, currency: 2000 },
        { checked: false, value: 5000000, currency: 8000 },
        { checked: false, value: 10000000, currency: 15000 },
        { checked: false, value: 20000000, currency: 25000 },
      ];
    }
    this.myData.color  = packageRest.restOfPayments.colorIndicator;
  }
  addAdditionalPackage(item: any) {
    if (this.myExtraData?.current > 0) {
      return;
    }
    const extraTerms: ExtraTerms = {
      value: item.value,
      transferType: this.page[this.type].key,
      item
    };

    const modalRef = this.modalService.open(AlertPackageComponent, options);
    modalRef.componentInstance.alertMessageType = 'additional_package';
    modalRef.componentInstance.packageRest = this.packageRest;
    modalRef.componentInstance.extraTerms = extraTerms;
    modalRef.componentInstance.companyId = this.companyId;
    modalRef.componentInstance.isSameDay = this.packageRest.isSameDay;
    modalRef.componentInstance.isWorkingDay = this.packageRest.isWorkingDay;
    modalRef.componentInstance.refresh.subscribe(({ typeUser, statusCode } ) => {
      this.getPackageRest();
      if (typeUser && statusCode) {
        this.analytics.logEvent('JBundle_AddPack_Result', {version: 1, TypeUser: typeUser, Status: statusCode});
      }
    });
  }

}
